<template>
  <div id="wheel" :style="{ width: size + 'px', height: size + 'px' }">
    <div
      class="wheel_arrow"
      :style="{
        width: arrowSize.width + 'px',
        height: arrowSize.height + 'px'
      }"
    ></div>
    <div
      class="wheel__bg"
      :style="{
        width: size * emptyCircleRatio + 'px',
        height: size * emptyCircleRatio + 'px'
      }"
    ></div>
    <div
      class="wheel__border"
      :style="{ width: size * 0.8 + 'px', height: size * 0.8 + 'px' }"
    ></div>
    <div class="wheel__inner-glow"></div>
    <div
      class="wheel__animate"
      :style="{ width: size + 'px', height: size + 'px' }"
    >
      <div class="zero-inner-glow absolute absolute-full"></div>
      <div
        class="wheel__numbers absolute absolute-full"
        :style="{ fontSize: fontSize + 'px' }"
      >
        <div
          class="wheel__number text-weight-medium"
          :key="value"
          :style="{
            transform: calculateNumberAngle(index),
            width: circumference / numbersCount + 'px',
            height: circumference / numbersCount + 'px',
            margin: -circumference / numbersCount / 2 + 'px',
            paddingTop: fontSize * 0.15 + 'px'
          }"
          v-for="(value, index) in numbers"
        >
          <span>{{ value }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { SVG } from "@svgdotjs/svg.js";
import _ from "lodash";
import anime from "animejs";
import { mapState } from "vuex";
import Chance from "chance";
import {rouletteProcess} from '@/howler'

export default {
  name: "Wheel",
  props: ["size", "numbersCount"],
  data() {
    return {
      emptyCircleRatio: 0.6
    };
  },
  mounted() {
    this.draw();
    // this.rotate({ number: 5 });
  },
  watch: {
    size() {
      this.draw();
    },
    numbersCount() {
      this.draw();
    }
  },
  methods: {
    rotate({
      duration = 10000,
      endedAt = this.ts.now() - 10000,
      number = 0,
      animationTurns = 6,
      hash = "abc"
    } = {}) {
      let chance = new Chance(hash);
      let numberAngle = 360 / this.numbersCount;
      let degree =
        -this.numbers.indexOf(number) * numberAngle -
        chance.integer({
          min: Math.floor(0.1 * numberAngle),
          max: Math.floor(0.9 * numberAngle)
        });
      let targets = this.$el.querySelector(".wheel__animate");
      let animation = anime({
        targets,
        duration,
        autoplay: false,
        easing: "easeOutQuad",
        rotate: 360 * animationTurns + degree,
        complete: function() {
          targets.style.transform = `rotate(${degree}deg)`;
        }
      });
      let offset = this.ts.now() - endedAt;
      offset = offset > duration ? duration : offset < 0 ? 0 : offset;

      animation.seek(offset);
      let id = rouletteProcess.play()
      rouletteProcess.seek(offset/1000,id)

      if (offset >= duration) {
        return Promise.resolve();
      }

      animation.play();
      return animation.finished;
    },
    getColorByNumber(number) {
      let result;
      if (number === 0) {
        result = "#3f8701";
      } else if (number <= this.numbersCount / 2) {
        result = "#d90303";
      } else {
        result = "#000000";
      }
      return result;
    },
    draw() {
      let svg = this.$el.querySelector("svg");
      if (svg) {
        svg.remove();
      }
      let draw = SVG()
        .addTo(this.$el.querySelector(".wheel__animate"))
        .size("100%", "100%");
      let group = draw.group();
      let emptyCircleRatio = this.emptyCircleRatio;
      let sliceAngle = 360 / this.numbersCount;
      let sliceAngleInRadians = ((sliceAngle - 90) * Math.PI) / 180;
      this.numbers.forEach((number, index) => {
        let xcord = this.center + this.radius * Math.cos(sliceAngleInRadians);
        let ycord = this.center + this.radius * Math.sin(sliceAngleInRadians);
        let emptyCircleXcord =
          this.center +
          this.radius * emptyCircleRatio * Math.cos(sliceAngleInRadians);
        let emptyCircleYcord =
          this.center +
          this.radius * emptyCircleRatio * Math.sin(sliceAngleInRadians);
        let pathD = `M${this.center},${this.center -
          this.radius * emptyCircleRatio} V0 A${this.radius} ${
          this.radius
        } 0 0 1 ${xcord} ${ycord} L${emptyCircleXcord},${emptyCircleYcord} A${this
          .radius * emptyCircleRatio} ${this.radius * emptyCircleRatio} 0 0 0 ${
          this.center
        },${this.center - this.radius * emptyCircleRatio}`;
        let path = group.path(pathD);
        if (number === 0)
          draw
            .clip()
            .add(draw.path(pathD))
            .attr({ id: "zeroclippath" });
        path.fill(this.getColorByNumber(number));
        path.rotate(sliceAngle * index, this.center, this.center);
      });
    },
    calculateNumberAngle(index) {
      let angle = 360 / this.numbersCount;
      let rot = angle / 2; // потому что круг слегка повернут вправо
      let result = "";

      rot = rot + index * angle;
      result = `rotate(${rot}deg) translate(0, ${-this.radius +
        this.circumference / this.numbersCount / 2}px)`;

      return result;
    }
  },
  computed: {
    numbers() {
      let lastBlack = Math.floor(this.numbersCount / 2 + 1);
      return [
        0,
        ..._.flatten(
          _.range(1, lastBlack).map(number => {
            let result = [number];
            if (lastBlack < this.numbersCount) {
              result.push(lastBlack++);
            }
            return result;
          })
        )
      ];
    },
    fontSize() {
      return (this.size * 34) / 525;
    },
    arrowSize() {
      return {
        width: (this.size * 247) / 525,
        height: (this.size * 124) / 525
      };
    },
    center() {
      return this.size / 2;
    },
    radius() {
      return this.size / 2;
    },
    circumference() {
      //длина окружности
      return this.radius * 2 * Math.PI;
    },
    ...mapState({
      ts: state => state.time.ts
    })
  }
};
</script>

<style lang="scss">
#wheel {
  position: relative;
  z-index: 1;
  .wheel__animate {
    will-change: transform;
  }
  .wheel__inner-glow {
    position: absolute;
    $size: -7%;
    top: $size;
    right: $size;
    bottom: $size;
    left: $size;
    border-radius: 50%;
    background: radial-gradient(
      circle,
      rgba(230, 14, 17, 1) 50%,
      rgba(14, 19, 25, 0) 69%
    );
    z-index: -3;
  }
  .zero-inner-glow {
    background: radial-gradient(
      circle,
      rgba(#006400, 1) 60%,
      rgba(0, 0, 0, 0) 69%
    );
    z-index: -2;
    transform: scale(1.14);
    clip-path: url(#zeroclippath);
  }
  .wheel__number {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    align-items: flex-start;
    justify-content: center;
  }
  .wheel_arrow {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -60%);
    background: url("./assets/roulette-arrow@2x.png") center center / 100% 100%
      no-repeat;
    z-index: 3;
  }
  .wheel__border {
    position: absolute;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url("./assets/roulette-border@2x.png") center center / 100% 100%
      no-repeat;
    z-index: 3;
  }
  .wheel__bg {
    position: absolute;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url("./assets/roulette-bg.png") center center / 120% 120%
      no-repeat;
    z-index: -1;
  }
}
</style>
