export default {
  methods: {
    splitMarketHashName(marketHashName) {
      let [name, ...marketName] = marketHashName.split("|");
      marketName = marketName.join("|");
      name = name.trim();
      let quality = marketName.match(/\(.+\)$/),
        shortQuality;
      if (quality) {
        quality = quality[0].replace(/[()]/g, "");
        marketName = marketName.slice(0, marketName.indexOf(quality) - 1);
        shortQuality = quality.replace(/[^A-Z]/g, "");
      }
      return { name, marketName, quality, shortQuality };
    }
  }
};
