<template>
  <q-dialog v-model="show">
    <q-card class="app-dialog">
      <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>

      <q-card-section style="min-width: 24em">
        <div class="q-mb-md">
          Создание промокода
        </div>
        <q-input
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Промокод (необязательно)"
          v-model="promocode.code"
        />
        <q-select
          options-dark
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Тип промокода"
          v-model="promocode.type"
          :options="[
            'fix',
            'firstFix',
            'streamFix',
            'depositFix',
            'depositPercentage',
            'firstDepositPercentage',
            'firstDepositFix'
          ]"
        />
        <q-input
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Кол-во активаций"
          v-model.number="promocode.activations"
        />
        <q-input
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Сумма/процент промокода"
          v-model.number="promocode.value"
        />
        <q-input
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Годен до (по умолчанию месяц)"
          v-model="promocode.expireAt"
        >
          <template v-slot:prepend>
            <q-icon name="event" class="cursor-pointer">
              <q-popup-proxy transition-show="scale" transition-hide="scale">
                <q-date
                  v-model="promocode.expireAt"
                  mask="YYYY-MM-DD HH:mm"
                  dark
                >
                  <div class="row items-center justify-end">
                    <q-btn v-close-popup label="Close" color="primary" flat />
                  </div>
                </q-date>
              </q-popup-proxy>
            </q-icon>
          </template>

          <template v-slot:append>
            <q-icon name="access_time" class="cursor-pointer">
              <q-popup-proxy transition-show="scale" transition-hide="scale">
                <q-time
                  dark
                  v-model="promocode.expireAt"
                  mask="YYYY-MM-DD HH:mm"
                  format24h
                >
                  <div class="row items-center justify-end">
                    <q-btn v-close-popup label="Close" color="primary" flat />
                  </div>
                </q-time>
              </q-popup-proxy>
            </q-icon>
          </template>
        </q-input>
        <q-input
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Мин. депозит для depositFix/firstDepositFix монетах (необязательно)"
          v-model.number="promocode.minDepositAmount"
        />
        <q-input
          class="full-width"
          outlined
          bg-color="deposit-input"
          label-color="white"
          input-class="text-white q-px-sm"
          label="Макс. кол-во монет за процентный промокод (необязательно)"
          v-model.number="promocode.maxValue"
        />
        <ApolloMutation
          :mutation="require('../graphql/CreatePromocode.gql')"
          :variables="promocodeVariable"
          @done="
            () => {
              promocodeCreated = true;
            }
          "
        >
          <template v-slot="{ mutate, loading, error }">
            <q-btn
              class="full-width q-mt-sm"
              label="Создать"
              size="lg"
              @click="mutate()"
              :loading="loading"
              :disable="promocode.activations <= 0 || promocode.value <= 0"
              color="primary"
            >
            </q-btn>
            <div
              class="full-width text-center text-small text-negative q-mt-sm"
              v-if="error"
            >
              {{ error }}
            </div>
            <div
              class="full-width text-center text-small text-positive q-mt-sm"
              v-if="promocodeCreated"
            >
              Промокод создан
            </div>
          </template>
        </ApolloMutation>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
export default {
  name: "CreatePromocodeDialog",
  props: {
    value: {
      type: Boolean,
      defaultValue: false
    }
  },
  data() {
    return {
      promocode: {
        code: undefined,
        type: "depositFix",
        activations: 1,
        value: 0,
        expireAt: undefined,
        minDepositAmount: undefined,
        maxValue: undefined,
      },
      show: false,
      promocodeCreated: false
    };
  },
  computed: {
    promocodeVariable() {
      let variable = {
        ...this.promocode,
        expireAt:
          this.promocode.expireAt === undefined
            ? undefined
            : new Date(this.promocode.expireAt).getTime()
      };
      for (let key in variable) {
        if (!variable[key]) {
          delete variable[key];
        }
      }
      return variable;
    }
  },
  watch: {
    value(value) {
      this.show = value;
    },

    show(value) {
      this.$emit("input", value);
    }
  }
};
</script>

<style scoped></style>
