<template>
  <component
    :is="tag || 'div'"
    id="user-name"
    class="name text-weight-medium overflow-hidden full-width"
    :class="{ battlepass: user.hasBattlePass || !!hasBattlePass }"
    style="white-space: nowrap; text-overflow: ellipsis;"
  >
    <q-avatar v-if="showAvatar" class="q-mr-xs" size="3em">
      <q-img
          basic
        :src="user.avatar"
        placeholder-src="/img/avatar-fallback.png"
        height="100%"
        width="100%"
      />
    </q-avatar>
    <router-link
      :to="`/profile/${user._id}`"
      tag="span"
      class="cursor-pointer profile-link"
      :class="{ battlepass: user.hasBattlePass || !!hasBattlePass }"
      ><span v-if="user.role.prefix" :style="'color:' + user.role.color"
        >[{{ user.role.prefix }}] </span
      ><span class="user-name">{{ user.name }}</span>

      <q-menu
        v-if="isAuthenticated && storeUser._id != value._id"
        touch-position
        context-menu
        content-class="text-black"
      >
        <q-list dense style="min-width: 100px">
          <q-item
            clickable
            v-close-popup
            :to="`/profile/${user._id}#transfer-balance`"
          >
            <q-item-section class="q-py-sm text-black"
              >Перевести баланс</q-item-section
            >
          </q-item>
          <q-separator> </q-separator>
          <q-item clickable v-close-popup :to="`/profile/${user._id}`">
            <q-item-section class="q-py-sm text-black"
              >Перейти в профиль</q-item-section
            >
          </q-item>
          <q-separator> </q-separator>
          <q-item
            clickable
            v-close-popup
            @click="
              () => {
                setChatBanDialogUser(user);
                toggleChatBanDialog(true);
              }
            "
            v-if="storeUser.role.permissions.includes('chatBan')"
          >
            <q-item-section class="q-py-sm text-black"
              >Заблокировать в чате</q-item-section
            >
          </q-item>
        </q-list>
      </q-menu>
    </router-link>
  </component>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import dialogs from "../mixins/dialogs";

export default {
  name: "UserName",
  props: ["value", "showAvatar", "tag", "hasBattlePass"],
  mixins: [dialogs],
  data() {
    return {
      user: this.value
    };
  },
  created() {
    this.user =
      this.user._id === this.storeUser._id ? this.storeUser : this.user;
  },
  computed: {
    ...mapState({
      storeUser: state => state.user
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  }
};
</script>

