              @click="() => (createPromocodeDialogShow = true)"
            >
            </q-btn>
            <CreatePromocodeDialog
              v-model="createPromocodeDialogShow"
              v-if="user.role.permissions.includes('createPromocode')"
            >
            </CreatePromocodeDialog>
