                <!-- Loading -->
                <div
                  v-if="loading && (!userPromocodes || !userPromocodes.length)"
                  class="relative-position full-width"
                  :style="{ height: `${6.8281 * 2}em` }"
                >
                  <Loading></Loading>
                </div>

                <q-scroll-area
                  class="q-mt-md full-width"
                  v-if="userPromocodes && userPromocodes.length"
                  :style="{
                    height:
                      userPromocodes.length * 6.8281 < 6.8281 * 2
                        ? userPromocodes.length * 6.8281 + 'em'
                        : `${6.8281 * 2}em`
                  }"
                  :thumb-style="scrollThumbStyle"
                  :bar-style="scrollBarStyle"
                  id="deposit-dialog__promocodes-scroll"
                >
                  <q-infinite-scroll
                    @load="
                      (index, done) =>
                        laodMoreUserPromocodes(query, index, done)
                    "
                    :offset="10"
                    :debounce="0"
                    :disable="!hasMoreUserPromocodes"
                    scroll-target="#deposit-dialog__promocodes-scroll > .scroll"
                  >
                    <Promocode
                      :value="promocode"
                      :key="promocode._id"
                      v-for="promocode in userPromocodes"
