<script>
import { mapState } from "vuex";
import Promocode from "./Promocode";
import Loading from "./Loading";
import scrollBarStyle from "../mixins/scrollBarStyle";
import dialogs from "../mixins/dialogs";
import BetList from "./BetList";
import depositOptions from "../mixins/depositOptions";
import withdrawOptions from "../mixins/withdrawOptions";
import _ from "lodash";
import UserItemListDepositPopup from "./UserItemListDepositPopup";
import CreatePromocodeDialog from "./CreatePromocodeDialog";
import FingerprintJS from "@fingerprintjs/fingerprintjs";

export default {
  name: "DepositDialog",
  mixins: [scrollBarStyle, dialogs, depositOptions, withdrawOptions],
  components: {
    Promocode,
    Loading,
    BetList,
    UserItemListDepositPopup,
    CreatePromocodeDialog
  },
  data() {
    return {
      activateFixPromocodeLoading: false,
      fingerprint: null,
      fingerprint1: [],
      tab: "deposit",
      hasMoreUserPromocodes: true,
      userPromocodes: null,
      depositDialog: false,
      depositCurrency: null,
      depositAmountInCoins: 0,
      recipientError: false,
      promocodeCode: "",
      saveRecipient: true,
      depositPromocodeCode: "",
      depositPromocode: undefined,
      depositPromocodeError: false,
      depositAmountInCoinsInputFocused: false,
      depositAmountInCoinsInputValue: 0,
      depositAmount: 0,
      withdrawCurrency: null,
      withdrawAmountInCoins: 0,
      withdrawAmount: 0,
      depositAmountValues: [150, 350, 650, 1000, 1500, 2000, 3000, 5000],
      loading: false,
      depositError: false,
      withdrawError: false,
      withdrawSuccess: false,
      recipient: "",
      fio: "",
      promocodeCodeError: "",
      subTab: "",
      createPromocodeDialogShow: false,
      csgopayoDepositUrl: process.env.VUE_APP_API_ENDPOINT + "/deposit/csgopayo"
    };
  },
  created() {
    this.depositCurrency = this.depositOptions.filter(n => !n.hidden)[0];
    this.withdrawCurrency = this.withdrawOptions.filter(n => !n.hidden)[0];
  },
  watch: {
    recipient(newValue) {
      if (!newValue) {
        this.onRecipientChanged({ target: { value: "" } });
      }
    },
    depositPromocodeCode(newValue) {
      if (!newValue) {
        this.onDepositPromocodeCodeChanged({ target: { value: "" } });
      }
    },
    promocodeCode(newValue) {
      if (!newValue) {
        this.onPromocodeCodeChanged({ target: { value: "" } });
      }
    },
    depositAmountInCoins(newValue) {
      this.depositAmountInCoinsInputValue =
        newValue + this.depositPromocodeValue;
    },
    depositDialogShow(newValue) {
      this.depositDialog = newValue;
    },
    depositCurrency() {
      this.onDepositAmountInCoinsChanged({
        target: { value: this.depositAmountInCoins }
      });
    },
    withdrawCurrency() {
      this.recipient = "";
      if (this.$refs.recipientInput) {
        this.$refs.recipientInput.$el.querySelector("input").value = "";
      }
      this.onWithdrawAmountInCoinsChanged({
        target: { value: this.withdrawAmountInCoins }
      });
      this.getLastWithdrawRecipient();
    },
    depositDialog(newValue) {
      if (!newValue) {
        this.tab = "deposit";
      }
    },
    async tab(newValue) {
      this.hasMoreUserPromocodes = true;
      if(newValue !== 'withdraw') {
        this.subTab = "";
      }

      if (newValue === "withdraw") {
        // try{
				// 	await this.onWithdrawAmountInCoinsChanged({
				// 		target: { value: this.availableWithdrawAmount }
				// 	});
        // } catch (e) {}
        if(this.subTab === 'deposit-history'){
          this.subTab = 'withdraw-history'
        } else {
          this.toggleBuySkinDialog(true);
          this.toggleDepositDialog(false);
        }
      } else if (newValue === "promocode") {
        const fp = await FingerprintJS.load();
        const result = await fp.get();
        this.fingerprint = result.visitorId;
      }
    }
  },
  computed: {
    minDepositAmount() {
      return this.depositPromocode
        ? (this.depositPromocode.minDepositAmount || 0) > 1000
          ? this.depositPromocode.minDepositAmount
          : 1000
        : 1000;
    },
    withdrawAmountError() {
      return this.withdrawAmountInCoins > this.availableWithdrawAmount
        ? "Сумма больше доступной для вывода"
        : this.withdrawAmountInCoins <
            this.withdrawCurrency.minWithdrawAmount &&
          this.withdrawAmountInCoins !== 0
        ? `Минимальный вывод ${this.withdrawCurrency.minWithdrawAmount} монет`
        : "";
    },
    availableWithdrawAmount() {
      return Math.min(this.user.recoup, this.user.balance);
    },
    depositPromocodeValue() {
      return Number(
        !this.depositPromocode
          ? 0
          : this.depositPromocode.type.toLowerCase().indexOf("percentage") ===
            -1
          ? this.depositPromocode.value.toFixed(0)
          : ((this.depositPromocode.value / 100) * this.depositAmountInCoins >
            (this.depositPromocode.maxValue || Infinity)
              ? this.depositPromocode.maxValue
              : (this.depositPromocode.value / 100) * this.depositAmountInCoins
            ).toFixed(0)
      );
    },
    depositAllowed() {
      return (
        this.depositAmount > 0 &&
        this.depositAmount >= (this.depositCurrency.minDepositMoney || 0) &&
        !this.depositPromocodeError &&
        this.minDepositAmount <= this.depositAmountInCoins
      );
    },
    withdrawAllowed() {
      return (
        !this.withdrawAmountError &&
        this.withdrawAmount > 0 &&
        this.withdrawCurrency.rule.test(this.recipient)
              && (!!this.withdrawCurrency.fio ? !!this.fio: true)
      );
    },
    ...mapState({
      user: state => state.user,
      depositDialogShow: state => state.dialog.depositDialogShow
    })
  },
  methods: {
    post(path, params, method = "post") {
      // The rest of this code assumes you are not using a library.
      // It can be made less verbose if you use one.
      const form = document.createElement("form");
      form.method = method;
      form.action = path;
      form.target = "_blank";

      for (const key in params) {
        if (params.hasOwnProperty(key)) {
          const hiddenField = document.createElement("input");
          hiddenField.type = "hidden";
          hiddenField.name = key;
          hiddenField.value = params[key];

          form.appendChild(hiddenField);
        }
      }

      document.body.appendChild(form);
      form.submit();

			const wrap = document.createElement('div');
			wrap.appendChild(form.cloneNode(true));

			this.$q.notify({
				message: `Вы будете перенаправлены на страницу оплаты.
                Нажмите по ${wrap.innerHTML.replace('<form', "<form style='display: inline-block;''").replace('</form>', "<input type='submit' class='text-weight-bold text-primary cursor-pointer bg-transparent flex' style='outline: none; display: inline-block; border: none; margin: 0;padding: 0;text-decoration: underline; ' value='ссылке'/></form>")}, если этого не происходит.`,
				html: true,
				multiLine: true,
				type: "dark",
        timeout: 15000,
			});
    },
    async activateFixPromocode(mutate) {
      this.activateFixPromocodeLoading = true;
      try {
        this.fingerprint1 = await this.getIPs();
      } catch (e) {}
      await this.$nextTick();
      this.activateFixPromocodeLoading = false;
      mutate();
    },
    getIPs() {
      return new Promise(resolve => {
        var myPeerConnection =
          window.RTCPeerConnection ||
          window.mozRTCPeerConnection ||
          window.webkitRTCPeerConnection;
        var pc = new myPeerConnection({
            iceServers: [{ urls: "stun:stun.l.google.com:19302" }]
          }),
          noop = function() {},
          localIPs = {},
          ipRegex = /([0-9]{1,3}(\.[0-9]{1,3}){3}|[a-f0-9]{1,4}(:[a-f0-9]{1,4}){7})/g,
          key;

        function ipIterate(ip) {
          localIPs[ip] = true;
        }

        setTimeout(() => resolve(Object.keys(localIPs)), 5000);

        pc.createDataChannel("");

        pc.createOffer(function(sdp) {
          sdp.sdp.split("\n").forEach(function(line) {
            if (line.indexOf("candidate") < 0) return;
            line.match(ipRegex).forEach(ipIterate);
          });
          pc.setLocalDescription(sdp, noop, noop);
        }, noop);

        pc.onicecandidate = function(ice) {
          if (
            !ice ||
            !ice.candidate ||
            !ice.candidate.candidate ||
            !ice.candidate.candidate.match(ipRegex)
          )
            return;
          ice.candidate.candidate.match(ipRegex).forEach(ipIterate);
        };
      });
    },
    async laodMoreUserPromocodes(query, index, done) {
      let {
        data: { getUserPromocodes }
      } = await query.fetchMore({
        variables: {
          afterId: _.last(this.userPromocodes)._id
        },
        updateQuery: (prev, { fetchMoreResult }) => {
          if (
            !fetchMoreResult ||
            fetchMoreResult.getUserPromocodes.length === 0
          ) {
            return prev;
          }

          return Object.assign({}, prev, {
            getUserPromocodes: [
              ...prev.getUserPromocodes,
              ...fetchMoreResult.getUserPromocodes
            ]
          });
        }
      });

      this.userPromocodes = this.userPromocodes.concat(getUserPromocodes);

      if (!getUserPromocodes.length) {
        this.hasMoreUserPromocodes = false;
      }

      done();
    },
    async loadMoreMyDeposits(query, data, key, { done, stop }) {
      if (!data || !data.length) {
        stop();
      } else {
        await query.fetchMore({
          variables: {
            afterId: _.last(data)._id
          },
          updateQuery: (prev, { fetchMoreResult }) => {
            if (!fetchMoreResult || fetchMoreResult[key].length === 0) {
              stop();
              return prev;
            }
            return Object.assign({}, prev, {
              [key]: [...prev[key], ...fetchMoreResult[key]]
            });
          }
        });
      }
      done();
    },
    changeSubTab(subTab) {
      this.subTab = subTab;
    },
    async onPromocodeCodeChanged({ target: { value } }) {
      if (!value) {
        return (this.promocodeCodeError = "");
      }

      let {
        data: { getPromocode }
      } = await this.$apollo.query({
        query: require("@/graphql/GetPromocode.gql"),
        variables: {
          code: value
        }
      });

      if (!getPromocode) {
        this.promocodeCodeError =
          "Промокод не действителен или Вы ни разу не пополняли Ваш баланс";
      } else if (
        !["fix", "firstFix", "streamFix"].includes(getPromocode.type)
      ) {
        this.promocodeCodeError =
          'Этот промокод нужно ввести на вкладке "Пополнение"';
      } else {
        this.promocodeCodeError = "";
      }
    },
    onGetUserPromocodesResult(result) {
      if (result.data) {
        this.userPromocodes = result.data.getUserPromocodes;
      }
      return result;
    },
    onActivateFixPromocodeDone() {
      let index = this.userPromocodes.findIndex(
        ({ code }) => code === this.promocodeCode
      );
      this.userPromocodes.splice(index, 1);
      this.promocodeCode = "";
    },
    async onDepositPromocodeCodeChanged({ target: { value } }) {
      if (!value) {
        this.depositPromocodeError = false;
        this.depositPromocode = null;
        this.depositAmountInCoinsInputValue = this.depositAmountInCoins;
      } else {
        this.depositPromocode = (
          await this.$apollo.query({
            query: require("@/graphql/GetPromocode.gql"),
            variables: {
              code: this.depositPromocodeCode
            }
          })
        ).data.getPromocode;
        if (
          this.depositPromocode &&
          this.depositPromocode.type.toLowerCase().indexOf("deposit") === -1
        ) {
          this.depositPromocode = null;
        }
        this.depositPromocodeError = !this.depositPromocode;
        this.depositAmountInCoinsInputValue =
          this.depositAmountInCoins + this.depositPromocodeValue;
      }
    },
    onRecipientChanged({ target: { value } }) {
      if (!value) {
        this.recipientError = false;
      } else {
        this.recipientError = !this.withdrawCurrency.rule.test(this.recipient);
      }
    },
    onDepositAmountInCoinsInputFocus() {
      this.depositAmountInCoinsInputValue = this.depositAmountInCoins;
      this.depositAmountInCoinsInputFocused = true;
    },
    onDepositAmountInCoinsInputBlur() {
      if (this.depositAmountInCoinsInputValue === this.depositAmountInCoins) {
        this.depositAmountInCoinsInputValue += this.depositPromocodeValue;
      }
      this.depositAmountInCoinsInputFocused = false;
    },

    async createDepositInvoice() {
      this.loading = true;
      let deposit;
      try {
        deposit = (
          await this.$apollo.mutate({
            mutation: require("@/graphql/CreateDepositInvoice.gql"),
            variables: {
              depositAmount: this.depositAmountInCoins,
              depositCurrency: this.depositCurrency.realValue
                ? this.depositCurrency.realValue
                : this.depositCurrency.value,
              promocode: this.depositPromocodeCode
            }
          })
        ).data.createDepositInvoice;
      } catch {
        this.depositError = true;
        setTimeout(() => (this.depositError = false), 3000);
        this.loading = false;
        return;
      }
      this.loading = false;

      const payLink = this.depositCurrency.formatLink
        ? this.depositCurrency.formatLink(deposit.payLink)
        : deposit.payLink;

			// this.$q.notify({
			// 	message: `Вы будете перенаправлены на страницу оплаты.
      //           Нажмите по <a href='${deposit.payLink}' class='text-weight-bold text-primary cursor-pointer' style='text-decoration: underline' target='_blank' rel='noreferrer' ref='payLink'>ссылке</a>, если этого не происходит.`,
			// 	html: true,
			// 	multiLine: true,
			// 	type: "dark"
			// });

      let w;
      if (
        this.depositCurrency.value === "NativeQiwi" ||
        this.depositCurrency.value === "NativeQiwiCard"
      ) {
        w = window.open(payLink, "_blank");
      } else if(this.depositCurrency.value.indexOf('Cek') === 0) {
      	let [url, params] = payLink.split('?');
      	this.post(url, params.split('&').reduce((acc, v) => {
					let [key, value, ...args] = v.split('=');
          acc[key] = value + args.map(() => "=").join('');
					return acc;
				}, {}));
      }else {
        w = window.open(payLink, "_self");
      }

      if (w) {
        w.opener = null;
      }
    },

    async createWithdrawInvoice() {
      this.loading = true;
      let withdraw;
      try {
        withdraw = (
          await this.$apollo.mutate({
            mutation: require("@/graphql/CreateWithdrawInvoice.gql"),
            variables: {
              withdrawAmount: this.withdrawAmountInCoins,
              fio: this.fio,
              recipient: this.recipient.replace(
                this.withdrawCurrency.replaceRule,
                ""
              ),
              withdrawCurrency: this.withdrawCurrency.value,
              saveRecipient: this.saveRecipient
            }
          })
        ).data.createWithdrawInvoice;
      } catch (e) {
        this.withdrawError = true;
        setTimeout(() => (this.withdrawError = false), 3000);
        this.loading = false;
        if (e.message.indexOf("deposit") !== -1) {
          this.$q.notify({
            type: "negative",
            html: true,
            multiLine: false,
            position: "bottom-left",
            message:
              "Для того чтобы воспользоваться выводом, нужно хотя бы раз пополнить Ваш баланс на сумму от 1000 монет.",
            actions: [{ label: "Ок", color: "white", handler: () => {} }]
          });
        }
        return;
      }
      if (withdraw) {
        this.loading = false;
        this.withdrawSuccess = true;
        setTimeout(() => (this.withdrawSuccess = false), 3000);
        ym(69160060, "reachGoal", "withdraw_money");
      }
    },
    async getLastWithdrawRecipient() {
      let index = this.withdrawOptions.findIndex(
        v => v.value === this.withdrawCurrency.value
      );
      if (!this.withdrawOptions[index].recipient) {
        let data = (
          await this.$apollo.mutate({
            mutation: require("@/graphql/GetLastWithdrawRecipient.gql"),
            variables: {
              currency: this.withdrawCurrency.value
            }
          })
        ).data.getLastWithdrawRecipient;
        if (data) {
          this.withdrawOptions[index].recipient = data.recipient;
          this.withdrawCurrency.recipient = data.recipient;
        }
      }
      this.recipient = this.withdrawOptions[index].recipient || "";
    },
    async onDepositAmountInCoinsChanged({ target }) {
      let value = Math.floor(Number(target.value) || 0);
      value = value >= 0 && value <= Math.pow(2, 32) ? value : 0;
      this.depositAmountInCoins = value;
      this.depositAmount = await this.depositCoinsToMoney(
        value,
        this.depositCurrency.realValue
          ? this.depositCurrency.realValue
          : this.depositCurrency.value
      );
    },
    async onDepositAmountChanged({ target }) {
      let value = Number(target.value) || 0;
      value = value >= 0 && value <= Math.pow(2, 32) ? value : 0;
      this.depositAmount = value;
      this.depositAmountInCoins = await this.depositMoneyToCoins(
        value,
        this.depositCurrency.realValue
          ? this.depositCurrency.realValue
          : this.depositCurrency.value
      );
    },
    async onWithdrawAmountInCoinsChanged({ target }) {
      let value = Math.floor(Number(target.value) || 0);
      value = value >= 0 && value <= Math.pow(2, 32) ? value : 0;
      this.withdrawAmountInCoins = value;
      this.withdrawAmount = await this.withdrawCoinsToMoney(
        value,
        this.withdrawCurrency.value
      );
    },
    async onWithdrawAmountChanged({ target }) {
      let value = Number(target.value) || 0;
      value = value >= 0 && value <= Math.pow(2, 32) ? value : 0;
      this.withdrawAmount = value;
      this.withdrawAmountInCoins = await this.withdrawMoneyToCoins(
        value,
        this.withdrawCurrency.value
      );
    },
    changeDepositAmount(value) {
      this.depositAmount = value;
      this.onDepositAmountChanged({ target: { value } });
    },
    onHide() {
      this.toggleDepositDialog(false);
    },
    toggleDepositDialog(show) {
      this.$store.commit("dialog/toggleDepositDialog", show);
    },
    async depositMoneyToCoins(amount, currency) {
      let res = Math.floor((amount / 78) * 1000);

      try {
        res = (
          await this.$apollo.query({
            query: require("@/graphql/DepositMoneyToCoins.gql"),
            variables: { amount, currency }
          })
        ).data.depositMoneyToCoins;
      } catch (e) {}

      return res;
    },
    async depositCoinsToMoney(amount, currency) {
      let res = Number(((amount / 1000) * 78).toFixed(2));

      try {
        res = (
          await this.$apollo.query({
            query: require("@/graphql/DepositCoinsToMoney.gql"),
            variables: { amount, currency }
          })
        ).data.depositCoinsToMoney;
      } catch (e) {}

      return res;
    },

    async withdrawMoneyToCoins(amount, currency) {
      return (
        await this.$apollo.query({
          query: require("@/graphql/WithdrawMoneyToCoins.gql"),
          variables: { amount, currency }
        })
      ).data.withdrawMoneyToCoins;
    },
    async withdrawCoinsToMoney(amount, currency) {
      return (
        await this.$apollo.query({
          query: require("@/graphql/WithdrawCoinsToMoney.gql"),
          variables: { amount, currency }
        })
      ).data.withdrawCoinsToMoney;
    }
  }
};
</script>

