<template>
  <div id="royal-battle" class="q-pt-lg q-pb-lg q-px-xs-md q-px-md-none">
    <div class="wrapper-secondary game-title">
      <GameTitle
        :game="royalBattle"
        :countdown="countdown"
        :sumOfBets="sumOfBets"
        gameName="Царь горы"
        :gameIcon="royalBattleIcon"
      >
      </GameTitle>
    </div>
    <div class="royal-battle__inner wrapper">
      <div class="royal-battle__body wrapper-secondary">
        <div class="row">
          <History
            v-if="$q.screen.gt.sm"
            :load-more="loadMoreHistory"
            :history="royalBattle.history"
          ></History>
          <div
            class="col-md-4 col-xs-12 items-stretch flex"
            :style="{ paddingTop: $q.screen.gt.sm ? '1.5em' : '' }"
          >
            <div class="full-width" :style="{ maxHeight: wheelSize + 'px' }">
              <RoyalBattleWheel
                :size="wheelSize"
                :slices="this.slices"
                ref="wheel"
              >
              </RoyalBattleWheel>
              <q-resize-observer @resize="updateWheelSize" />
            </div>
          </div>
          <div
            class="col-md-4 col-xs-12 q-pl-md-xl flex items-center"
            :style="{ paddingRight: $q.screen.gt.sm ? '5em' : '' }"
          >
            <BetMaker
              v-model="betMakerProps"
              class="full-width"
              game-type="royal-battle"
              @bet="createRoyalBattleBet"
            ></BetMaker>
          </div>
        </div>
      </div>
      <div class="row wrapper-secondary q-py-md-none q-py-md">
        <div class="col-md-5 col-xs-12 q-pr-md-sm q-mb-md q-mb-md-none">
          <BetList :value="players" game-type="royal-battle-players"> </BetList>
        </div>
        <div class="col-md-7 col-xs-12 q-pl-md-sm">
          <BetList :value="betsReversed" game-type="royal-battle"> </BetList>
        </div>
      </div>
      <History
        v-if="$q.screen.lt.md"
        :load-more="loadMoreHistory"
        :history="royalBattle.history"
      ></History>
    </div>
  </div>
</template>

<script>
import RoyalBattleWheel from "./RoyalBattleWheel";
import { mapGetters, mapState } from "vuex";
import store from "@/store";
import BetMaker from "../../components/BetMaker";
import BetList from "../../components/BetList";
import GameTitle from "../../components/GameTitle";
import royalBattleIcon from "@/../public/img/game-icons/royalbattle-icon.svg";
import _ from "lodash";
import History from "./History";
import {royalBattleStart, betSound} from '@/howler'

export default {
  name: "RoyalBattle",
  components: {
    RoyalBattleWheel,
    BetMaker,
    GameTitle,
    BetList,
    History
  },
  meta: {
    title: "Царь горы"
  },
  data() {
    return {
      wheelSize: 0,
      betMakerProps: { betAmount: 100 },
      isMounted: false,
      countdown: 0,
      royalBattleStartTimer: null,
      royalBattleIcon,
      lastBetsCount: 0,
    };
  },
  beforeRouteLeave(to, from, next){
    //store.dispatch("time/stopUpdateInterval");
    store.dispatch("royalBattle/reset");
    next();
  },
  async beforeRouteEnter(to, from, next) {
    //store.dispatch("time/startUpdateInterval");
    if (!store.state.royalBattle._id) {
      await store.dispatch("royalBattle/getGame");
    }

    next();
  },
  watch: {
    'royalBattle.bets': {
      handler(newValue = []){
        if(this.lastBetsCount < newValue.length){
          betSound.play();
        }
        this.lastBetsCount = newValue.length;
      }
    },
    "royalBattle.startedAt": {
      immediate: true,
      handler: function(newValue) {
        if (newValue) {
          let result = this.royalBattle.startedAt - this.now;
          this.countdown = result < 0 ? 0 : result;
            clearTimeout(this.royalBattleStartTimer);
          this.royalBattleStartTimer = setTimeout(()=>{
              let id = royalBattleStart.play();
              royalBattleStart.seek(result < 0? Math.abs(result/1000) : 0, id);
            }, this.countdown)

        }
      }
    },
    isMounted() {
      if (typeof this.royalBattle.number === "number") {
        this.$refs.wheel.rotate({
          sliceIndex: this.players.findIndex(player => {
            return player.user._id === this.royalBattle.winnerUserId;
          }),
          endedAt: this.royalBattle.startedAt,
          hash: this.royalBattle._id
        });
      }
    },
    ["royalBattle.number"](newValue) {
      if (newValue !== null && this.isMounted) {
        this.$refs.wheel.rotate({
          sliceIndex: this.players.findIndex(player => {
            return player.user._id === this.royalBattle.winnerUserId;
          }),
          endedAt: this.royalBattle.startedAt,
          hash: this.royalBattle._id
        });
      }
    }
  },
  mounted() {
    this.isMounted = true;
  },
  beforeDestroy() {
    clearTimeout(this.royalBattleStartTimer)
    royalBattleStart.stop();
  },
  methods: {
    async loadMoreHistory({ done, stop }) {
      let history = await this.$store.dispatch("royalBattle/loadMoreHistory", {
        afterId: _.last(this.royalBattle.history)._id
      });

      if (!history.length) {
        stop();
      }

      done();
    },
    updateWheelSize({ width }) {
      this.wheelSize = width;
    },
    async createRoyalBattleBet({ betAmount, betUserItemId }) {
      try{
        await this.$apollo.mutate({
          mutation: require("@/graphql/CreateRoyalBattleBet.gql"),
          variables: { betAmount, betUserItemId }
        });
      }catch(e){
        if(e.message.indexOf("deposit") !== -1){
          this.$q.notify({
            type: "negative",
            html: true,
            multiLine: false,
            position: "bottom-left",
            message:  'Для того чтобы делать ставки в этой игре, необходимо пополнить Ваш баланс на сумму от 1000 монет.' ,
            actions: [{ label: "Ок", color: "white", handler: () => {} }]
          });
        }
      }

    }
  },
  computed: {
    betsReversed() {
      return this.royalBattle.bets;
    },
    slices() {
      return this.players.length
        ? this.players
        : [{ ratio: 1, color: "#3b3f4e" }];
    },
    ...mapState({
      user: state => state.user,
      royalBattle: state => state.royalBattle,
      now: state => state.time.now
    }),
    ...mapGetters({
      sumOfBets: "royalBattle/sumOfBets",
      players: "royalBattle/players"
    })
  }
};
</script>

<style lang="scss">
#royal-battle {
  .game-title {
    .game-title__text {
      padding-left: 5em;
    }
    .game-buttons__container {
      padding-right: 5em;
    }
  }
  .royal-battle-history {
    height: 15em;
    width: 100%;
    background-color: rgba(37, 39, 45, 0.5);
    border: 0.2em solid #292b33;
    position: relative;
    background-clip: padding-box;
    border-radius: 0.75em;
    cursor: pointer;
    .history__inner {
      height: 100%;
      mask: linear-gradient(
        to top,
        transparent 10%,
        #fff 18% 82%,
        transparent 95%
      );
      mask-size: 120% 120%;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-composite: intersect;
      .history__item-container:nth-child(even) {
        .history__item {
          background: rgba(#282930, 0.5);
        }
      }
    }
  }
}
</style>
