<template>
  <div id="blackjack" class="q-pt-lg q-pb-lg q-px-xs-md q-px-md-none">
    <div class="wrapper-secondary game-title">
      <GameTitle
        :game="animatedGameData"
        gameName="Блэкджек"
        :gameIcon="BlackjackIcon"
        class=""
        :betAmount="animatedGameData.bet.betAmount"
        :lastProfitAmount="animatedGameData.bet.profitAmount"
      >
      </GameTitle>
    </div>
    <div class="blackjack__inner wrapper">
      <div class="wrapper-secondary q-pb-xl q-pt-sm">
        <div class="row q-pt-md-xl q-pt-lg q-pb-xl q-pb-md-none">
          <div class="col-4 flex items-center justify-md-end justify-start">
            <BlackjackHand
              v-model="dealerHand"
              :reversed="true"
              ref="dealerHand"
              key="dealerHand"
              :width="$q.screen.lt.md ? 100 : 149"
            >
            </BlackjackHand>
          </div>
          <div class="col-4 flex items-center column">
            <BlackjackDeck
              ref="deck"
              :width="$q.screen.lt.md ? 75 : 149"
              :hand-card-width="$q.screen.lt.md ? 100 : 149"
            >
            </BlackjackDeck>
            <div
              :style="{ width: $q.screen.gt.sm ? '35%' : '100%' }"
              class="q-mt-lg"
            >
              <q-btn
                class="rounded-borders-2x full-width q-mb-md"
                color="grey-10"
                label="Стоп"
                :disabled="!blackjack.active || betMakerProps.inAnimation"
                size="md"
                padding="1em xs"
                @click="standBlackjackGame"
              ></q-btn>
              <q-btn
                class="rounded-borders-2x full-width"
                color="primary"
                label="Взять еще"
                :disabled="!blackjack.active || betMakerProps.inAnimation"
                size="md"
                padding="1em xs"
                @click="hitBlackjackGame"
              ></q-btn>
            </div>
          </div>
          <div class="col-4 flex items-center justify-md-start justify-end">
            <BlackjackHand
              v-model="userHand"
              ref="userHand"
              key="userHand"
              :width="$q.screen.lt.md ? 100 : 149"
            >
            </BlackjackHand>
          </div>
        </div>
        <div class="row q-pt-md">
          <div class="offset-md-2"></div>
          <div class="col-md-8 col-xs-12">
            <BetMaker
              class="full-width"
              v-model="betMakerProps"
              game-type="blackjack"
              @bet="createBlackjackGame"
            >
            </BetMaker>
          </div>
          <div class="offset-md-2"></div>
        </div>
      </div>
      <BetList
        @load="loadMoreHistory"
        :offset="100"
        :debounce="250"
        :value="blackjack.history"
        game-type="blackjack"
        ref="betList"
        class="wrapper-secondary"
      ></BetList>
    </div>
  </div>
</template>

<script>
import BlackjackHand from "./BlackjackHand";
import BlackjackDeck from "./BlackjackDeck";
import { mapState } from "vuex";
import BetMaker from "../../components/BetMaker";
import _ from "lodash";
import BetList from "../../components/BetList";
import store from "@/store";
import BlackjackIcon from "@/../public/img/game-icons/blackjack-icon.svg";
import GameTitle from "../../components/GameTitle";
import { betSound } from "../../howler";

export default {
  name: "Blackjack",
  components: {
    BlackjackHand,
    BlackjackDeck,
    BetMaker,
    BetList,
    GameTitle
  },
  meta: {
    title: "Блэкджек"
  },
  data() {
    return {
      animatedGameData: null,
      BlackjackIcon,
      userHand: {
        cards: [],
        score: 0,
        status: ""
      },
      dealerHand: {
        cards: [],
        score: 0,
        status: ""
      },
      betMakerProps: {
        betAmount: 100,
        inAnimation: false
      },
      loadingHistory: false
    };
  },

  created() {
    this.userHand.cards = this.blackjack.userHand;
    this.dealerHand.cards = this.blackjack.dealerHand;
    this.dealerHand.score = this.blackjack.dealerScore;
    this.userHand.score = this.blackjack.userScore;
    this.userHand.status = this.blackjack.gameResult;
    this.dealerHand.status = this.reverseGameResult(this.blackjack.gameResult);
    this.animatedGameData = _.cloneDeep(this.blackjack);
  },
  async beforeRouteEnter(to, from, next) {
    if (!store.state.blackjack._id) {
      await store.dispatch("blackjack/getGame");
    }

    next();
  },
  beforeRouteLeave(to, from, next) {
    store.dispatch("blackjack/reset");
    next();
  },
  watch: {
    "blackjack.history": {
      handler() {
        betSound.play();
      }
    }
  },
  methods: {
    async loadMoreHistory({ done = () => {}, stop = () => {} } = {}) {
      this.loadingHistory = true;

      let history = this.blackjack.history.length
        ? await this.$store.dispatch("blackjack/loadMoreHistory", {
            afterId: _.last(this.blackjack.history)._id
          })
        : [];

      if (history.length) {
        this.loadingHistory = false;
      } else {
        stop();
      }

      done();
    },

    reverseGameResult(result) {
      return result === "victory"
        ? "defeat"
        : result === "draw"
        ? "draw"
        : result === "defeat"
        ? "victory"
        : "";
    },
    scoreCounter(cards) {
      let score = 0;

      /*
        Убираем карты со значением null
      */
      cards = cards.filter(n => !!n);

      /*
        Проверяем все карты
      */
      let aceCount = 0;
      cards.forEach(card => {
        const numberCard = /(\d+)[a-z]/i.exec(card); // дает очков = number
        if (numberCard) {
          score += Number(numberCard[1]);
        } else {
          const symbolCard = /([jqk])[a-z]/i.exec(card); // дает 10 очков
          if (symbolCard) {
            score += 10;
          } else {
            aceCount += 1;
          }
        }
      });

      /*
        Тут добавляем очки за все тузы
      */
      if (aceCount > 0) {
        if (score > 10) {
          score += aceCount; // Все тузы дают 1 очко
        } else {
          score += aceCount;
          if (score - 1 + 11 <= 21) {
            score -= 1;
            score += 11;
          }
        }
      }

      return score;
    },
    async updateHandStatus({ status, hand = "userHand" }) {
      if (!status) {
        return null;
      }

      await this.$refs[hand].animateStatus({ status });
    },
    async reverseHandCard({
      index = 1,
      hand = "dealerHand",
      card,
      cards,
      wait = true
    }) {
      let refHand = this.$refs[hand];
      await refHand.setCard({ card, index });
      let animatedScore = this.scoreCounter(cards.slice(0, index + 1));
      let status =
        cards.length === 2 && animatedScore === 21
          ? "blackjack"
          : animatedScore > 21
          ? "bust"
          : "";

      if (card && wait) {
        await Promise.all([
          refHand.animateReverse({ index }),
          refHand.animateScore({ score: animatedScore }),
          this.updateHandStatus({ status, hand })
        ]);
      } else if (card) {
        refHand.animateReverse({ index }).catch(console.error);
        refHand.animateScore({ score: animatedScore }).catch(console.error);
        this.updateHandStatus({ status, hand }).catch(console.error);
      }
    },
    async hitHandCards({
      index = 0,
      cards = [],
      hand = "userHand",
      wait = false
    }) {
      let newCards = cards.slice(index);

      if (!newCards.length) {
        return null;
      }

      let card = newCards[0];
      await this.$refs.deck.animate(
        this.$refs[hand].getCardAbsoluteCoordinates(index)
      );
      await this.reverseHandCard({ index, hand, card, wait, cards });

      return await this.hitHandCards({
        index: ++index,
        cards: cards,
        hand,
        wait
      });
    },
    async updateGame(game) {
      this.betMakerProps.inAnimation = true;
      const isGameNew = this.blackjack._id !== game._id;
      const isGameEnd = !game.active;

      this.$store.commit("blackjack/updateValues", game);

      if (isGameNew) {
        await Promise.all([
          this.$refs.dealerHand.animateRemove(),
          this.$refs.userHand.animateRemove()
        ]);
      }

      await this.hitHandCards({
        cards: game.userHand,
        index: this.userHand.cards.length,
        wait: !isGameNew,
        score: game.userScore
      });

      if (game.dealerHand[1] !== null && this.dealerHand.cards[1] === null) {
        await this.reverseHandCard({
          card: game.dealerHand[1],
          cards: game.dealerHand
        });
      }

      await this.hitHandCards({
        cards: game.dealerHand,
        index: this.dealerHand.cards.length,
        hand: `dealerHand`,
        wait: !isGameNew || isGameEnd,
        score: game.dealerScore
      });

      if (game.gameResult) {
        await Promise.all([
          this.updateHandStatus({ status: game.gameResult, hand: "userHand" }),
          this.updateHandStatus({
            status: this.reverseGameResult(game.gameResult),
            hand: "dealerHand"
          })
        ]);
      }

      this.betMakerProps.inAnimation = false;
      this.animatedGameData = _.cloneDeep(this.blackjack);
    },
    async hitBlackjackGame() {
      const game = (
        await this.$apollo.mutate({
          mutation: require("@/graphql/HitBlackjackGame.gql")
        })
      ).data.hitBlackjackGame;

      this.updateGame(game).catch(
        process.env.NODE_ENV === "development" ? console.error : () => {}
      );
    },
    async standBlackjackGame() {
      const game = (
        await this.$apollo.mutate({
          mutation: require("@/graphql/StandBlackjackGame.gql")
        })
      ).data.standBlackjackGame;

      this.updateGame(game).catch(
        process.env.NODE_ENV === "development" ? console.error : () => {}
      );
    },

    async createBlackjackGame(variables) {
      const game = (
        await this.$apollo.mutate({
          mutation: require("@/graphql/CreateBlackjackGame.gql"),
          variables
        })
      ).data.createBlackjackGame;
      this.animatedGameData = Object.assign(_.cloneDeep(this.blackjack), {
        number: null,
        salt: null,
        active: true
      });
      this.animatedGameData.bet.betAmount = game.bet.betAmount;
      this.animatedGameData.bet.profitAmount = 0;
      this.updateGame(game).catch(
        process.env.NODE_ENV === "development" ? console.error : () => {}
      );
    }
  },
  computed: {
    ...mapState({
      user: state => state.user,
      blackjack: state => state.blackjack
    })
  }
};
</script>

<style lang="scss">
#blackjack {
  .game-title {
    .game-title__text {
      padding-left: 9em;
    }

    .game-buttons__container {
      padding-right: 9em;
    }
  }
}
</style>
