<template>
  <div id="message" class="q-px-lg relative-position q-py-lg">
    <div
      class="text-small text-grey absolute message-time absolute-bottom-right q-pr-lg"
      style="transform: translateY(50%)"
    >
      {{ value.createdAt | moment("from", "now") }}
    </div>
    <div class="message__header flex full-width no-wrap">
      <div class="message-avatar q-mr-md relative-position">
        <q-avatar rounded>
          <q-img
              basic
            :src="user.avatar"
            placeholder-src="/img/avatar-fallback.png"
          />
        </q-avatar>
        <div
          class="avatar-level full-width text-center absolute-bottom absolute text-highlight-white text-uppercase text-small"
          style="transform: translateY(100%)"
        >
          <span class="text-amber-7 text-weight-bold text-highlight-orange">
            {{ user.level || 1 }}
          </span>
          lvl
        </div>
      </div>
      <div
        class="message-info flex column overflow-hidden full-width"
        style="flex: 0 1 auto"
      >
        <div
          class="message-info__header flex justify-between overflow-hidden items-center full-width no-wrap"
        >
          <div
            class="left flex column overflow-hidden full-width q-pr-md"
            style="flex: 0 1 auto"
          >
            <UserName v-bind:value="user"> </UserName>
            <div class="info text-grey" style="font-size: 0.9em">
              {{ getUserRoleNameById(user.role._id) }}
            </div>
          </div>
          <div class="right text-right" style="flex: 1 0 auto" v-if="reply">
            <q-btn
              v-if="!storeUser.role.permissions.includes('chatBan')"
              size="md"
              class="chat__header-btn reply-btn"
              @click="reply(Object.assign({}, value, { user }))"
            >
              <q-icon size="xs" name="fas fa-reply"> </q-icon>
            </q-btn>
            <q-fab
              v-else
              color="transparent"
              icon="keyboard_arrow_left"
              direction="left"
              square
              padding="0.5em"
              class="expand-btn"
            >
              <q-fab-action
                @click="reply(Object.assign({}, value, { user }))"
                icon="fas fa-reply"
                color="primary"
                class="expand-btn__action"
              />
              <q-fab-action
                @click="deleteMessage"
                v-if="!value.deleted"
                icon="fas fa-trash-alt"
                color="negative"
                class="expand-btn__action"
              />
            </q-fab>
          </div>
        </div>
        <div class="message__text q-pt-md">
          <keep-alive v-if="!value.deleted">
            <MessageText
                    @type-match="typeMatch"
                    :role="(user.role || {})._id"
                    v-bind:value="value.text"
            ></MessageText>
          </keep-alive>
          <span
            class="text-grey-8 text-small text-weight-medium"
            style="font-style: italic"
            v-else
            >Сообщение удалено</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserName from "./UserName";
import role from "@/mixins/role";
import MessageText from "./MessageText";
import { mapState } from "vuex";
import {chatMessage} from '@/howler'

export default {
  name: "Message",
  props: ["value", "reply"],
  components: { UserName, MessageText },
  mixins: [role],
  data() {
    return {
      user: this.value.user
    };
  },
  created() {
    this.user =
      this.user._id === this.storeUser._id ? this.storeUser : this.user;
  },
  methods: {
    typeMatch({type, args}){
      if(type === 'profileLink' && Math.abs(this.value.createdAt - this.ts.now()) < 1000 && args[0] == this.storeUser._id){
        chatMessage.play();
      }
    },
    async deleteMessage() {
      await this.$apollo.mutate({
        mutation: require("@/graphql/DeleteMessage.gql"),
        variables: { messageId: this.value._id }
      });
    }
  },
  computed: {
    ...mapState({
      storeUser: state => state.user,
      ts: state => state.time.ts,
    })
  }
};
</script>

