#message {
  display: flex;
  flex-direction: column;
  width: 100%;
  .message-time {
    opacity: 0;
    transition: opacity 1000ms;
  }
  &:hover {
    .message-time {
      opacity: 1;
    }
  }
  .message__text {
    overflow: hidden;
    width: 100%;
    word-wrap: break-word;
    overflow-wrap: break-word;
  }
  .message__header {
    display: flex;
    align-items: flex-start;
    .message-info {
      .expand-btn {
        border: 0.15em solid rgba(255, 255, 255, 0.15) !important;
        &:hover {
          border: 0.15em solid #73ad15 !important;
        }
        .expand-btn__action {
          .q-icon {
            font-size: 1.25em !important;
          }
        }
      }
      .reply-btn {
        color: #4c5d6e !important;
        .q-btn__wrapper {
          border-radius: 0.5em !important;
          border: 0.15em solid rgba(255, 255, 255, 0.15) !important;
          background: transparent !important;
        }
        &:hover {
          color: white;
          .q-btn__wrapper {
            border: 0.15em solid #73ad15 !important;
            background-color: transparent !important;
          }
        }
      }
    }
  }
}
</style>
