<template>
  <div
    id="hilo-card"
    :class="{ ['color-' + color]: color, ['card-' + card]: card }"
    :style="{ width: width + 'px', height: height + 'px' }"
  >
    <div class="forward">
      <div class="forward-logo">
        <!--        <img svg-inline src="../assets/images/logo.svg">-->
        <q-icon
          size="xl"
          v-html="jokerHistoryIcon"
          v-if="card === 'joker'"
        ></q-icon>
      </div>

      <div class="number number-up">
        {{ card }}
      </div>

      <div class="number number-down">
        {{ card }}
      </div>
    </div>

    <div class="backward"></div>
  </div>
</template>

<script>
import anime from "animejs";
import { mapState } from "vuex";
import jokerHistoryIcon from "./assets/joker-history-icon.svg";

export default {
  name: "HiloCard",
  props: {
    width: {
      type: Number,
      default: 205
    },
    height: {
      type: Number,
      default: 205
    },
    number: {
      type: String,
      default: "joker"
    }
  },
  data() {
    return {
      jokerHistoryIcon
    };
  },
  mounted() {},
  methods: {
    async appear({ startedAt = this.ts.now(), duration = 1000 } = {}) {
      const targets = this.$el;
      targets.classList.remove("reversed");

      const animation = anime({
        targets,
        duration,
        easing: "linear",
        translateX: [
          {
            value: 0,
            duration: duration * 0.25
          },
          {
            value: "-30%",
            duration: duration * 0.25,
            easing: "cubicBezier(0.39, 0.575, 0.565, 1)"
          },
          {
            value: 0,
            duration: duration * 0.5
          }
        ],
        rotateY: [
          {
            value: "0deg",
            duration: 0
          },
          {
            value: "-180deg",
            duration: duration
          }
        ],
        autoplay: false
      });

      let offset = this.ts.now() - startedAt;
      offset = offset > duration ? duration : offset < 0 ? 0 : offset;

      animation.seek(offset);

      if (offset >= duration) {
        return Promise.resolve();
      }

      animation.play();

      return animation.finished;
    },
    disappear({ startedAt = this.ts.now(), duration = 1000 } = {}) {
      const targets = this.$el;

      const animation = anime({
        targets,
        duration,
        easing: "cubicBezier(0.215, 0.61, 0.355, 1)",
        rotateY: [
          {
            value: "-180deg",
            duration: 0
          }
        ],
        translateX: [
          {
            value: "-300%",
            duration: duration
          },
          {
            value: 0,
            duration: 0
          }
        ],
        translateY: [
          {
            value: "-300%",
            duration: duration
          },
          {
            value: 0,
            duration: 0
          }
        ],
        autoplay: false
      });

      let offset = this.ts.now() - startedAt;
      offset = offset > duration ? duration : offset < 0 ? 0 : offset;
      animation.seek(offset);

      if (offset >= duration) {
        return Promise.resolve();
      }

      animation.play();

      return animation.finished;
    }
  },
  beforeDestroy() {
    // audio.cardAppear.stop();
    // audio.cardDisappear.stop();
  },
  computed: {
    ...mapState({
      ts: state => state.time.ts
    }),
    card() {
      return this.number.split("-")[0];
    },
    color() {
      return this.number.split("-")[1] || "";
    }
  }
};
</script>

