import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";
import _ from "lodash";

let reservedHistory = {};
const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    type: undefined,
    number: undefined,
    salt: undefined,
    hash: undefined,
    active: undefined,
    createdAt: undefined,
    startedAt: undefined,
    endedAt: undefined,
    betAllowed: undefined,
    bets: [],
    history: [],
    rouletteBetType: "red"
  }),
  getters: {
    betsByType(state) {
      return {
        red: state.bets.filter(bet => bet.rouletteBet.type === "red"),
        green: state.bets.filter(bet => bet.rouletteBet.type === "green"),
        black: state.bets.filter(bet => bet.rouletteBet.type === "black")
      };
    },
    sumOfBets(state) {
      return _.sumBy(state.bets, bet => bet.betAmount);
    },
    sumOfBetsByType(state, getters) {
      function sumByColor(color) {
        return _.sumBy(getters.betsByType[color], bet => bet.betAmount);
      }
      return [
        { type: "red", sum: sumByColor("red") },
        { type: "green", sum: sumByColor("green") },
        { type: "black", sum: sumByColor("black") }
      ];
    },
    betAllowed(state, getters, rootState) {
      return rootState.time.now < state.startedAt;
    },
    userBet(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"]
        ? state.bets.find(bet => bet.userId === rootState.user._id)
        : undefined;
    },
    isUserHaveBet(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"] ? !!getters.userBet : false;
    }
  },
  mutations: {
    addBet(state, bet){
      state.bets.push(bet)
    },
    reset(state){
      state.history = state.history.slice(0,30);
    },
    setRouletteBetType(state, type) {
      state.rouletteBetType = type;
    },

    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },
    unshiftHistory(state, game) {
      if (
        state.history.length &&
        typeof game.number === "number" &&
        game._id !== state.history[0]._id &&
        game._id !== reservedHistory._id
      ) {
        reservedHistory = game;
        let delay = 0;
        if (game._id === state._id) {
          delay = 10000;
        }
        setTimeout(() => {
          Vue.set(state, "history", [
            { _id: game._id, number: game.number, type: game.type },
            ...state.history
          ]);
        }, delay);
      }
    },
    updateValues(state, game) {
      if (game) {
        Object.keys(game).map(key => {
          if (typeof game[key] !== "undefined") {
            if(key === 'bets' && game[key] === null){
              return;
            }
            state[key] = game[key];
          }
        });
      }
    }
  },
  actions: {
    async loadMoreHistory({ commit }, { afterId }) {
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetRouletteHistory.gql"),
          variables: { afterId }
        })
      ).data.getRouletteHistory;

      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },
    async getGame({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetRouletteGame.gql")
      });
      const game = data.data.getRouletteGame;
      const history = data.data.getRouletteHistory;
      commit("updateValues", game);

      if (history[0] && history[0]._id === game._id) {
        commit("setHistory", history.slice(1));
        commit("unshiftHistory", game);
      } else {
        commit("setHistory", history);
      }

      if (game) dispatch("subscribeGameUpdated");
    },
    reset({commit}){
      commit('reset');
    },
    subscribeGameUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/RouletteUpdated.gql")
      });
      observer.subscribe({
        next: data => {
          commit("updateValues", data.data.rouletteUpdated);
          commit("unshiftHistory", data.data.rouletteUpdated);
        }
      });

      const observer1 = apolloClient.subscribe({
        query: require("@/graphql/RouletteBetAdded.gql")
      });
      observer1.subscribe({
        next: data => {
          commit("addBet", data.data.rouletteBetAdded);
        }
      });
    }
  }
};
