import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";

const apolloClient = apolloProvider.defaultClient;
let subscription;

export default {
  namespaced: true,
  state: () => (
    {
      _id: undefined,
      number: undefined,
      type: undefined,
      userNumber: undefined,
      multiplier: undefined,
      victory: undefined,
      bet: {},
      history: []
    }
  ),
  getters: {},
  mutations: {
    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },
    unshiftHistory(state, game) {
      let delay = 1000;
      setTimeout(() => {
        state.history.unshift(game);
      }, delay);
    },
    updateValues(state, game) {
      if (game) {
        Object.keys(game).map(key => {
          if (typeof game[key] !== "undefined") {
            state[key] = game[key];
          }
        });
      }
    },
    reset(state){

      state.history = state.history.slice(0,15);
      state._id = undefined;
    },
  },
  actions: {
    async loadMoreHistory({ commit }, { afterId }) {
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetDiceHistory.gql"),
          variables: { afterId }
        })
      ).data.getDiceHistory;

      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },
    async createGame({ commit }, variables) {
      const game = (
        await apolloClient.mutate({
          mutation: require("@/graphql/CreateDiceGame.gql"),
          variables
        })
      ).data.createDiceGame;
      commit("updateValues", game);
      return game;
    },
    async getGame({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetDiceGame.gql")
      });
      const game = data.data.getDiceGame;
      const history = data.data.getDiceHistory;
      commit("updateValues", game);
      commit("setHistory", history);
      dispatch("subscribeGameAdded");
    },
    reset({commit}){
      subscription.unsubscribe();
      commit('reset');
    },
    subscribeGameAdded({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/DiceAdded.gql")
      });
      subscription = observer.subscribe({
        next: data => {
          commit("unshiftHistory", data.data.diceAdded);
        }
      });
    }
  }
};
