<template>
  <div id="promocode" class="q-pa-sm q-px-md">
    <div class="text-white flex text-weight-medium items-end">
      <span style="font-size: 1.3em" class="flex items-center">
        {{ value.type.toLowerCase().indexOf("percentage") !== -1 ? "" : "$"
        }}{{
          value.type.toLowerCase().indexOf("percentage") === -1
            ? $options.filters.coinsToDollars(value.value)
            : value.value
        }}{{ value.type.toLowerCase().indexOf("percentage") !== -1 ? "%" : "" }}
        {{
          value.type.toLowerCase().indexOf("firstdeposit") !== -1
            ? "к депозиту"
            : value.type.toLowerCase().indexOf("deposit") !== -1
            ? "к депозиту"
            : ""
        }}
        {{
          value.minDepositAmount
            ? `от $${$options.filters.coinsToDollars(value.minDepositAmount)}`
            : ""
        }}
      </span>
    </div>

    <div class="text-uppercase  ">
      {{ value.code }}
    </div>
    <div class=" " v-if="!noExpireAt">
      годен до {{ new Date(value.expireAt) | moment("DD/MM/YY HH:mm:ss") }}
    </div>
    <q-icon
      style="font-size: 2em"
      class="copy-btn cursor-pointer "
      :name="copySuccess ? 'fas fa-check' : 'fas fa-copy'"
      @click="copy"
    >
    </q-icon>
    <div
      class="inactive text-uppercase text-weight-bold text-negative"
      style="font-size: 1.75em"
      v-if="(!value.activations || value.expireAt < Date.now()) && !active"
    >
      не действителен
    </div>
  </div>
</template>

<script>
import coins from "../mixins/coins";
import { copyToClipboard } from "quasar";

export default {
  name: "Promocode",
  props: ["value", "active", "noExpireAt"],
  mixins: [coins],
  data() {
    return {
      copySuccess: false
    };
  },
  methods: {
    async copy() {
      await copyToClipboard(this.value.code);
      this.copySuccess = true;
      setTimeout(() => (this.copySuccess = false), 3000);
    }
  }
};
</script>

<style lang="scss">
#promocode {
  height: 5.7142em;
  background: url("/img/promocode-bg.png") center center / 100% 100% no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: nowrap;
  position: relative;
  font-size: 1rem;
  .copy-btn {
    position: absolute;
    right: 0.75em;
    top: 50%;
    transform: translateY(-50%);
  }
  .inactive {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(black, 0.9);
  }
}
</style>
