<template>
  <div
    id="royal-battle-wheel"
    :style="{ width: size + 'px', height: size + 'px' }"
  >
    <div
      class="royal-battle-wheel__bg"
      :style="{ width: 2 * radius + 'px', height: 2 * radius + 'px' }"
    ></div>
    <div
      class="royal-battle-wheel__border"
      :style="{
        width: 2 * radius * 0.8 + 'px',
        height: 2 * radius * 0.8 + 'px',
        borderColor: currentBorderColor
      }"
    ></div>
    <div
      class="royal-battle-wheel__arrow"
      :style="{ top: (center - radius) * 1.5 + 'px' }"
    ></div>
    <div
      class="circle__animate"
      :style="{ width: size + 'px', height: size + 'px' }"
    ></div>
  </div>
</template>

<script>
import { SVG } from "@svgdotjs/svg.js";
import Chance from "chance";
import anime from "animejs";
import { mapState } from "vuex";
import _ from "lodash";
require("@svgdotjs/svg.filter.js");

export default {
  name: "RoyalBattleWheel",
  props: ["size", "slices"],
  data() {
    return {
      strokeWidth: 10,
      gaussianBlur: 5,
      currentSliceIndex: -1
    };
  },
  mounted() {
    this.draw();
  },
  watch: {
    size() {
      this.update();
    },
    slices() {
      this.update();
    }
  },
  methods: {
    rotate({
      duration = 10000,
      endedAt = this.ts.now() - 10000,
      sliceIndex = 0,
      animationTurns = 7,
      hash = "abc"
    } = {}) {
      let chance = new Chance(hash);
      let sliceAngle = this.slices[sliceIndex].ratio * this.totalDegrees;
      let offsetAngle = _.range(0, sliceIndex).reduce((p, c) => {
        p += this.padding + this.slices[c].ratio * this.totalDegrees;
        return p;
      }, 0);
      let lastAngle = 0,
        slicesAngles = this.slices.map(slice => {
          let result = lastAngle + slice.ratio * this.totalDegrees;
          lastAngle += result + this.padding;
          return result;
        });
      let degree =
        -offsetAngle -
        chance.integer({
          min: Math.floor(0.1 * sliceAngle),
          max: Math.floor(0.9 * sliceAngle)
        });
      let targets = this.$el.querySelector(".circle__animate");
      let animation = anime({
        targets,
        duration,
        autoplay: false,
        easing: "easeOutQuad",
        rotate: -360 * animationTurns + degree,
        update: () => {
          let currentAngle = Math.abs(
            Number(targets.style.transform.slice(7, -4))
          );
          let circleAngle = currentAngle - Math.floor(currentAngle / 360) * 360;
          let currentSliceIndex = slicesAngles.findIndex(sliceAngle => {
            return circleAngle <= sliceAngle;
          });
          this.currentSliceIndex =
            currentSliceIndex < 0 ? 0 : currentSliceIndex;
        }
      });
      let offset = this.ts.now() - endedAt;
      offset = offset > duration ? duration : offset < 0 ? 0 : offset;

      animation.seek(offset);

      if (offset >= duration) {
        return Promise.resolve();
      }

      animation.play();
      return animation.finished;
    },

    update() {
      this.draw();
      let targets = this.$el.querySelector(".circle__animate");
      targets.style.transform = `rotate(${0}deg)`;
      this.currentSliceIndex = -1;
    },
    draw() {
      let svg = this.$el.querySelector("svg");

      if (svg) {
        svg.remove();
      }

      let draw = SVG()
        .addTo(this.$el.querySelector(".circle__animate"))
        .size("100%", "100%");

      let group = draw.group();
      let sumOfSliceAngle = 0;
      this.slices.forEach(({ color, ratio }, index) => {
        let sliceAngle = ratio * this.totalDegrees;
        let sliceAngleInRadians = ((sliceAngle - 90) * Math.PI) / 180;
        let x = this.center + this.radius * Math.cos(sliceAngleInRadians);
        let y = this.center + this.radius * Math.sin(sliceAngleInRadians);
        let pathD = `M${this.center},${this.center - this.radius} A${
          this.radius
        } ${this.radius} 0 ${sliceAngle < 180 ? "0" : "1"} 1 ${x} ${y} A${
          this.radius
        } ${this.radius} 0 ${sliceAngle < 180 ? "0" : "1"} 0 ${
          this.center
        } ${this.center - this.radius} Z`;
        let path = group.path(pathD);
        path.fill(color);
        path.stroke({
          color,
          width: this.strokeWidth,
          linecap: "round",
          linejoin: "round"
        });
        path.attr({
          "fill-opacity": 0
        });
        path.rotate(
          sumOfSliceAngle + this.padding * index,
          this.center,
          this.center
        );
        sumOfSliceAngle += sliceAngle;
      });

      let gaussianBlur = this.gaussianBlur;
      group.filterWith(function(add) {
        let blur = add.gaussianBlur(gaussianBlur, gaussianBlur);
        add.blend(add.$source, blur);
        this.size("200%", "200%").move("-50%", "-50%");
      });
    }
  },
  computed: {
    currentBorderColor() {
      return this.currentSliceIndex < 0
        ? "#3b3f4e"
        : this.slices[this.currentSliceIndex].color;
    },
    padding() {
      let padding = (-10 / 45) * this.slices.length + 10;
      return this.slices.length === 1 ? 0.00001 : padding < 0 ? 0 : padding;
    },
    totalDegrees() {
      return 360 - this.slices.length * this.padding;
    },
    center() {
      return this.size / 2;
    },
    radius() {
      return (this.size - this.strokeWidth * this.gaussianBlur) / 2;
    },
    circumference() {
      //длина окружности
      return this.radius * 2 * Math.PI;
    },
    ...mapState({
      ts: state => state.time.ts
    })
  }
};
</script>

<style lang="scss">
#royal-battle-wheel {
  position: relative;
  z-index: 1;
  .circle__animate {
    will-change: transform;
  }
  .royal-battle-wheel__bg {
    position: absolute;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    z-index: -1;
    transform: translate(-50%, -50%);
    background: url("./assets/royal-battle-circle-bg.png") 67% 125% / 60% auto
      no-repeat;
  }
  .royal-battle-wheel__border {
    position: absolute;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    z-index: -2;
    transform: translate(-50%, -50%);
    border-width: 0.2em;
    border-style: solid;
  }
  .royal-battle-wheel__arrow {
    position: absolute;
    left: 50%;
    z-index: 1;
    width: 46px;
    height: 49px;
    transform: translate(-50%, 0%);
    background: url("./assets/royal-battle-circle-arrow@2x.png") center center /
      100% 100% no-repeat;
  }
}
</style>
