<template>
  <div id="case-bonus" class="relative-position full-width flex q-px-md-xl q-px-sm items-center">
    <q-btn
            to="/faq?id=tokens"
            flat
            class="absolute absolute-top-left q-mt-sm q-ml-sm"
            style="z-index: 1"
            round
            icon="fas fa-info-circle"
            color="white"
            v-close-popup
    >
      <q-tooltip
              content-class="bg-modal"
              content-style="font-size: 1em"
              anchor="top middle"
              self="center middle"
      >
        Что это?
      </q-tooltip></q-btn
    >
    <transition name="fade" mode="out-in" :duration="500">
      <div
        class="tape flex flex-center full-width full-height overflow-hidden"
        v-if="inAnimation"
      >
        <Tape key="tape" ref="tape" :elementWidth="100">
          <img
            alt="item"
            :key="index"
            :src="element.image"
            :style="`width:100px`"
            v-for="(element, index) in tapeItems"
          />
        </Tape>
      </div>

      <div
        class="flex full-width full-height items-center"
        key="default"
        v-else
      >
        <q-img basic :src="giftImg" height="73px" width="76px" class="gift"/>
        <div class="q-pl-lg flex column title">
          <span :class="$q.screen.lt.xl ? 'text-h5':'text-h4'">
            Гарантированный приз
          </span>
          <span class="text-grey" :class="$q.screen.lt.xl ? 'text-small':''">
            Пополняйте баланс и получайте
            <span class="text-amber-6 cursor-pointer"
              >токены<q-tooltip
                content-class="bg-modal"
                content-style="font-size: 1em;"
              >
                За пополнение баланса вы получаете 1 токен за каждые 10 000
                монет.
              </q-tooltip>
            </span>
            на счет!
          </span>
        </div>
        <div class="col relative-position full-height">
          <div
            class="divider absolute absolute-left q-ml-md"
            style="height: 60%; top: 50%; transform: translateY(-50%); width: 1px; background: rgba(255,255,255, 0.05)"
          ></div>
          <ApolloQuery
            class="absolute absolute-bottom"
            :variables="{ category: 'bonus' }"
            :query="
              gql => gql`
                query($category: String!) {
                  getCasesByCategory(category: $category) {
                    _id
                    name
                    image
                    tokens
                    items {
                      _id
                      image
                      price
                      rarity
                      rarityColor
                      buyPrice
                    }
                  }
                }
              `
            "
            @result="onResult"
          >
            <template v-slot="{ result: { error, data, loading }, isLoading }">
              <Loading v-if="loading"> </Loading>
              <q-carousel
                v-else-if="cases"
                v-model="selectedCaseIndex"
                swipeable
                animated
                control-type="flat"
                control-color="primary"
                padding
                ref="carousel"
                height="130px"
                class="bg-transparent rounded-borders"
              >
                <template v-slot:control>
                  <q-carousel-control
                    position="right"
                    :offset="$q.screen.lt.md?[0,45]:$q.screen.lt.xl ?[10,45]:[40,45]"
                    class="q-gutter-xs"
                    v-if="selectedCaseIndex !== cases.length - 1"
                  >
                    <q-btn
                      round
                      flat
                      color="orange"
                      @click="$refs.carousel.next()"
                    >
                      <img
                        src="../assets/img/token-arrow.png"
                        height="50px"
                        width="50px"
                      />
                    </q-btn>
                  </q-carousel-control>
                  <q-carousel-control
                    position="left"
                    v-if="selectedCaseIndex !== 0"
                    :offset="$q.screen.lt.md?[-5,45]:$q.screen.lt.xl ?[0,45]:[30,45]"
                    class="q-gutter-xs"
                  >
                    <q-btn
                      round
                      flat
                      color="orange"
                      @click="$refs.carousel.previous()"
                    >
                      <img
                        src="../assets/img/token-arrow.png"
                        height="50px"
                        width="50px"
                        style="transform: scaleX(-1)"
                      />
                    </q-btn>
                  </q-carousel-control>
                </template>
                <q-carousel-slide
                  v-for="(box, i) in cases"
                  :key="i"
                  :name="i"
                  class="column no-wrap flex-center"
                >
                  <div class="cursor-pointer text-center text-black">
                    <q-img basic :src="box.image" height="130px" width="184px" />
                    <q-tooltip
                      content-class="bg-modal"
                      content-style="font-size: 1em;"
                    >
                      Нажмите для просмотра содержимого кейса
                    </q-tooltip>
                    <q-popup-proxy
                      content-class="bg-transparent"
                      anchor="center middle"
                      self="center middle"
                    >
                      <q-banner dark dense>
                        <UserItemList
                          :items="steamItemsToUserItems(box.items)"
                          :hasMore="false"
                          :col="$q.screen.gt.sm ? 4 : 6"
                          style="max-width: 80vw; width: 35em; height: 30em;"
                          mode="mini"
                          :virtual-scroll-item-size="82"
                          :selected-items="box.items"
                        >
                        </UserItemList>
                      </q-banner>
                    </q-popup-proxy>
                  </div>
                </q-carousel-slide>
              </q-carousel>
            </template>
          </ApolloQuery>
        </div>
        <div class="flex column items-center buy-btn">
          <q-btn
            v-if="isAuthenticated"
            class="rounded-borders"
            outline
            :disable="tokens < (selectedCase.tokens || Infinity)"
            style="color: #3585ff;font-size: 1em"
            :style="{minWidth: $q.screen.gt.sm ? '12.5em': 0}"
            :loading="loading"
            @click="openCase"
          >
            <div
              class="text-white flex flex-center text-font-weigh-300 text-highlight"
            >
              <template v-if="$q.screen.gt.sm">Открыть за</template>
               {{ this.selectedCase.tokens }}
              <q-icon class="q-pl-md-xs q-pl-sm">
                <img
                  class=""
                  height="55px"
                  src="../assets/img/token.png"
                  alt="токен"
                />
              </q-icon>
            </div>
          </q-btn>
          <q-btn
            v-else
            class=" rounded-borders"
            outline
            style="color: #3585ff;font-size: 1em"
            no-caps
            @click="() => this.$root.$emit('authorizationDialog')"
          >
            <span class="text-white text-font-weigh-300 text-highlight">
              <q-icon name="fas fa-user" left size="xs"></q-icon>
              Авторизация
            </span>
          </q-btn>
          <span class="text-grey text-font-montserrat q-pt-sm text-small"
            >У вас {{ tokens || 0 }} токена(ов)</span
          >
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import giftImg from "../assets/img/bonus-case-gift.png";
import { mapGetters } from "vuex";
import Loading from "./Loading";
import UserItemList from "./UserItemList";
import Tape from "./Tape";
import _ from "lodash";
import Chance from "chance";
import gql from "graphql-tag";

const chance = new Chance();

export default {
  name: "CaseBonus",
  props: ["tokens"],
  components: {
    Loading,
    UserItemList,
    Tape
  },
  data() {
    return {
      giftImg,
      cases: [],
      tapeItems: [],
      loading: false,
      inAnimation: false,
      selectedCaseIndex: 0,
      timer: null
    };
  },
  beforeDestroy() {
    clearTimeout(this.timer);
  },
  methods: {
    async openCase() {
      this.loading = true;
      let {
        data: {
          openCase: { itemId }
        }
      } = await this.$apollo.mutate({
        mutation: gql`
          mutation {
            openCase(caseId: "${this.selectedCase._id}"){
               itemId
            }
          }
        `
      });
      this.$emit("update:tokens", this.tokens - this.selectedCase.tokens);
      this.tapeItems = _.range(0, 150).map(value => {
        return value !== 138
          ? chance.pickone(this.selectedCase.items)
          : this.selectedCase.items.find(item => item._id === itemId);
      });
      this.inAnimation = true;
      await this.$nextTick();
      this.timer = setTimeout(async () => {
        await this.$refs.tape.animate({
          duration: 15000,
          animateToElementWithIndex: 138,
          endedAt: Date.now() + 15000
        });
        this.timer = setTimeout(() => {
          this.inAnimation = false;
          this.loading = false;
          this.$q.dialog({
            title: "Поздравляем!",
            message: `Вы выиграли скин "${itemId}"! Скин находится в вашем инвентаре, Вы можете продать его или вывести в любой момент.`,
            cancel: false,
            persistent: true,
            dark: true
          });
        }, 3000);
      }, 750);
    },
    steamItemsToUserItems(items) {
      return items
        .map(item => {
          return {
            item,
            _id: item._id,
            soldPrice: item.buyPrice
          };
        })
        .sort((a, b) => b.soldPrice - a.soldPrice);
    },
    onResult({ data }) {
      if (data && data.getCasesByCategory) {
        this.cases = data.getCasesByCategory;
      }
    }
  },
  computed: {
    selectedCase() {
      return this.cases[this.selectedCaseIndex] || {};
    },
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  }
};
</script>

