<template>
  <div
    class="relative-position rounded-borders-2x full-width"
    :class="{ 'overflow-hidden': isAuthenticated }"
  >
    <template v-if="isAuthenticated">
      <q-btn
        class="full-width relative-position rounded-borders-2x full-height"
        color="primary"
        label="Сделать ставку"
        :disable="disable"
        style="font-size: 1em"
        :size="size"
        :style="contentStyle"
        padding="1.25em xs"
        @click="emitBetCreation()"
      >
      </q-btn>
      <q-btn
        flat
        class="absolute absolute-bottom full-width"
        icon="fas fa-angle-down"
        color="white"
        padding="0.5em 0"
        style="font-size: 0.5em; z-index: 1"
      >
        <q-tooltip content-class="bg-modal" content-style="font-size: 1em">
          Открыть инвентарь
        </q-tooltip>
        <q-popup-proxy
          content-class="bg-transparent"
          anchor="center middle"
          self="center middle"
        >
          <UserItemListPopup :disable="disableItems" @bet="emitBetCreation">
          </UserItemListPopup>
        </q-popup-proxy>
      </q-btn>
    </template>
    <q-btn
      v-else
      class="full-width rounded-borders-2x full-height"
      outline
      style="color: #3585ff;"
      no-caps
      :size="size"
      padding="1.25em xs"
      @click="() => this.$root.$emit('authorizationDialog')"
    >
      <span class="text-white text-font-weigh-300 text-highlight">
        <q-icon name="fas fa-user" left size="xs"></q-icon>
        Авторизация
      </span>
    </q-btn>
  </div>
</template>

<script>
import UserItemListPopup from "./UserItemListPopup";
import { mapGetters } from "vuex";

export default {
  name: "BetMakerBtn",
  components: {
    UserItemListPopup
  },
  props: {
    disable: {
      type: Boolean,
      default: false
    },
    disableItems: {
      type: Boolean,
      default: false
    },
    contentStyle: {
      type: String
    },
    size: {
      type: String,
      default: "md"
    },
    padding: {
      type: String,
      default: "1.25em xs"
    }
  },
  methods: {
    emitBetCreation(...args) {
      this.$emit("bet", ...args);
    }
  },
  computed: {
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  }
};
</script>

<style scoped></style>
