<template>
  <component :is="tag">
    <slot name="prepend" v-bind:animatedNumber="animatedNumber"> </slot>
    <span :class="contentClass" :style="contentStyle">
      {{ filter(animatedNumber) }}
    </span>
  </component>
</template>

<script>
import anime from "animejs";

export default {
  name: "AnimatedNumber",
  props: {
    contentClass: {
      type: [String, Object, Array],
      default: ""
    },
    contentStyle: {
      type: [String, Object, Array],
      default: ""
    },
    tag: {
      type: String,
      default: "span"
    },
    number: {
      type: Number,
      default: 0
    },
    duration: {
      type: Number,
      default: 1000
    },
    filter: {
      type: Function,
      default: v => v
    }
  },
  data() {
    return {
      animatedNumber: 0
    };
  },
  watch: {
    number() {
      this.animate();
    }
  },
  created() {
    this.animatedNumber = this.number;
  },
  methods: {
    animate() {
      const animation = anime({
        targets: this,
        animatedNumber: this.number,
        round: 1,
        easing: "linear",
        duration: this.duration
      });
      return animation.finished;
    }
  }
};
</script>

<style scoped></style>
