<template>
  <div
    id="user-item"
    class="text-right"
    :class="{
      'cursor-pointer user-item-mini':
        mode === 'mini' || mode === `deposit-mini`
    }"
    v-if="value.item"
    @click="click"
  >
    <div
      class="user-item__inner"
      :style="
        ` background: radial-gradient(circle at left 170%, ${value.item
          .rarityColor || '#034db0'} 30%, #0e1319 80%);`
      "
    >
      <div class="border"></div>
      <div
        class="item__status"
        :class="{ withdrawn: value.withdrawn, sold: value.sold }"
        v-if="value.withdrawn || value.sold"
      ></div>
      <div
        class="item__price flex items-baseline no-wrap absolute absolute-top-right q-pr-md q-pt-sm"
        v-if="mode === 'skinsPlus'"
      >
        <q-icon
          style="font-size: 0.9em"
          name="fas fa-ruble-sign"
          class="q-mr-sm text-highlight"
        >
        </q-icon>
        <span
          style="font-size: 0.9em"
          class="text-weight-bold text-highlight-white"
        >
          {{ value.priceRub }}
        </span>
      </div>
      <div
        class="item__price-mini item__price flex items-baseline no-wrap"
        v-if="mode === 'mini' || mode === `deposit-mini`"
      >
        <q-icon
          style="font-size: 0.9em"
          name="fas fa-sack-dollar"
          class="q-mr-sm text-highlight"
        >
        </q-icon>
        <span
          style="font-size: 0.9em"
          class="text-weight-bold text-highlight-white"
        >
          {{ (mode === `mini` ? value.soldPrice : value.item.price) | coins }}
        </span>
      </div>
      <template v-if="mode === 'normal'">
        <div
          class="item__actions"
          v-if="
            isAuthenticated &&
              user._id == value.userId &&
              !value.withdrawn &&
              !value.sold
          "
        >
          <ApolloMutation
            :mutation="require('../graphql/WithdrawUserItem.gql')"
            :variables="{ userItemId: value._id }"
            @done="onWithdrawItem"
            @error="onWithdrawItemError"
          >
            <template v-slot="{ mutate, loading, error }">
              <div>
                <q-btn
                  @click="mutate()"
                  :loading="loading"
                  label="Вывести"
                  :color="error ? 'negative' : 'positive'"
                  class="text-uppercase"
                  padding="xs sm"
                  :disable="
                    !user.tradeUrl || value.soldPrice > availableWithdrawAmount
                  "
                >
                </q-btn>
                <q-tooltip
                  content-class="bg-modal"
                  v-if="
                    !user.tradeUrl || value.soldPrice > availableWithdrawAmount
                  "
                  content-style="font-size: 1em"
                >
                  <span v-if="!user.tradeUrl">
                    Введите вашу трейд ссылку на странице профиля
                  </span>
                  <span v-else-if="value.soldPrice > availableWithdrawAmount">
                    Доступно для вывода
                    {{ availableWithdrawAmount | coinsWithoutShort }}
                  </span>
                </q-tooltip>
              </div>
            </template>
          </ApolloMutation>
          <ApolloMutation
            :mutation="require('../graphql/SoldUserItem.gql')"
            :variables="{ userItemId: value._id }"
            @done="onSoldItem"
          >
            <template v-slot="{ mutate, loading, error }">
              <q-btn
                @click="mutate()"
                :loading="loading"
                label="Продать"
                :color="error ? 'negative' : 'primary'"
                class="text-uppercase"
                padding="xs sm"
              >
              </q-btn>
            </template>
          </ApolloMutation>
        </div>
      </template>
      <template v-else-if="mode === 'market'">
        <div class="item__actions mode-market" v-if="isAuthenticated">
          <q-btn
            @click="buySteamItem"
            :loading="buyLoading"
            label="Купить"
            size="lg"
            :color="buyError ? 'negative' : 'positive'"
            class="text-uppercase"
            padding="xs sm"
            v-if="user.balance >= value.soldPrice"
          >
          </q-btn>
          <q-btn
            @click="toggleDepositDialog(true)"
            label="Пополнить"
            size="lg"
            color="primary"
            class="text-uppercase"
            padding="xs sm"
            v-else
          >
            <q-tooltip content-class="bg-modal" content-style="font-size: 1em;">
              Пополните баланс на
              {{ (value.soldPrice - user.balance) | coinsWithoutShort }} монет
              для совершения покупки
            </q-tooltip>
          </q-btn>
        </div>
      </template>
      <template v-else-if="mode === 'skinsPlus' && !value.withdrawn">
        <div class="item__actions mode-market" v-if="isAuthenticated">
          <div v-if="user.balance >= value.price">
            <q-btn
              @click="withdrawSkinsPlusItem"
              :loading="buyLoading"
              label="Вывести"
              size="lg"
              :color="buyError ? 'negative' : 'positive'"
              class="text-uppercase"
              padding="xs sm"
              :disable="!user.tradeUrl || user.recoup < value.price"
            >
            </q-btn>
            <q-tooltip
              content-class="bg-modal"
              v-if="!user.tradeUrl || value.price > user.recoup"
              content-style="font-size: 1em"
            >
              <span v-if="!user.tradeUrl">
                Введите вашу трейд ссылку на странице профиля
              </span>
              <span v-else-if="value.price > user.recoup">
                Доступно для вывода
                {{ user.recoup | coinsWithoutShort }}
              </span>
            </q-tooltip>
          </div>

          <q-btn
            @click="toggleDepositDialog(true)"
            label="Пополнить"
            size="lg"
            color="primary"
            class="text-uppercase"
            padding="xs sm"
            v-else
          >
            <q-tooltip content-class="bg-modal" content-style="font-size: 1em;">
              Пополните баланс на
              {{ (value.price - user.balance) | coinsWithoutShort }} монет для
              вывода этого предмета
            </q-tooltip>
          </q-btn>
        </div>
      </template>
      <div
        class=" absolute absolute-top-left q-pl-lg q-pt-md"
        v-if="quality"
        style="z-index: 1; font-size: 0.7em;  color: rgba(255, 255, 255, 0.5);"
      >
        {{ quality || "..." }}
      </div>
      <q-img
          basic
        :src="value.item.image"
        :ratio="100 / 72"
        spinner-color="white"
        style="width: 90%"
        placeholder-src="/img/steam-item-fallback.png"
      />
      <div
        class="full-width flex justify-between item__info no-wrap"
        :class="{
          'item__info-mini': mode === 'mini' || mode === `deposit-mini`
        }"
      >
        <div
          class="item__name text-left full-width overflow-hidden"
          :title="value.item._id"
        >
          <div class="name__title text-white">
            {{ name || "..." }}
          </div>
          <div class="name__market-name">
            {{ marketName || "..." }}
          </div>
        </div>
        <div
          class="item__price flex q-pt-md items-baseline no-wrap relative-position"
          style="flex: 1 0 auto"
          v-if="mode !== 'mini' && mode !== `deposit-mini`"
        >
          <span
            class="text-weight-bold text-small text-grey"
            style="text-decoration: line-through; position: absolute;right: 0;transform: translateY(-100%);"
            v-if="mode === 'skinsPlus'"
          >
            {{ value.item.buyPrice | coins }}
          </span>
          <q-icon
            style="font-size: 0.9em"
            name="fas fa-sack-dollar"
            class="q-mr-sm text-highlight"
          >
          </q-icon>
          <span
            style="font-size: 0.9em"
            class="text-weight-bold text-highlight-white"
          >
            <template v-if="mode === 'skinsPlus'">
              {{ value.price | coins }}
            </template>
            <template v-else>
              {{ value.soldPrice | coins }}
            </template>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import steamItem from "../mixins/steamItem";
import { mapGetters, mapState } from "vuex";
import dialogs from "../mixins/dialogs";

export default {
  name: "UserItem",
  props: {
    value: {
      type: Object
    },
    mode: {
      type: String,
      default: "normal"
    }
  },
  mixins: [steamItem, dialogs],
  data: () => ({
    buyError: false,
    buyLoading: false,
    name: "",
    marketName: "",
    quality: "",
    soldItemSuccess: false
  }),
  created() {
    this.handleName();
  },
  methods: {
    async buySteamItem() {
      let itemId = this.value.item._id;
      this.buyLoading = true;
      this.$q
        .dialog({
          title: "Приобретение скина",
          message: `Вы действительно хотите приобрести "${
            this.value.item._id
          }"? Монеты спишутся с вашего баланса, скин будет находиться в вашем инвентаре и будет стоить ${this.$options.filters.coinsWithoutShort(
            this.value.soldPrice
          )} монет. Вы можете продать его или вывести в любой момент.`,
          cancel: true,
          persistent: true,
          dark: true
        })
        .onOk(async () => {
          try {
            await this.$apollo.mutate({
              mutation: require("@/graphql/BuySteamItem.gql"),
              variables: { itemId }
            });
            this.$q.notify({
              type: "positive",
              position: "bottom-left",
              message: `Предмет добавлен в ваш инвентарь`,
              actions: [
                {
                  label: "Перейти",
                  color: "white",
                  handler: () => {
                    this.$router.push(`/profile/${this.user._id}`);
                  }
                }
              ]
            });
            ym(69160060, "reachGoal", "buy_p2p");
          } catch {
            this.buyError = true;
            setTimeout(() => {
              this.buyError = false;
            }, 3000);
          }
          this.buyLoading = false;
        })
        .onCancel(() => {
          this.buyLoading = false;
        });
    },
    async withdrawSkinsPlusItem() {
      let itemId = this.value._id;
      this.buyLoading = true;
      this.$q
        .dialog({
          title: "Вывод скина",
          message: `Вы действительно хотите вывести "${
            this.value.item._id
          }" стоимостью ${this.$options.filters.coinsWithoutShort(
            this.value.price
          )} монет? Деньги спишутся с вашего баланса, скин будет отправлен по трейд ссылке, которую вы указали в своем профиле.`,
          cancel: true,
          persistent: true,
          dark: true
        })
        .onOk(async () => {
          try {
            await this.$apollo.mutate({
              mutation: require("@/graphql/CreateSkinsPlusWithdrawInvoice.gql"),
              variables: { itemId }
            });

            this.$q.notify({
              type: "positive",
              position: "bottom-left",
              message: `Предмет успешно отправлен по вашей трейд ссылке`
            });
            this.value.withdrawn = true;
            this.$emit("input", this.value);
            ym(69160060, "reachGoal", "withdraw_skinsplus");
          } catch (e) {
            this.buyError = true;
            setTimeout(() => {
              this.buyError = false;
            }, 3000);
            this.$q.notify({
              type: "negative",
              html: true,
              multiLine: false,
              position: "bottom-left",
              message:
                e.message.indexOf("deposit") === -1
                  ? `Не удалось вывести данный предмет, проверьте правильность<br> вашей трейд ссылки,
        если ошибка повторяется<br> обратитесь в службу поддержки или<br> воспользуйтесь другими способами вывода средств.`
                  : "Для того чтобы воспользоваться выводом, нужно хотя бы раз пополнить Ваш баланс на сумму от 1000 монет.",
              actions: [{ label: "Ок", color: "white", handler: () => {} }]
            });
          }
          this.buyLoading = false;
        })
        .onCancel(() => {
          this.buyLoading = false;
        });
    },
    click(evt) {
      this.$emit("click", evt);
    },
    onSoldItem() {
      this.value.sold = true;
      this.$emit("input", this.value);
    },
    onWithdrawItemError(e) {
      let message = `Не удалось вывести данный предмет, проверьте правильность<br> вашей трейд ссылки,
        если ошибка повторяется<br> попробуйте продать скин и воспользоваться<br> другими способами вывода средств.`;

      if (e.message.indexOf("deposit") !== -1) {
        message =
          "Для того чтобы воспользоваться выводом, нужно хотя бы раз пополнить Ваш баланс на сумму от 1000 монет.";
      } else if (
        e.message.indexOf("No item was found at the specified price or lower.") !== -1
      ) {
        message =
          "Не удалось найти предмет по данной цене, Вы можете продать его и воспользоваться моментальным выводом.";
      }
      this.$q.notify({
        type: "negative",
        html: true,
        multiLine: false,
        position: "bottom-left",
        message,
        actions: [
          {
            label: "Ок",
            color: "white",
            handler: () => {}
          }
        ]
      });
    },
    onWithdrawItem() {
      this.value.withdrawn = true;
      this.$emit("input", this.value);
    },
    handleName() {
      const { name, marketName, quality } = this.splitMarketHashName(
        this.value.item._id
      );
      this.name = name;
      this.marketName = marketName;
      this.quality = quality;
    }
  },
  computed: {
    availableWithdrawAmount() {
      return this.isAuthenticated ? this.user.recoup : 0;
    },
    ...mapState({
      user: state => state.user
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  }
};
</script>

<style lang="scss">
