  padding: 0.35em;
  position: relative;
  &.user-item-mini {
    opacity: 0.5;
    &.active {
      opacity: 1;
    }
    &:hover {
      opacity: 1;
    }
    .user-item__inner {
      padding: 0.5em 0.75em;
    }
  }

  .user-item__inner {
    width: 100%;

    padding: 0.75em 1em;
    $border: 0 0 rgba(255, 255, 255, 0.15);
    $border-size: 0.05em;
    $neg-border-size: -$border-size;
    box-shadow: inset $border-size $border-size $border,
      inset $neg-border-size $neg-border-size $border;
    position: relative;
    .item__status {
      position: absolute;
      left: 0px;
      top: 0px;
      right: 0px;
      bottom: 0px;
      z-index: 2;
      &.sold {
        background: url("/img/inventory-item-sold.png") center center / 80% auto
          no-repeat rgba(black, 0.7);
      }
      &.withdrawn {
        background: url("/img/inventory-item-withdrawn.png") center center / 80%
          auto no-repeat rgba(black, 0.7);
      }
    }
    .border {
      position: absolute;
      left: 0px;
      top: 0px;
      right: 0px;
      bottom: 0px;
    }

    &:before,
    &:after,
    .border:before,
    .border:after {
      position: absolute;
      width: 0.5em;
      height: 0.5em;
      content: "";
      border-color: rgba(#fff, 0.5);
      border-style: solid;
    }

    &:before {
      left: 0px;
      top: 0px;
      border-width: 0.01em 0 0 0.01em;
    }

    &:after {
      right: 0px;
      top: 0px;
      border-width: 0.01em 0.01em 0 0;
    }

    .border:before {
      right: 0px;
      bottom: 0px;
      border-width: 0 0.01em 0.01em 0;
    }

    .border:after {
      left: 0px;
      bottom: 0px;
      border-width: 0 0 0.01em 0.01em;
    }
    &:hover {
      .item__actions {
        opacity: 1;
      }
    }
  }
  .item__price-mini {
    position: absolute;
    z-index: 2;
    right: 0.5em;
    top: 0.5em;
  }
  .item__actions {
    z-index: 2;
    position: absolute;
    top: 0.5em;
    right: 0.5em;
    left: 0.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    opacity: 0;
    transition: opacity 0.5s;
    &.mode-market {
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      background: rgba(black, 0.5);
      display: flex;
      justify-content: center;
      justify-items: center;
    }
  }
  .item__info-mini {
    .name__title {
      font-size: 0.8em;
    }
  }
  .item__info {
    margin-top: -2em;
    z-index: 1;
    position: relative;
  }
  .item__name {
    .name__title {
      font-size: 0.9em;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .name__market-name {
      font-size: 0.7em;
      color: rgba(255, 255, 255, 0.5);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
</style>
