    :query="require('@/graphql/GetSocialNetworkingBonus.gql')"
  >
    <template v-slot="{ result: { error, data, loading } }">
      <Loading v-if="loading"> </Loading>
      <div v-else class="wrapper-secondary">
        <div class="bonus-title q-mx-auto text-center q-pb-xl">
          <span
            class=" text-highlight text-uppercase q-pb-lg text-weight-medium"
          >
            Бонусная система
          </span>
        </div>
        <CaseBonus :tokens.sync="(getSocialNetworkingBonus || {}).tokens"  class="q-mb-md" />
        <div class="row justify-center q-pt-md">
          <div
            class="col-md-2 col-xs-6 q-px-xs"
            v-for="bonus in socialNetworkingBonuses"
            :key="bonus.value"
          >
            <div
              class="bonus q-pt-md"
              :style="{ opacity: bonus.link ? 1 : 0.6 }"
            >
              <div class="bonus__container q-pa-lg ">
                <div
                  class="bonus-icon q-mx-auto q-mb-md"
                  :class="{
                    active:
                      bonus.took ||
                      (
                        (data || {}).getSocialNetworkingBonus || {
                          socialNetworkingBonuses: []
