                      ).socialNetworkingBonuses.indexOf(bonus.value) !== -1
                  }"
                >
                  <q-icon
                    :name="bonus.icon"
                    class="bonus-q-icon text-highlight"
                  >
                  </q-icon>
                  <q-icon
                    :name="
                      !bonus.took ||
                      (
                        (data || {}).getSocialNetworkingBonus || {
                          socialNetworkingBonuses: []
                        }
                      ).socialNetworkingBonuses.indexOf(bonus.value) !== -1
                        ? 'fas fa-lock-alt'
                        : 'fas fa-check'
                    "
                    class="took-icon"
                  >
                  </q-icon>
                </div>
                <div
                  class="text-center text-highlight q-py-lg text-small text-weight-light"
                >
                  Подписаться на<br />{{ bonus.name }}
                </div>
                <div
                  class="text-center bonus-amount text-weight-medium q-mb-lg"
                >
                  ${{ bonus.bonus | coinsToDollars }}
                </div>
                <template v-if="isAuthenticated">
                  <q-btn
                    :color="bonus.link ? 'primary' : 'grey'"
                    :label="bonus.link ? 'Получить' : 'Скоро...'"
                    class="full-width"
                    :disable="!bonus.link"
                    @click="takeSocialNetworkingBonus(bonus)"
                    v-if="
                      !bonus.took &&
                        (
                          (data || {}).getSocialNetworkingBonus || {
                            socialNetworkingBonuses: []
                          }
                        ).socialNetworkingBonuses.indexOf(bonus.value) === -1
                    "
                  >
                  </q-btn>
                  <q-btn
                    color="positive"
                    label="Выполнено"
                    class="full-width"
                    v-else
                  >
                  </q-btn>
                </template>
                <q-btn
                  v-else
                  class="full-width"
                  outline
                  style="color: #3585ff;"
                  no-caps
                  @click="() => $root.$emit('authorizationDialog')"
                >
                  <span class="text-white text-font-weigh-300 text-highlight">
                    <q-icon
                      name="fas fa-user"
                      class="q-mr-xs"
                      size="xs"
                    ></q-icon>
                    Авторизация
                  </span>
                </q-btn>
              </div>
            </div>
          </div>
        </div>
        <div class="full-width   text-small q-pt-lg q-pb-xl">
          <div
            style="max-width: 750px; color: #4d586d"
            class="text-center q-mx-auto"
            :class="{ 'text-negative': takeSocialNetworkingBonusError }"
          >
            Сумма Ваших депозитов должна быть не менее 10 000 монет, с момента
            запуска данной системы, чтобы получить бонус за подписки на
            социальные сети. Если Вы уже подписаны, то нажмите на кнопку
            получения бонуса. Бонус за подписку выдается один раз.
          </div>
        </div>
        <div class="full-width steam-bonus__container">
          <div class="row">
            <div
              class="col-md-4 col-xs-12 q-py-md-none q-py-xs-lg flex items-stretch justify-center relative-position no-wrap"
            >
              <div
                class="flex items-center justify-between q-mr-lg steam-icon__container"
              >
                <div class="bonus-icon steam">
                  <q-icon
                    name="fab fa-steam"
                    class="bonus-q-icon text-highlight"
                  >
                  </q-icon>
                </div>
              </div>
              <div class="flex justify-center column">
                <div class="bonus-amount q-pa-none">
                  {{ 5 }}
                  {{ steamNickChangedDaysOffset === 7 ? "×10" : "" }} монет
                </div>
                <div
                  class="text-highlight text-small text-weight-light q-mb-sm"
                >
                  Ежедневный бонус
                </div>
                <q-btn
                  v-if="isAuthenticated && user.provider === 'steam'"
                  color="amber-7"
                  text-color="black"
                  class="full-width"
                  @click="takeSteamNickBonus"
                  :disable="steamNickCountdown > 0"
                  :loading="takeSteamNickBonusLoading"
                >
                  <countdown
                    v-if="steamNickCountdown > 0"
                    :time="steamNickCountdown"
                  >
                    <template slot-scope="props">
                      Ждите {{ padStart(props.hours) }}:{{
                        padStart(props.minutes)
                      }}:{{ padStart(props.seconds) }}
                    </template>
                  </countdown>
                  <span v-else>
                    Получить бонус
                  </span>
                </q-btn>
                <q-btn
                  v-else
                  class="full-width "
                  outline
                  style="color: #3585ff;"
                  no-caps
                  @click="() => $root.$emit('authorizationDialog')"
                >
