                  <div
                    class="text-small text-negative absolute text-center"
                    style="left: 25px; right: 25px; bottom: 20px"
                    v-show="takeSteamNickBonusError"
                  >
                    Вы не выполнили условия акции или с момента смены ника
                    прошло менее 24 часов.
                  </div>
                </q-slide-transition>
              </div>
              <div
                class="divider absolute absolute-right"
                style="height: 80%; top: 50%; transform: translateY(-50%)"
              ></div>
            </div>
            <div
              class="col-md-8 col-xs-12 relative-position flex items-center justify-center q-pa-md-xl q-pa-xs-none"
            >
              <div
