                :class="{ 'column-xs': $q.screen.lt.md }"
              >
                <div
                  class="flex items-center q-pr-md-lg q-pr-xs-none q-mx-md-none q-mx-xs-auto"
                >
                  <q-img
                      basic
                    src="/img/bonus-steam-nick-x10.svg"
                    height="100px"
                    width="150px"
                  >
                  </q-img>
                </div>
                <div
                  :class="{ 'text-center': $q.screen.lt.md }"
                  style="color: #afb3bb"
                >
                  Используйте домен
                  <span style="color: #ffc600">csgotune.com</span> в нике Steam
                  и получайте бонусы каждый день! А через 7 дней получите
                  десятикратный бонус. <br /><br />
                  Акция работает с условием совершения депозитов на этой неделе
                  суммой не менее 5 000 монет.
                </div>
              </div>
              <div class="absolute bonus-steam-nick-days">
                <div class="flex no-wrap">
                  <div
                    :key="n"
                    class="day"
                    v-for="n in 7"
                    :class="{ active: n <= steamNickChangedDaysOffset }"
                  >
                    {{ n }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </ApolloQuery>
</template>

<script>
import coins from "../mixins/coins";
import { mapGetters, mapState } from "vuex";
import Loading from "../components/Loading";
import padStart from "../mixins/padStart";
import CaseBonus from "../components/CaseBonus";

export default {
  name: "SocialNetworkingBonus",
  mixins: [coins, padStart],
  meta: {
    title: "Бонусная система"
  },
  components: {
    Loading,
    CaseBonus
  },
  data() {
    return {
      takeSocialNetworkingBonusError: false,
      takeSteamNickBonusError: false,
      takeSteamNickBonusLoading: false,
      socialNetworkingBonuses: [
        {
          value: "vkontakte",
          name: "Вконтакте",
          icon: "fab fa-vk",
          bonus: 50,
          link: "https://vk.com/csgotune",
          counter: 0,
          took: false
        },
        {
          value: "telegram",
          name: "Telegram",
          icon: "fab fa-telegram-plane",
          bonus: 50,
          link: "https://t.me/csgotune",
          counter: 0,
          took: false
        },
        {
          value: "instagram",
          name: "Instagram",
          icon: "fab fa-instagram",
          bonus: 50,
          link: "",
          counter: 0,
          took: false
        },
        {
          value: "steam",
          name: "Steam",
          icon: "fab fa-steam-symbol",
          bonus: 50,
          link: "",
          counter: 0,
          took: false
        },
        {
          value: "youtube",
          name: "YouTube",
          icon: "fab fa-youtube",
          bonus: 50,
          link: "",
          counter: 0,
          took: false
        },
        {
          value: "tiktok",
          name: "TikTok",
          icon: "fab fa-tiktok",
          bonus: 50,
          link: "",
          counter: 0,
          took: false
        }
      ],
      getSocialNetworkingBonus: null,
      steamNickCountdown: 0
    };
  },

  computed: {
    steamNickChangedDaysOffset() {
      if (
        !this.getSocialNetworkingBonus ||
        !this.getSocialNetworkingBonus.steamNickChangedAt
      ) {
        return 1;
      }

      const timeOffset =
        this.ts.now() - this.getSocialNetworkingBonus.steamNickChangedAt;
      let daysOffset = Math.floor(timeOffset / (24 * 60 * 60 * 1000)) || 1;
      if (daysOffset > 7) {
        daysOffset -= Math.floor(daysOffset / 7) * 7;
        if (daysOffset === 0) {
          daysOffset = 7;
        }
      }
      return daysOffset;
    },
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    }),
    ...mapState({
      user: state => state.user,
      ts: state => state.time.ts
    })
  },
  methods: {
    async takeSteamNickBonus() {
      this.takeSteamNickBonusLoading = true;
      try {
        let {
          data: { takeSteamNickBonus }
        } = await this.$apollo.mutate({
          mutation: require("@/graphql/TakeSteamNickBonus.gql")
        });
        this.getSocialNetworkingBonus.lastSteamNickBonusAt =
          takeSteamNickBonus.lastSteamNickBonusAt;
        this.getSocialNetworkingBonus.steamNickChangedAt =
          takeSteamNickBonus.steamNickChangedAt;
        this.takeSteamNickBonusError = false;
      } catch {
        this.takeSteamNickBonusError = true;
      }
      this.takeSteamNickBonusLoading = false;
    },
    onGetSocialNetworkingBonus(result) {
      if (result.data) {
        this.getSocialNetworkingBonus = result.data.getSocialNetworkingBonus;
        this.steamNickCountdown = this.getSocialNetworkingBonus
          ? this.getSocialNetworkingBonus.lastSteamNickBonusAt +
            24 * 60 * 60 * 1000 -
            this.ts.now()
          : 0;
      }

      return result;
    },
    async takeSocialNetworkingBonus(bonus) {
      this.takeSocialNetworkingBonusError = false;
      if (!bonus.counter) {
        bonus.counter++;
        window.open(bonus.link, "_blank");
      } else {
        try {
          await this.$apollo.mutate({
            mutation: require("@/graphql/TakeSocialNetworkingBonus.gql"),
            variables: { socialNetworkingName: bonus.value }
          });
          bonus.took = true;
        } catch {
          this.takeSocialNetworkingBonusError = true;
        }
      }
    }
  }
};
</script>

