<template>
  <q-dialog v-model="jackpotDialog" @hide="onHide">
    <q-card class="jackpot-dialog">
      <div class="jackpot-dialog__title">
        <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
          <q-space />
          <q-btn icon="close" flat round dense v-close-popup />
        </q-card-section>
        <div class="flex column items-center q-pb-xl q-pt-md">
          <h5 class="text-weight-medium q-ma-none q-mb-sm">
            ВЫИГРЫШ
          </h5>
          <span class="text-grey text-small">
            Вы сорвали джекпот, поздравляем! Ваш выигрыш:
          </span>
        </div>
      </div>

      <q-card-section class="q-py-xl flex items-center justify-center column">
        <div
          class="flex jackpot-sum column items-center q-px-lg q-pb-lg q-pt-xs"
        >
          <q-icon name="fas fa-sack-dollar" class="text-highlight q-mb-xs" />
          <span class="text-weight-medium text-highlight">
            {{
              (jackpotHistory.length ? jackpotHistory[0].sumOfJackpot : 0)
                | coinsWithoutShort
            }}
          </span>
        </div>
        <q-btn
          class="q-px-lg q-py-sm q-mt-xl"
          @click="() => (this.jackpotDialog = false)"
          outline
          style="color: #3585ff;"
          no-caps
        >
          <span class="text-white text-font-weigh-300">
            Забрать награду
          </span>
        </q-btn>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import dialogs from "../mixins/dialogs";
import { mapState } from "vuex";
import coins from "../mixins/coins";

export default {
  name: "JackpotDialog",
  mixins: [dialogs, coins],
  data() {
    return {
      jackpotDialog: false
    };
  },
  created() {
    this.jackpotDialog = this.jackpotDialogShow;
  },
  watch: {
    jackpotDialogShow(newValue) {
      this.jackpotDialog = newValue;
    }
  },
  computed: {
    ...mapState({
      user: state => state.user,
      jackpotHistory: state => state.jackpot.history,
      jackpotDialogShow: state => state.dialog.jackpotDialogShow
    })
  },
  methods: {
    onHide() {
      this.toggleJackpotDialog(false);
    }
  }
};
</script>

