export default {
  methods: {
    urlBase64ToUint8Array(base64String) {
      let padding = "=".repeat((4 - (base64String.length % 4)) % 4);
      let base64 = (base64String + padding)
        .replace(/-/g, "+")
        .replace(/_/g, "/");

      let rawData = window.atob(base64);
      let outputArray = new Uint8Array(rawData.length);

      for (let i = 0; i < rawData.length; ++i) {
        outputArray[i] = rawData.charCodeAt(i);
      }
      return outputArray;
    },
    async getSubscription() {
      if (Notification.permission !== "granted" || !navigator.serviceWorker) {
        return null;
      }
      let registration = await navigator.serviceWorker.ready;
      let subscription = await registration.pushManager.getSubscription();
      return subscription ? JSON.parse(JSON.stringify(subscription)) : null;
    },
    async requestPermission() {
      let permission = await Notification.requestPermission();
      if (permission === "granted" && navigator.serviceWorker) {
        let registration = await navigator.serviceWorker.ready;
        let subscription = await registration.pushManager.getSubscription();

        if (!subscription) {
          let {
            data: { getWebPushVapidPublicKey }
          } = await this.$apollo.mutate({
            mutation: require("@/graphql/GetWebPushVapidPublicKey.gql")
          });
          const convertedVapidKey = this.urlBase64ToUint8Array(
            getWebPushVapidPublicKey
          );
          subscription = await registration.pushManager.subscribe({
            userVisibleOnly: true,
            applicationServerKey: convertedVapidKey
          });
        }

        subscription = JSON.parse(JSON.stringify(subscription));

        await this.$apollo.mutate({
          mutation: require("@/graphql/WebPushRegister.gql"),
          variables: {
            subscription: {
              endpoint: subscription.endpoint,
              keys: subscription.keys
            }
          }
        });
      }
    }
  }
};
