import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    chatWidth: 0,
    chatOpened: false,
    chatMessages: { public: [] },
    chatSubscriptions: {}
  }),
  getters: {},
  mutations: {
    reset(state, chatId){
      let length = state.chatMessages[chatId].length
      if(length > 25){
        state.chatMessages[chatId] = state.chatMessages[chatId].slice(length-25,length);
      }
    },
    setChatMessages(state, { chatId, messages }) {
      Vue.set(state.chatMessages, chatId, messages);
    },
    unsetChatSubscription(state, chatId) {
      delete state.chatSubscriptions[chatId];
    },
    setChatSubscription(state, { chatId, subscription }) {
      state.chatSubscriptions[chatId] = subscription;
    },
    unshiftChatMessages(state, { chatId, messages }) {
      Vue.set(state.chatMessages, chatId, [
        ...messages,
        ...state.chatMessages[chatId]
      ]);
    },
    updateChatMessage(state, { chatId, message }) {
      let index = state.chatMessages[chatId].findIndex(
        v => v._id === message._id
      );

      if (index !== -1) {
        let merge = {};
        Object.keys(message).map(key => {
          if (typeof message[key] !== "undefined" && message[key] !== null) {
            merge[key] = message[key];
          }
        });
        Vue.set(
          state.chatMessages[chatId],
          index,
          Object.assign({}, state.chatMessages[chatId][index], merge)
        );
      }
    },
    addChatMessage(state, { chatId, message }) {
      Vue.set(
        state.chatMessages[chatId],
        state.chatMessages[chatId].length,
        message
      );
    },
    changeChatOpenedState(state, open) {
      if (typeof open === `boolean`) {
        state.chatOpened = open;
      } else {
        state.chatOpened = !state.chatOpened;
      }
    },
    changeChatWidth(state, width) {
      state.chatWidth = width || 0;
    }
  },
  actions: {
    async createPublicMessage(_, { chatId = "public", text }) {
      return (
        await apolloClient.mutate({
          mutation: require("@/graphql/CreatePublicMessage.gql"),
          variables: {
            chatId,
            text
          }
        })
      ).data.createPublicMessage;
    },
    async getMessages({ commit, dispatch }, { chatId, afterId }) {
      const messages = (
        await apolloClient.query({
          query: require("@/graphql/GetMessages.gql"),
          variables: {
            chatId,
            afterId
          }
        })
      ).data.getMessages;

      if (afterId && messages) {
        commit("unshiftChatMessages", { chatId, messages });
      } else if (messages) {
        commit("setChatMessages", { chatId, messages });
        dispatch("subscribeMessageSubscription", { chatId });
      }

      return messages;
    },
    unsubscribeMessageSubscription({ state, commit }, { chatId }) {
      if (state.chatSubsriptions[chatId]) {
        state.chatSubsriptions[chatId].unsubscribe();
        commit("unsetChatSubscription", chatId);
      }
    },
    subscribeMessageSubscription({ state, commit }, { chatId }) {
      if (state.chatSubscriptions[chatId]) {
        return;
      }

      const observer = apolloClient.subscribe({
        query: require("@/graphql/MessageSubscription.gql"),
        variables: {
          chatId
        }
      });

      let subscription = observer.subscribe({
        next: data => {
          let message = data.data.messageSubscription;
          if (message.createdAt !== message.updatedAt) {
            commit("updateChatMessage", { message, chatId });
          } else {
            commit("addChatMessage", { message, chatId });
          }
        }
      });

      commit("setChatSubscription", { chatId, subscription });
    },
    openChat({ commit }) {
      commit("changeChatOpenedState", true);
    },
    closeChat({ commit }) {
      commit("changeChatOpenedState", false);
    },
    toggleChat({ commit }) {
      commit("changeChatOpenedState");
    }
  }
};
