export default {
  data() {
    return {
      lastScrollPos: 0,
      lastScrollDelta: 0,
      scrollMomentumID: 0
    };
  },
  beforeDestroy() {
    this.cancelMomentumTracking();
  },
  methods: {
    momentumLoop() {
      const currentScrollPosition = this.$refs.scrollArea.getScrollPosition();
      this.$refs.scrollArea.setScrollPosition(
        currentScrollPosition - this.lastScrollDelta
      );
      this.lastScrollPos = this.$refs.scrollArea.getScrollPosition();

      this.lastScrollDelta *= 0.95;

      if (Math.abs(this.lastScrollDelta) > 0.5) {
        this.scrollMomentumID = requestAnimFrame(this.momentumLoop);
      } else {
        this.lastScrollDelta = 0;
      }
    },
    cancelMomentumTracking() {
      cancelAnimationFrame(this.scrollMomentumID);
    },
    async handlePan({ direction, delta, isFinal }) {
      const deltaAxis =
        direction === "up" || direction === "down" ? delta.y : delta.x;
      this.cancelMomentumTracking();

      const currentScrollPosition = this.$refs.scrollArea.getScrollPosition();
      this.$refs.scrollArea.setScrollPosition(
        currentScrollPosition - deltaAxis
      );
      this.lastScrollPos = this.$refs.scrollArea.getScrollPosition();

      if (isFinal) {
        this.scrollMomentumID = requestAnimFrame(this.momentumLoop);
      } else {
        this.lastScrollDelta = deltaAxis;
      }
    }
  }
};
