<template>
  <div id="hilo" class="q-pt-lg q-pb-lg q-px-xs-md q-px-md-none">
    <div class="roulette__inner wrapper-secondary">
      <GameTitle
        :game="hilo"
        :countdown="countdown"
        :sumOfBets="sumOfBets"
        gameName="Hi Lo"
        :gameIcon="hiloIcon"
        class="q-mb-md"
      >
      </GameTitle>
      <div class="row q-mb-xl">
        <div
          class="col-md-4 col-xs-12 order-xs-last order-md-none q-pr-md-lg flex column items-stretch"
        >
          <div class="flex column justify-center" style="flex-grow: 1">
            <div
              class="q-pb-md q-pb-md-sm"
              v-for="(btn, i) in mainBetsButtons"
              :key="i"
            >
              <q-btn
                class="full-width hilo-btn"
                :class="[
                  `btn-${btn.type}`,
                  { active: btn.type === betMakerProps.type }
                ]"
                @click="changeType(btn.type)"
                :key="btn.type"
              >
                <div class="flex justify-between full-width">
                  <div>{{ btn.multiplier.toFixed(2) }}X</div>
                  <q-icon v-html="btn.icon"></q-icon>
                  <div>{{ btn.chance.toFixed(2) }}%</div>
                </div>
              </q-btn>
            </div>
          </div>
          <BetMaker
            class="full-width"
            v-model="betMakerProps"
            game-type="hilo"
            @bet="createHiloBet"
          ></BetMaker>
        </div>
        <div
          class="flex items-stretch col-md-4 col-xs-9 q-pr-lg"
          style="min-height: 300px"
        >
          <div
            class="full-height full-width hilo__deck-wrapper  q-px-lg q-pb-xl q-pt-md "
          >
            <div class="full-height hilo__deck full-width">
              <HiloDeck
                :number="hilo.number"
                :width="hiloCardWidth"
                :height="hiloCardHeight"
                :startedAt="hilo.startedAt"
                :previous-number="hilo.previousNumber"
              ></HiloDeck>
              <q-resize-observer @resize="updateCardSize" />
            </div>
          </div>
        </div>

        <div
          class="col-md-1 col-xs-3 flex items-stretch hilo__history-container"
        >
          <div class="history-title">История</div>
          <div class="hilo__history full-width q-pt-xl">
            <VirtualHistory
              :horizontal="false"
              :items="hilo.history"
              :virtual-scroll-item-size="63"
              :virtual-scroll-slice-size="25"
              class="history__inner"
              :offset="500"
              :style="{ maxHeight: `calc(${historyHeight}px - 2.625em)` }"
              @load="loadMoreHistory"
            >
              <template v-slot="{ item }">
                <div class="history__item-container">
                  <div
                    class="history__item"
                    :key="item._id"
                    :class="{
                      'item-red': item.number.indexOf('red') !== -1,
                      'item-black': item.number.indexOf('black') !== -1,
                      'item-joker': item.number === 'joker'
                    }"
                  >
                    <span v-if="item.number !== 'joker'">{{
                      item.number.split("-")[0]
                    }}</span>
                    <q-icon v-html="jokerHistoryIcon" v-else></q-icon>
                  </div>
                </div>
              </template>
            </VirtualHistory>
            <q-resize-observer @resize="updateHistoryHeight" />
          </div>
        </div>
        <div class="row col-md-3 col-xs-12 q-pl-md-lg q-pt-md q-pt-md-none">
          <div
            class="col-xs-6 col-md-12 q-px-xs q-px-md-none q-pb-md q-pb-md-xs"
            v-for="(btn, i) in additionalBetsButtons"
            :key="i"
          >
            <q-btn
              class="full-width hilo-btn"
              :class="[
                `btn-${btn.type}`,
                { active: btn.type === betMakerProps.type }
              ]"
              :key="btn.type"
              @click="changeType(btn.type)"
            >
              <div class="flex justify-between full-width">
                <div>{{ typeNames[btn.type] }}</div>
                <div>{{ btn.multiplier.toFixed(2) }}X</div>
              </div>
            </q-btn>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <BetList :value="hilo.bets" game-type="hilo"> </BetList>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import store from "@/store";
import { mapState, mapGetters } from "vuex";
import padStart from "../../mixins/padStart";
import BetMaker from "../../components/BetMaker";
import BetList from "../../components/BetList";
import hiloIcon from "@/../public/img/game-icons/hilo-icon.svg";
import hiIcon from "./assets/hi-icon.svg";
import loIcon from "./assets/lo-icon.svg";
import jokerIcon from "./assets/joker-icon.svg";
import jokerHistoryIcon from "./assets/joker-history-icon.svg";
import HiloDeck from "./HiloDeck";
import VirtualHistory from "../../components/VirtualHistory";
import GameTitle from "../../components/GameTitle";
import {betSound} from '@/howler'

export default {
  name: "Hilo",
  mixins: [padStart],
  meta: {
    title: "Hilo"
  },
  data() {
    return {
      jokerHistoryIcon,
      hiloIcon,
      isMounted: false,
      countdown: 0,
      loadingHistory: false,
      hiloCardWidth: 0,
      hiloCardHeight: 0,
      historyHeight: 0,
      betMakerProps: {
        type: "joker",
        betAmount: 100
      },
      typeNames: {
        red: "Красный",
        black: "Черный",
        numbers: "2-9",
        letters: "J,Q,K,A",
        ka: "K,A",
        a: "A"
      },
      lastBetsCount: 0,
    };
  },
  components: {
    BetMaker,
    BetList,
    GameTitle,
    HiloDeck,
    VirtualHistory
  },
  watch: {
    'hilo.bets': {
      handler(newValue = []){
        if(this.lastBetsCount < newValue.length){
          betSound.play();
        }
        this.lastBetsCount = newValue.length;
      }
    },
    "hilo.startedAt": {
      immediate: true,
      handler: function(newValue) {
        if (newValue) {
          let result = this.hilo.startedAt - this.now;
          this.countdown = result < 0 ? 0 : result;
        }
      }
    }
  },
  async mounted() {
    this.isMounted = true;
  },
  methods: {
    updateHistoryHeight({ height }) {
      this.historyHeight = height;
    },
    updateCardSize({ width, height }) {
      this.hiloCardHeight = height;
      this.hiloCardWidth = width;
    },
    async createHiloBet({ type, betAmount, betUserItemId }) {
      await this.$apollo.mutate({
        mutation: require("@/graphql/CreateHiloBet.gql"),
        variables: { betAmount, type, betUserItemId }
      });
    },
    changeType(type) {
      this.betMakerProps.type = type;
    },
    async loadMoreHistory({ stop, done }) {
      let history = await this.$store.dispatch("hilo/loadMoreHistory", {
        afterId: _.last(this.hilo.history)._id
      });

      if (!history.length) {
        stop();
      }

      done();
    }
  },
  beforeRouteLeave(to, from, next){
    //store.dispatch("time/stopUpdateInterval");
    store.dispatch("hilo/reset");
    next();
  },
  async beforeRouteEnter(to, from, next) {
    //store.dispatch("time/startUpdateInterval");
    if (!store.state.hilo._id) {
      await store.dispatch("hilo/getGame");
    }

    next();
  },
  computed: {
    ...mapState({
      user: state => state.user,
      hilo: state => state.hilo,
      now: state => state.time.now
    }),
    ...mapGetters({
      sumOfBets: "hilo/sumOfBets"
    }),
    mainBetsButtons() {
      return [
        {
          type: "hi",
          icon: hiIcon,
          multiplier: this.hilo.hiMultiplier,
          chance: this.hilo.hiMultiplier
            ? Number(((1 / this.hilo.hiMultiplier) * 100).toFixed(2))
            : 0
        },
        {
          type: "joker",
          icon: jokerIcon,
          multiplier: 24,
          chance: Number(((1 / 24) * 100).toFixed(2))
        },
        {
          type: "lo",
          icon: loIcon,
          multiplier: this.hilo.loMultiplier,
          chance: this.hilo.loMultiplier
            ? Number(((1 / this.hilo.loMultiplier) * 100).toFixed(2))
            : 0
        }
      ];
    },
    additionalBetsButtons() {
      return [
        { type: "red", multiplier: 2 },
        { type: "black", multiplier: 2 },
        { type: "numbers", multiplier: 1.5 },
        { type: "letters", multiplier: 3 },
        { type: "ka", multiplier: 6 },
        { type: "a", multiplier: 12 }
      ];
    }
  }
};
</script>
<style lang="scss">
#hilo {
  .q-scrollarea__thumb {
    display: none;
  }
  .hilo__history-container {
    position: relative;
    .history-title {
      padding-left: inherit;
      position: absolute;
      left: 50%;
      top: 0.75em;
      transform: translateX(-50%);
      z-index: 1;
    }
  }
  .hilo__history {
    background: #1a1a22;
    border-radius: 0.5em;
    display: flex;
    justify-content: center;
    .history__inner {
      margin-left: auto;
      margin-right: auto;
      .history__item-container {
        padding-bottom: 0.5em;
      }
      .history__item {
        border-radius: 50%;
        width: 2.5em;
        height: 2.5em;
        display: flex;
        justify-content: center;
        cursor: pointer;
        align-items: center;
        text-transform: uppercase;
        &.item-joker {
          background-color: var(--q-color-primary);
        }
        &.item-red {
          background-color: #d90303;
        }
        &.item-black {
          background-color: #0d1015;
        }
      }
    }
  }
  .hilo__deck-wrapper {
    border: 0.25em solid var(--q-color-primary);
    border-radius: 0.5em;
  }
  .hilo-btn {
    &:last-child {
      padding-bottom: 0;
    }
    &.btn-hi,
    &.btn-lo,
    &.btn-joker {
      position: relative;
      .q-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 4em;
      }
    }
    &.btn-hi {
      .q-btn__wrapper {
        border-color: #b40b09;
        background-color: transparent;
        box-shadow: 0px 0px 10px 0px rgba(#b40b09, 0.75);
      }
    }
    &.btn-joker {
      .q-btn__wrapper {
        border-color: #ddcb33;
        background-color: transparent;
        box-shadow: 0px 0px 10px 0px rgba(#ddcb33, 0.75);
      }
    }
    &.btn-lo {
      .q-btn__wrapper {
        border-color: #89d733;
        background-color: transparent;
        box-shadow: 0px 0px 10px 0px rgba(#89d733, 0.75);
      }
    }
    &.btn-red {
      .q-btn__wrapper {
        border-color: #b60909;
        background-color: rgba(#b60909, 0.2);
      }
    }
    &.btn-black {
      .q-btn__wrapper {
        border-color: #0b0c0f;
        background-color: rgba(#0b0c0f, 0.5);
      }
    }
    &.active {
      .q-focus-helper {
        opacity: 0.15;
        &:before,
        &:after {
          opacity: 1;
        }
      }
    }
    .q-btn__wrapper {
      text-transform: none;
      border-radius: 0.5em;
      padding: 1.5em 1em;
      border: 0.17em;
      border-style: solid;
      border-color: #89cc30;
      background-color: rgba(#89cc30, 0.1);
      transition: 0.5s background, 1s border;
    }
  }
}
</style>
