import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";
import _ from "lodash";
import Chance from "chance";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    type: undefined,
    number: undefined,
    salt: undefined,
    hash: undefined,
    active: undefined,
    createdAt: undefined,
    startedAt: undefined,
    endedAt: undefined,
    betAllowed: undefined,
    winnerUserId: undefined,
    winnerUser: undefined,
    winnerBetIndex: undefined,
    sumOfBets: undefined,
    playerOdds: undefined,
    bets: [],
    history: []
  }),
  getters: {
    sumOfBets(state) {
      return _.sumBy(state.bets, bet => bet.betAmount);
    },
    players(state, getters) {
      const chance = new Chance(state._id);
      let userIdIndexes = {};
      let lastIndex = 0;
      return _.clone(state.bets)
        .reverse()
        .reduce((p, c) => {
          if (typeof userIdIndexes[c.userId] !== "number") {
            userIdIndexes[c.userId] = lastIndex++;
            p.push({
              _id: c.user._id,
              user: c.user,
              sumOfBets: 0,
              color: chance.color({ format: "hex" })
            });
          }
          p[userIdIndexes[c.userId]].sumOfBets += c.betAmount;
          return p;
        }, [])
        .map(player => {
          player.ratio = player.sumOfBets / getters.sumOfBets;
          player.chance = player.ratio * 100;
          return player;
        });
    },
    betAllowed(state, getters, rootState) {
      return !state.startedAt ? true : rootState.time.now < state.startedAt;
    }
  },
  mutations: {
    addBet(state, bet){
      state.bets.unshift(bet)
    },
    reset(state){
      state.history = state.history.slice(0,30);
    },
    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },
    unshiftHistory(state, game) {
      if (
        typeof game.number === "number" &&
        (!state.history[0] || game._id !== state.history[0]._id)
      ) {
        let delay = 0;
        if (game._id === state._id) {
          delay = 10000;
        }
        setTimeout(() => {
          Vue.set(state, "history", [game, ...state.history]);
        }, delay);
      }
    },
    updateValues(state, game) {
      if (game) {
        Object.keys(game).map(key => {
          if (typeof game[key] !== "undefined") {
            if(key === 'bets' && game[key] === null){
              return;
            }
            state[key] = key === "bets" ? game[key].reverse() : game[key];
          }
        });
      }
    }
  },
  actions: {
    async loadMoreHistory({ commit }, { afterId }) {
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetRoyalBattleHistory.gql"),
          variables: { afterId }
        })
      ).data.getRoyalBattleHistory;

      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },
    async getGame({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetRoyalBattleGame.gql")
      });
      const game = data.data.getRoyalBattleGame;
      const history = data.data.getRoyalBattleHistory;
      commit("updateValues", game);
      commit("setHistory", history);
      if (game) dispatch("subscribeGameUpdated");
    },
    reset({commit}){
      commit('reset');
    },
    subscribeGameUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/RoyalBattleUpdated.gql")
      });
      observer.subscribe({
        next: data => {
          commit("updateValues", data.data.royalBattleUpdated);
          commit("unshiftHistory", data.data.royalBattleUpdated);
        }
      });
      const observer1 = apolloClient.subscribe({
        query: require("@/graphql/RoyalBattleBetAdded.gql")
      });
      observer1.subscribe({
        next: data => {
          commit("addBet", data.data.royalBattleBetAdded);
        }
      });
    }
  }
};
