<template>
  <q-dialog v-model="chatBanDialog" @hide="onHide">
    <q-card class="app-dialog chat-ban-dialog" v-if="chatBanDialog">
      <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>

      <q-card-section style="transition: all 2500ms;" class="q-pt-none">
        <div class="full-width text-center q-mb-md">
          Вы действительно хотите заблокировать <br />
          пользователя
          <span class="text-weight-bold text-highlight"
            >"{{ chatBanDialogUser.name }}"</span
          >
          до {{ banDateModel }}?
        </div>
        <div class="q-gutter-md row justify-center no-wrap q-mb-md">
          <q-date
            v-model="banDateModel"
            mask="YYYY-MM-DD HH:mm"
            color="primary"
            dark
            :options="dateLimit"
          />
          <q-time
            v-model="banDateModel"
            mask="YYYY-MM-DD HH:mm"
            color="primary"
            dark
            ref="timePicker"
          />
        </div>
        <div class="full-width flex justify-end">
          <q-btn label="Отмена" flat color="grey" v-close-popup> </q-btn>
          <q-btn
            :label="banTime ? 'Заблокировать' : 'Разблокировать'"
            flat
            color="primary"
            @click="setPublicChatBan"
            v-close-popup
          >
          </q-btn>
        </div>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { mapState } from "vuex";
import moment from "moment";

export default {
  name: "ChatBanDialog",
  components: {},
  data() {
    return {
      banDateModel: moment(new Date()).format("YYYY-MM-DD HH:mm"),
      chatBanDialog: false
    };
  },
  watch: {
    chatBanDialogShow(newValue) {
      this.chatBanDialog = newValue;
    },
    async banDateModel(newValue) {
      if (new Date(newValue).getTime() < Date.now()) {
        await this.$nextTick();
        this.$refs.timePicker.setNow();
      }
    }
  },
  methods: {
    async setPublicChatBan() {
      await this.$apollo.mutate({
        mutation: require("@/graphql/SetPublicChatBan.gql"),
        variables: {
          banDate: this.banDate,
          banUserId: this.chatBanDialogUser._id
        }
      });
    },
    onHide() {
      this.$store.commit("dialog/toggleChatBanDialog", false);
    },
    dateLimit(date) {
      return date >= moment().format("YYYY/MM/DD");
    }
  },
  computed: {
    banTime() {
      let time = this.banDate.getTime() - Date.now();
      return time < 0 ? 0 : time;
    },
    banDate() {
      return new Date(this.banDateModel);
    },
    ...mapState({
      user: state => state.user,
      chatBanDialogShow: state => state.dialog.chatBanDialogShow,
      chatBanDialogUser: state => state.dialog.chatBanDialogUser
    })
  }
};
</script>

<style lang="scss">
.chat-ban-dialog {
  min-width: 45em;
}
</style>
