<template>
  <div id="tape" class="full-width">
    <div class="tape__arrow">
      <q-icon
        name="fas fa-chevron-down"
        style="font-size: 1.2em"
        color="amber-14"
      ></q-icon>
    </div>

    <div class="tape__arrow-bottom">
      <q-icon
        name="fas fa-chevron-down fa-flip-vertical"
        style="font-size: 1.2em"
        color="amber-14"
      ></q-icon>
    </div>

    <div
      class="tape__inner"
      :style="`margin-left:${tapeWidth / 2 - elementWidth / 2}px`"
      ref="tape"
    >
      <slot></slot>
    </div>
    <q-resize-observer @resize="onTapeResize" />
  </div>
</template>

<script>
import anime from "animejs";
import { mapState } from "vuex";

export default {
  name: "Tape",
  props: ["elementWidth", "autoStart"],
  data: () => ({
    tapeWidth: 0
  }),
  methods: {
    onTapeResize({ width }) {
      this.tapeWidth = width;
    },
    async animate({
      duration = 10000,
      animateToElementWithIndex = 87,
      endedAt = Date.now()
    }) {
      const targets = this.$refs.tape;

      const animation = anime({
        targets,
        duration,
        autoplay: false,
        easing: "easeInOutQuad",
        translateX: -this.elementWidth * animateToElementWithIndex
      });

      let offset = this.ts.now() - endedAt;
      offset = offset > duration ? duration : offset < 0 ? 0 : offset;

      if (offset >= duration) {
        return Promise.resolve();
      }

      animation.seek(offset);
      animation.play();

      return animation.finished;
    }
  },
  computed: {
    ...mapState({
      ts: state => state.time.ts
    })
  }
};
</script>

<style lang="scss">
#tape {
  position: relative;
  .tape__arrow,
  .tape__arrow-bottom {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    z-index: 1;
  }
  .tape__arrow-bottom {
    bottom: 0;
    top: auto;
    transform: translate(-50%, 50%);
  }
  .tape__inner {
    display: flex;
    flex-wrap: nowrap;
    will-change: transform;
    img {
      width: 3em;
    }
  }
}
</style>
