<template>
  <div id="internal-error">
    <div class="internal-error__container">
      <img src="/img/500.png" class="internal-error__img" alt="500" />

      <h4 class="text-white q-pa-none q-ma-none text-center">
        Ведутся технические работы.
      </h4>

      <p class="text-center text-grey">
        В скором времени мы возобновим работу.
      </p>

      <q-btn
        class="q-px-lg q-py-sm"
        @click="reloadPage"
        outline
        style="color: #3585ff;"
        :loading="loading"
        no-caps
      >
        <span class="text-white text-font-weigh-300">
          Обновить страницу
        </span>
      </q-btn>
    </div>
  </div>
</template>

<script>
export default {
  name: "InternalError",
  data: () => ({
    loading: false,
    intervalTimer: null
  }),
  meta: {
    title: "Ведутся технические работы"
  },
  created() {
    this.$root.$emit("appLoaded");
    this.intervalTimer = setInterval(this.reloadPage.bind(this), 10000);
  },
  async beforeRouteEnter(to, from, next) {
    try {
      const response = await fetch(
        `${process.env.VUE_APP_API_ENDPOINT}/healthcheck`
      );

      if (response.status === 200) {
        next(`/`);
      } else {
        throw new Error("response.status !== 200");
      }
    } catch {
      next();
    }
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalTimer);
    next();
  },
  methods: {
    async reloadPage() {
      if (this.loading === true) {
        return;
      }

      this.loading = true;

      try {
        const response = await fetch(
          `${process.env.VUE_APP_API_ENDPOINT}/healthcheck`
        );

        if (response.status === 200) {
          location.reload();
        }
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>

