<template>
  <div id="web-push"></div>
</template>
<script>
import notification from "../mixins/notification";

export default {
  name: "WebPush",
  mixins: [notification],
  data() {
    return {
      permission: "default",
      lastWebPushProposalAt: 0
    };
  },
  created() {
    if (Notification) {
      this.permission = Notification.permission;
    } else {
      this.permission = "denied";
    }

    this.lastWebPushProposalAt =
      Number(this.$q.cookies.get("lastWebPushProposalAt")) || 0;
    if (!this.lastWebPushProposalAt && this.permission === "default") {
      this.showWebPushProposal();
    }
  },
  methods: {
    showWebPushProposal() {
      this.showNotify();
    },
    showNotify() {
      this.$q.notify({
        color: "white",
        textColor: "black",
        html: true,
        message:
          "<div class='row '><div class='col-2 notification-avatar q-pr-lg'><img src='/img/icons/apple-touch-icon-152x152.png' width='100%'></div><div class='col notification-message'><span class='text-h6 text-weight-light'>Подпишитесь на уведомления, чтобы быть вкурсе последних <br> новостей сайта, узнавать о ваших выигрышах <br> в розыгрышах и многом другом.</span> </div></div>",
        position: this.$q.screen.gt.sm ? "top" : "center",
        multiLine: true,
        actions: [
          {
            label: "Напомнить позже",
            color: "grey",
            size: this.$q.screen.gt.sm ? "lg" : "md",
            handler: () => {
              this.lastWebPushProposalAt = Date.now();
              this.$q.cookies.set(
                "lastWebPushProposalAt",
                this.lastWebPushProposalAt,
                {
                  expires: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000)
                }
              );
            }
          },
          {
            label: "Подписаться",
            color: "primary",
            handler: this.requestPermission,
            flat: false,
            size: this.$q.screen.gt.sm ? "lg" : "md"
          }
        ],
        timeout: 100000
      });
    }
  }
};
</script>

<style lang="scss">
.bg-web-push {
  background: white;
}

  @media (max-width: 700px){
    .notification-avatar{
      display: none;
    }
    .notification-message{
      br{
        display: none;
      }
    }
  }
</style>
