import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";
import _ from "lodash";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    type: undefined,
    number: undefined,
    previousNumber: undefined,
    salt: undefined,
    hash: undefined,
    active: undefined,
    createdAt: undefined,
    startedAt: undefined,
    endedAt: undefined,
    betAllowed: undefined,
    hiMultiplier: undefined,
    loMultiplier: undefined,
    bets: [],
    history: []
  }),
  getters: {
    sumOfBets(state) {
      return _.sumBy(state.bets, bet => bet.betAmount);
    },
    betAllowed(state, getters, rootState) {
      return rootState.time.now < state.startedAt;
    },
    userBet(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"]
        ? state.bets.find(bet => bet.userId === rootState.user._id)
        : undefined;
    },
    isUserHaveBet(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"] ? !!getters.userBet : false;
    }
  },
  mutations: {
    addBet(state, bet){
      state.bets.push(bet)
    },
    reset(state){
      state.history = state.history.slice(0,30);
    },
    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },
    unshiftHistory(state, game) {
      if (
        state.history.length &&
        typeof game.number === "string" &&
        game._id !== state.history[0]._id
      ) {
        let delay = 0;
        if (game._id === state._id) {
          delay = 2000;
        }
        setTimeout(() => {
          Vue.set(state, "history", [
            { _id: game._id, number: game.number },
            ...state.history
          ]);
        }, delay);
      }
    },
    updateValues(state, game) {
      if (game) {
        Object.keys(game).map(key => {
          if (typeof game[key] !== "undefined") {
            if(key === 'bets' && game[key] === null){
              return;
            }
            state[key] = game[key];
          }
        });
      }
    }
  },
  actions: {
    async loadMoreHistory({ commit }, { afterId }) {
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetHiloHistory.gql"),
          variables: { afterId }
        })
      ).data.getHiloHistory;

      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },
    async getGame({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetHiloGame.gql")
      });
      const game = data.data.getHiloGame;
      const history = data.data.getHiloHistory;
      commit("updateValues", game);
      commit("setHistory", history);
      if (game) dispatch("subscribeGameUpdated");
    },
    reset({commit}){
      commit('reset');
    },
    subscribeGameUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/HiloUpdated.gql")
      });
      observer.subscribe({
        next: data => {
          commit("updateValues", data.data.hiloUpdated);
          commit("unshiftHistory", data.data.hiloUpdated);
        }
      });
      const observer1 = apolloClient.subscribe({
        query: require("@/graphql/HiloBetAdded.gql")
      });
      observer1.subscribe({
        next: data => {
          commit("addBet", data.data.hiloBetAdded);
        }
      });
    }
  }
};
