<template>
  <q-dialog v-model="buySkinDialog" @hide="onHide" :maximized="$q.screen.lt.md">
    <q-card class="app-dialog buy-skin-dialog">
      <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>

      <q-card-section
        class="q-pt-none q-pb-xl q-pb-md-none"
        :class="{ 'full-height flex column': $q.screen.lt.md }"
      >
        <div class="full-width q-mb-md q-pl-md flex">
          <q-tabs
            v-model="tab"
            class="q-mb-md"
            :class="{ 'full-width': $q.screen.lt.md }"
            indicator-color="primary"
            dense
            align="justify"
          >
            <q-tab
              class="text-grey"
              no-caps
              name="skinsPlus"
              label="Моментальный вывод"
            />
            <q-tab
              class="text-grey "
              no-caps
              name="market"
              label="Маркетплейс"
            />
          </q-tabs>
          <q-space />
          <div class="flex items-center" v-if="$q.screen.gt.sm">
            <q-icon size="sm" name="fas fa-sack-dollar" class="q-mr-sm">
            </q-icon>
            <span style="font-size: 1.5em" class="text-weight-bold">
              {{ user.balance | coinsWithoutShort }}
            </span>
          </div>
        </div>
        <div class="flex q-mb-md">
          <div class="q-pr-md-sm" style="flex: 1 1 50%;">
            <q-input
              class="full-width"
              :value="searchText"
              @change="
                e => {
                  searchText = e.target.value;
                }
              "
              bg-color="deposit-input"
              label-color="grey"
              label="Поиск по названию"
              input-style="color: white;"
              maxlength="100"
              outlined
            >
              <template v-slot:prepend>
                <q-icon name="fas fa-search" style="color: #646977" />
              </template>
              <template v-if="searchText" v-slot:append>
                <q-icon
                  name="cancel"
                  @click.stop="searchText = ''"
                  class="cursor-pointer"
                />
              </template>
            </q-input>
          </div>
          <div
            class="flex items-center q-pt-md q-pt-md-none no-wrap"
            style="flex: 1 1 50%;"
          >
            <q-input
              class="q-mr-sm"
              :value="fromPrice || ''"
              @change="
                e => {
                  fromPrice = Number(e.target.value) || 0;
                }
              "
              bg-color="deposit-input"
              input-style="color: white; width: 5em;"
              outlined
            >
              <template v-slot:prepend>
                <span class="text-grey text-small">
                  от
                </span>
              </template>
              <template v-slot:append>
                <span class="text-grey text-small">
                  $
                </span>
              </template>
            </q-input>
            <q-input
              class="q-mr-sm"
              :value="toPrice > (fromPrice || 0) ? toPrice : '' || ''"
              @change="
                e => {
                  toPrice = Number(e.target.value) || 0;
                }
              "
              bg-color="deposit-input"
              input-style="color: white; width: 5em;"
              outlined
            >
              <template v-slot:prepend>
                <span class="text-grey text-small">
                  до
                </span>
              </template>
              <template v-slot:append>
                <span class="text-grey text-small">
                  $
                </span>
              </template>
            </q-input>
            <q-btn
              class=" full-height"
              style="flex-grow: 1;"
              color="primary"
              @click="
                () => {
                  sortByPrice = !sortByPrice;
                }
              "
              :icon="
                sortByPrice
                  ? 'fas fa-sort-amount-down-alt'
                  : 'fas fa-sort-amount-up-alt'
              "
              :label="
                $q.screen.gt.sm
                  ? sortByPrice
                    ? 'Сортировка по возрастанию'
                    : 'Сортировка по убыванию'
                  : null
              "
            >
            </q-btn>
          </div>
        </div>
        <div
          class="items__container relative-position"
          :style="
            $q.screen.gt.sm
              ? 'height: 30em'
              : 'min-height: 20em; flex: 1 1 auto; height: 1px;'
          "
        >
          <Loading v-if="loading"></Loading>
          <UserItemList
            v-show="!loading"
            ref="userItemList"
            :mode="this.tab"
            :items="transformedItems"
            @onLoadMore="loadMore"
            :hasMore="hasMore"
            :col="$q.screen.gt.sm ? 2 : 6"
            class="full-height"
            :emptySlots="18"
            :virtual-scroll-item-size="$q.screen.gt.sm ? 130 : 110"
          >
          </UserItemList>
        </div>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { mapState } from "vuex";
import UserItemList from "./UserItemList";
import Loading from "./Loading";

export default {
  name: "BuySkinDialog",
  components: { UserItemList, Loading },
  data() {
    return {
      tab: `skinsPlus`,
      buySkinDialog: false,
      items: [],
      searchText: ``,
      fromPrice: undefined,
      toPrice: undefined,
      sortByPrice: false,
      page: 1,
      hasMore: false,
      loading: true
    };
  },
  watch: {
    tab: "searchParamsChanged",
    searchText: "searchParamsChanged",
    fromPrice: "searchParamsChanged",
    toPrice: "searchParamsChanged",
    sortByPrice: "searchParamsChanged",
    buySkinDialogShow(newValue) {
      this.buySkinDialog = newValue;
    }
  },
  async mounted() {
    this.buySkinDialog = true;
    await this.$nextTick();
    await this.searchParamsChanged();
    this.loading = false;
  },
  methods: {
    async searchParamsChanged() {
      this.page = 1;

      await this.getSteamItems({
        searchText: this.searchText,
        fromPrice: this.fromPrice,
        toPrice: this.toPrice,
        sortByPrice: this.sortByPrice
      });

      this.$refs.userItemList.$refs.scrollArea.setScrollPosition(0);
    },
    async loadMore(done) {
      this.page++;
      try {
        await this.getSteamItems();
      } catch {
        setTimeout(() => (this.hasMore = true), 1000);
        return done();
      }

      done();
    },
    async getSteamItems(
      searchParams = {
        searchText: this.searchText,
        fromPrice: this.fromPrice,
        toPrice: this.toPrice,
        sortByPrice: this.sortByPrice
      },
      page = this.page
    ) {
      searchParams.fromPrice = Math.floor(searchParams.fromPrice * 1000);
      searchParams.toPrice = Math.floor(searchParams.toPrice * 1000);
      let items = [];
      if (this.tab === `market`) {
        let {
          data: { getSteamItems }
        } = await this.$apollo.query({
          query: require("@/graphql/GetSteamItems.gql"),
          variables: { ...searchParams, page },
          fetchPolicy: "no-cache"
        });
        items = getSteamItems;
      } else if (this.tab === `skinsPlus`) {
        let {
          data: { getSkinsPlusItems }
        } = await this.$apollo.query({
          query: require("@/graphql/GetSkinsPlusItems.gql"),
          variables: { ...searchParams, page },
          fetchPolicy: "no-cache"
        });
        items = getSkinsPlusItems;
      }

      if (page === 1) {
        this.items = items;
      } else {
        this.items = this.items.concat(items);
      }
      this.hasMore = !!items.length;
      return items;
    },

    onHide() {
      this.$store.commit("dialog/toggleBuySkinDialog", false);
    }
  },
  computed: {
    transformedItems() {
      return this.tab === `market`
        ? this.items.map(item => {
            return {
              item,
              _id: item._id,
              soldPrice: item.buyPrice
            };
          })
        : this.items.filter((item)=> !!item.item);
    },
    ...mapState({
      user: state => state.user,
      buySkinDialogShow: state => state.dialog.buySkinDialogShow
    })
  }
};
</script>

<style lang="scss">
@import "../styles/quasar.variables.scss";
.buy-skin-dialog {
  @media (max-width: $breakpoint-sm-max) {
    & {
      width: 100% !important;
    }
  }
  max-width: 100vw !important;

  width: 80%;
  .q-tab--active,
  .q-tab:hover {
    color: white !important;
  }
  .q-tab__label {
    padding: 0.5em 0;
    font-size: 1em;
    font-weight: 400;
  }
}
</style>
