<template>
  <q-dialog v-model="historyDialog" @hide="onHide">
    <q-card class="app-dialog flex column" style="width: 31.3125em; height: 518px">
      <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
        <Giveaway
          :value="giveaway"
          class="no-skew full-width title-margin"
        ></Giveaway>
        <q-btn
          icon="close"
          class="absolute absolute-top-right q-mt-xs"
          flat
          round
          dense
          v-close-popup
        />
      </q-card-section>
      <q-separator> </q-separator>
      <q-card-section style="flex: 1" class="q-px-none q-py-none">
        <ApolloQuery
          :query="require('@/graphql/GetGiveawayHistoryByType.gql')"
          :variables="{ giveawayType: giveawayHistoryDialogType }"
          fetch-policy="cache-and-network"
          class="full-width full-height"
        >
          <template v-slot="{ result: { error, data, loading }, query }">
            <q-scroll-area
              class="giveaway-history__scroll full-width full-height"
              :thumb-style="scrollThumbStyle"
              :bar-style="scrollBarStyle"
              v-if="
                data &&
                  data.getGiveawayHistoryByType &&
                  data.getGiveawayHistoryByType.length
              "
            >
              <q-infinite-scroll
                @load="
                  (index, done) =>
                    loadMoreHistory(query, data.getGiveawayHistoryByType, done)
                "
                :offset="50"
                :debounce="500"
                :disable="!hasMoreHistory"
                scroll-target=".giveaway-history__scroll > .scroll"
              >
                <template v-slot:default>
                  <div
                    class="full-width row q-mb-sm q-px-md"
                    style="background: rgba(255, 255, 255, 0.025); border-left: 3px solid;"
                    v-for="giveaway in data.getGiveawayHistoryByType"
                    :key="giveaway._id"
                    :style="{ borderColor: giveaway.item.rarityColor }"
                  >
                    <div
                      class="col-4 relative-position text-center"
                      :style="
                        `background: radial-gradient(circle at 50% center,${giveaway
                          .item.rarityColor ||
                          '#1356e3'} -5%,${`rgba(${hexToRgb(
                          lightenDarkenColor(giveaway.item.rarityColor, -10)
                        )}, 0.25)` || '#172b5d'} 35%,transparent 75%);`
                      "
                    >
                      <div
                        class="absolute text-weight-medium text-highlight-white absolute-top-right text-white text-small q-pr-sm q-pt-xs"
                        style="z-index: 1;"
                      >
                        {{
                          splitMarketHashName(giveaway.item._id).shortQuality || '...'
                        }}
                      </div>

                      <div
                        class="absolute absolute-bottom-left text-highlight flex items-center text-weight-medium text-white text-small q-pb-xs"
                        style="z-index: 1;"
                      >
                        <q-icon name="fas fa-sack-dollar" class=" q-mr-sm">
                        </q-icon>
                        {{ giveaway.itemPrice | coinsWithoutShort }}
                      </div>
                      <q-img basic :src="giveaway.item.image" width="100px" />
                    </div>
                    <div
                      class="col-3 flex column justify-center overflow-hidden no-wrap q-pl-md"
                    >
                      <div
                        class="full-width text-weight-medium overflow-hidden"
                        style="font-size: 0.85em; white-space: nowrap; text-overflow: ellipsis"
                      >
                        {{ splitMarketHashName(giveaway.item._id).name }}
                      </div>
                      <div
                        class="full-width text-weight-light overflow-hidden text-grey-5 "
                        style="font-size: 0.75em; white-space: nowrap; text-overflow: ellipsis"
                      >
                        {{
                          splitMarketHashName(giveaway.item._id).marketName ||
                            "..."
                        }}
                      </div>
                    </div>
                    <div class="col-5 flex no-wrap items-center">
                      <q-avatar>
                        <q-img basic
                          :src="giveaway.winnerUser.avatar"
                          width="35px"
                          height="35px"
                        />
                      </q-avatar>
                      <div
                        class="flex column overflow-hidden"
                        style="flex: 1 1 auto;"
                      >
                        <UserName
                          :value="giveaway.winnerUser"
                          class="text-small"
                        ></UserName>
                        <span class="text-grey text-small">
                          {{ giveaway.endedAt | moment("from", "now") }}
                        </span>
                      </div>
                    </div>
                  </div>
                </template>
                <template v-slot:loading>
                  <div class="row justify-center q-my-md">
                    <q-spinner color="primary" size="xl" />
                  </div>
                </template>
              </q-infinite-scroll>
            </q-scroll-area>
            <Loading v-else-if="loading" />
            <div class="absolute absolute-center text-center" v-else="">
              Истории пока нет
            </div>
          </template>
        </ApolloQuery>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { mapState } from "vuex";
import color from "../mixins/color";
import scrollBarStyle from "../mixins/scrollBarStyle";
import steamItem from "../mixins/steamItem";
import _ from "lodash";
import UserName from "./UserName";
import Loading from "./Loading";
import Giveaway from "./Giveaway";
import coins from "../mixins/coins";

export default {
  name: "GiveawayHistory",
  components: {
    UserName,
    Loading,
    Giveaway
  },
  mixins: [steamItem, color, scrollBarStyle, coins],
  data() {
    return {
      hasMoreHistory: true,
      historyDialog: false
    };
  },
  watch: {
    giveawayHistoryDialog(newValue) {
      this.historyDialog = newValue;
    }
  },
  methods: {
    async loadMoreHistory(query, giveaways = [], done) {
      let {
        data: { getGiveawayHistoryByType }
      } = await query.fetchMore({
        variables: {
          afterId: _.last(giveaways)._id,
          giveawayType: this.giveawayHistoryDialogType
        },
        updateQuery: (prev, { fetchMoreResult }) => {
          if (
            !fetchMoreResult ||
            fetchMoreResult.getGiveawayHistoryByType.length === 0
          ) {
            return prev;
          }

          return Object.assign({}, prev, {
            getGiveawayHistoryByType: [
              ...prev.getGiveawayHistoryByType,
              ...fetchMoreResult.getGiveawayHistoryByType
            ]
          });
        }
      });
      if (!getGiveawayHistoryByType.length) {
        this.hasMoreHistory = false;
      }

      done();
    },
    onHide() {
      this.$store.commit("dialog/toggleGiveawayHistoryDialog", false);
	  this.hasMoreHistory = true;
    }
  },
  computed: {
    giveaway() {
      return this.$store.state.giveaway.giveaways.find(
        v => v.giveawayType === this.giveawayHistoryDialogType
      );
    },
    ...mapState({
      giveawayHistoryDialogType: state =>
        state.dialog.giveawayHistoryDialogType,
      giveawayHistoryDialog: state => state.dialog.giveawayHistoryDialog
    })
  }
};
</script>

<style scoped></style>
