<template>
  <div id="home" class="q-py-md">
    <v-teleport to="giveaway-home" :disabled="$q.screen.gt.sm">
      <div
        class="row q-mx-auto"
        :style="{ width: $q.screen.gt.sm ? '87%' : '95%' }"
        v-if="giveaways && giveaways.length"
      >
        <Giveaway
          :value="giveaway"
          class="col-md-4 col-12 q-py-md q-pr-md-xs"
          :class="{ 'no-skew': $q.screen.lt.md }"
          v-for="giveaway in giveaways"
          :key="giveaway._id"
        ></Giveaway>
      </div>
    </v-teleport>

    <Carousel
      :per-page="perPageCount"
      :paginationEnabled="true"
      :paginationPadding="3"
      :paginationSize="5"
      paginationPosition="top"
      :mouse-drag="true"
      v-model="currentPage"
    >
      <Slide v-for="(link, i) in gameLinks" :key="i">
        <div
          class="link__container"
          :style="
            'background: linear-gradient( rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05) ), url(' +
              link.bg +
              '); background-size: 100% 100%, 100% 100%;'
          "
          :class="{
            'link-first':
              currentPage + 1 === pageCount
                ? i ===
                  gameLinks.length -
                    (gameLinks.length - (gameLinks.length % perPageCount))
                : i === perPageCount * currentPage,
            'link-last':
              currentPage + 1 === pageCount
                ? i === gameLinks.length - 1
                : i === currentPage * perPageCount + perPageCount - 1
          }"
        >
          <div class="link__icon-container flex justify-center">
            <q-icon class="link__icon" v-html="link.icon" />
          </div>
          <div class="link__name text-center">
            <h5
              class="link__name-text text-font-montserrat text-weight-medium q-ma-none q-pt-md"
            >
              {{ link.name }}
            </h5>
          </div>
          <div class="full-width link__description">
            <p>
              {{ link.description }}
            </p>
          </div>
          <div class="q-px-xl full-width link__enter-btn__container">
            <q-btn
              :to="link.href"
              class="full-width link__enter-btn"
              outline
              style="color: #80bf1b;"
              padding="sm sm"
            >
              <span class="text-white text-weight-medium">
                Войти в режим
              </span>
            </q-btn>
          </div>
        </div>
