import DiceIcon from "@/../public/img/game-icons/dice-icon.svg";
import HiloIcon from "@/../public/img/game-icons/hilo-icon.svg";
import BlackjackIcon from "@/../public/img/game-icons/blackjack-icon.svg";
import { mapState } from "vuex";
import { vTeleport, vTeleportLocation } from "@desislavsd/vue-teleport";

export default {
  name: "Home",
  components: {
    Carousel,
    vTeleportLocation,
    vTeleport,
    Slide,
    Giveaway
