  meta: {
    title: "Главная"
  },
  computed: {
    ...mapState({
      user: state => state.user,
      giveaways: state => state.giveaway.giveaways
    }),
    pageCount() {
      return Math.ceil(this.gameLinks.length / this.perPageCount);
    }
  },
  data() {
    return {
      perPageCount: this.$q.screen.lt.md ? 1 : 6,
      currentPage: 0,
      gameLinks: [
        {
