        },
        /*{
        href: "/poker",
        name: "Покер",
        bg: "/img/game-bg/2-game-bg.jpg",
        icon: PokerIcon,
        description:
          "Карточная игра, цель - выиграть ставки, собрав как можно более высокую покерную комбинацию."
      },*/
        {
          href: "/roulette",
          name: "Рулетка",
          bg: "/img/game-bg/2-game-bg.jpg",
          icon: RouletteIcon,
          description:
            "Режим игры, в котором игроки делают ставки на победу черного, красного или зеленого сектора."
        },
        {
          href: "/crash",
          name: "Краш",
          bg: "/img/game-bg/3-game-bg.jpg",
          icon: CrashIcon,
          description:
            'Режим игры, в котором нужно успеть нажать кнопку Stop и выйти из игры до "краша" графика.'
        },
        /*{
        href: "/duel",
        name: "Дуэль",
        bg: "/img/game-bg/5-game-bg.jpg",
        icon: DuelIcon,
        description:
          "Режим игры, в котором нужно выставить сумму ставки и крутить круг фортуны."
      },*/

        {
          href: "/dice",
          name: "Кости",
          bg: "/img/game-bg/4-game-bg.jpg",
          icon: DiceIcon,
          description:
            "Режим игры, в котором игрок делает ставку на выпадение числа, которое больше или меньше указанного игроком."
        },
        {
          href: "/hilo",
          name: "Hilo",
          bg: "/img/game-bg/5-game-bg.jpg",
          icon: HiloIcon,
          description:
            "Режим игры, в котором нужно угадать следующую карту из колоды."
        },
        {
          href: "/blackjack",
          name: "Блэкджек",
          bg: "/img/game-bg/1-game-bg.jpg",
          icon: BlackjackIcon,
          description:
            "Игра очень проста, ее цель собрать 21 очко или же очков больше чем у дилера, но не больше 21."
        }
      ]
    };
  }
};
</script>

