import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";
import _ from "lodash";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    active: undefined,
    winnerUserId: undefined,
    winnerUser: undefined,
    sumOfJackpot: undefined,
    history: []
  }),

  mutations: {
    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },

    updateValues(state, jackpot) {
      if (jackpot) {
        if (jackpot.winnerUser) {
          Vue.set(state, "history", [jackpot, ...state.history]);
        }

        Object.keys(jackpot).map(key => {
          if (typeof jackpot[key] !== "undefined") {
            state[key] = jackpot[key];
          }
        });
      }
    }
  },
  actions: {
    async loadMoreHistory({ commit, state }) {
      let lastHistory = _.last(state.history);
      if (!lastHistory) {
        return [];
      }
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetJackpotHistory.gql"),
          variables: { afterId: lastHistory._id }
        })
      ).data.getJackpotHistory;
      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },
    async getJackpot({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetJackpot.gql")
      });
      const jackpot = data.data.getJackpot;
      const history = data.data.getJackpotHistory;
      commit("updateValues", jackpot);
      commit("setHistory", history);
      dispatch("subscribeJackpotUpdated");
    },
    subscribeJackpotUpdated({ commit, rootState }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/JackpotUpdated.gql")
      });
      observer.subscribe({
        next: data => {
          commit("updateValues", data.data.jackpotUpdated);
          let winnerUserId = data.data.jackpotUpdated.winnerUserId;
          if (winnerUserId && winnerUserId == rootState.user._id) {
            commit("dialog/toggleJackpotDialog", true, { root: true });
          }
        }
      });
    }
  }
};
