<template>
  <q-banner dark id="user-item-list-popup" class="rounded-borders">
    <div class="flex justify-between q-mb-md">
      <span class="flex items-center">
        <q-icon
          name="icon-icomoon-csgo-icon"
          color="white"
          class=" q-mr-sm"
        ></q-icon>
        Выберите скин
      </span>
      <q-btn icon="close" flat round dense v-close-popup />
    </div>
    <div class="row q-mb-lg">
      <div class="col-md-7 col-xs-9 q-pr-lg">
        <div
          class="bet-amount rounded-borders-2x flex items-center q-pl-lg text-highlight "
        >
          <q-icon name="fas fa-sack-dollar" class="q-mr-sm"> </q-icon>
          <span>
            {{
              (selectedItem ? selectedItem.soldPrice : 0) | coinsWithoutShort
            }}
          </span>
        </div>
      </div>
      <div class="col-md-5 col-xs-3">
        <q-btn
          :label="$q.screen.lt.md ? undefined : 'Сделать ставку'"
          :icon="$q.screen.lt.md ? 'fas fa-check' : undefined"
          style="font-size: 1em"
          class="rounded-borders-2x full-width"
          color="primary"
          padding="md xs"
          @click="bet"
          :disable="
            disable ||
              !selectedItem ||
              selectedItem.soldPrice < 10 ||
              selectedItem.soldPrice > 100000
          "
        >
        </q-btn>
      </div>
    </div>
    <ApolloQuery
      :query="require('@/graphql/GetUserItems.gql')"
      :variables="{ hideSoldAndWithdrawn: true }"
      fetchPolicy="no-cache"
      @result="onResult"
      class="items__container rounded-borders-2x "
    >
      <ApolloSubscribeToMore
        :document="
          gql => gql`
            subscription userItemReturned {
              userItemReturned {
                _id
                delay
              }
            }
          `
        "
        :updateQuery="onUserItemReturned"
      />
      <template v-slot="{ result: { error, data, loading } }">
        <!-- Loading -->
        <Loading v-if="loading"></Loading>
        <!-- Result -->
        <UserItemList
          :items="userItems"
          v-else
          @onLoadMore="loadMoreUserItems"
          :hasMore="hasMore"
          :col="$q.screen.gt.sm ? 4 : 6"
          class="full-height"
          mode="mini"
          :emptySlots="$q.screen.gt.sm ? 9 : 6"
          @selectItem="selectItem"
          :virtual-scroll-item-size="82"
          :selected-items="selectedItem ? [selectedItem] : []"
        >
        </UserItemList>
      </template>
    </ApolloQuery>
  </q-banner>
</template>

<script>
import UserItemList from "./UserItemList";
import scrollBarStyle from "../mixins/scrollBarStyle";
import Loading from "./Loading";
import _ from "lodash";

export default {
  name: "UserItemListPopup",
  mixins: [scrollBarStyle],
  components: {
    UserItemList,
    Loading
  },
  props: {
    disable: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      userItems: [],
      selectedItem: null,
      queryLoaded: false,
      hasMore: true,
      deletedUserItems: []
    };
  },
  methods: {
    onUserItemReturned(
      prevResult,
      {
        subscriptionData: {
          data: { userItemReturned }
        }
      }
    ) {
      let index = this.deletedUserItems.findIndex(
        item => item._id === userItemReturned._id
      );
      setTimeout(() => {
        this.userItems.unshift(this.deletedUserItems[index]);
        this.deletedUserItems.splice(index, 1);
        this.userItems = _.sortBy(this.userItems, "_id").reverse();
      }, userItemReturned.delay);
    },
    bet() {
      this.$emit("bet", this.selectedItem);
      let index = this.userItems.findIndex(
        item => item._id === this.selectedItem._id
      );
      this.deletedUserItems.push(this.userItems[index]);
      this.userItems.splice(index, 1);
      this.selectedItem = null;
    },
    selectItem(item) {
      this.selectedItem = item;
    },
    async loadMoreUserItems(done) {
      let lastUserItem = _.last(this.userItems);
      if (lastUserItem) {
        let { data } = await this.$apollo.query({
          query: require("@/graphql/GetUserItems.gql"),
          variables: {
            hideSoldAndWithdrawn: true,
            afterId: lastUserItem._id
          }
        });
        if (data.getUserItems.length) {
          this.userItems = [...this.userItems, ...data.getUserItems];
        } else {
          this.hasMore = false;
        }
      } else {
        this.hasMore = false;
      }
      done();
    },
    onResult(result) {
      if (result.data && !this.queryLoaded) {
        this.userItems = result.data.getUserItems;
        this.queryLoaded = true;
      }
      return result;
    }
  }
};
</script>

