  background: #11141b;
  width: 30em;
  padding: 1.5em 1em;
  .bet-amount {
    position: relative;
    background-color: #0e1016;
    width: 100%;
    height: 100%;
    font-size: 1.5em;
    &:before {
      content: "";
      border-style: solid;
      border-width: 1rem 0 1rem 0.7rem;
      border-color: transparent transparent transparent #0e1016;
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
      transform: translateY(-50%);
      right: -0.5rem;
    }
  }
  .items__container {
    background-color: #0e1016;
    margin: 0;
    padding: 1em;
    height: 22em;
    width: 100%;
  }
}
