<template>
  <ApolloQuery
    id="notification-popup"
    class="rounded-borders-2x q-pr-sm q-pt-sm q-pb-sm"
    :query="require('@/graphql/GetNotifications.gql')"
    @result="onResult"
  >
    <template v-slot="{ result: { error, data, loading } }">
      <!-- Loading -->
      <Loading v-if="loading"></Loading>
      <q-scroll-area
        v-else-if="notifications.length"
        class="notification-popup__scroll full-width full-height "
        :thumb-style="scrollThumbStyle"
        :bar-style="scrollBarStyle"
      >
        <q-infinite-scroll
          @load="loadMore"
          :offset="10"
          :debounce="0"
          :disable="!hasMore"
          :reverse="false"
          scroll-target=".notification-popup__scroll > .scroll"
        >
          <template v-for="(notification, i) in notifications">
            <div
              class="notification flex no-wrap q-px-md q-pt-md q-pb-xs"
              :key="notification._id"
            >
              <div class="notification-icon q-pr-md">
                <q-img basic :src="notification.icon || defaultIcon" width="75px">
                </q-img>
              </div>
              <div class="" style="flex: 1 1 auto">
                <div
                  class="text-white text-font-montserrat"
                  style="font-size: 1.15em"
                >
                  {{ notification.title }}
                </div>
                <div class="text-grey ">
                  {{ notification.body }}
                </div>
                <div
                  class="full-width text-right text-small text-white"
                  style="opacity: 0.2;"
                >
                  {{ notification.createdAt | moment("from", "now") }}
                </div>
              </div>
            </div>
            <q-separator :key="i" v-if="i !== notifications.length - 1">
            </q-separator>
          </template>

          <template v-slot:loading>
            <div class="row justify-center q-my-md">
              <q-spinner color="primary" size="xl" />
            </div>
          </template>
        </q-infinite-scroll>
      </q-scroll-area>
      <div
        v-else
        class="absolute absolute-full flex justify-center items-center"
      >
        <span>
          Нет уведомлений
        </span>
      </div>
      <div
        class="absolute absolute-full flex justify-center items-center"
        style="background: inherit;"
        v-if="fab && permission === 'denied'"
      >
        <q-slide-transition :duration="500" appear>
          <div class="text-center text-highlight" style="max-width: 300px;">
            Push-уведомления запрещены. Следуйте этим инструкциям, чтобы
            включить их.
            <br />
            <q-img
                basic
              class="q-mt-sm"
              width="250px"
              src="/img/allow-notifications.png"
            ></q-img>
          </div>
        </q-slide-transition>
      </div>
    </template>
    <q-btn
      color="grey"
      flat
      class="absolute absolute-top-left"
      fab-mini
      icon="fas fa-plus"
      v-if="
        isAuthenticated && user.role.permissions.includes('createNotification')
      "
      @click="() => (showCreateNotificationDialog = true)"
    >
    </q-btn>
    <q-dialog v-model="showCreateNotificationDialog">
      <q-card class="app-dialog">
        <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
          <q-space />
          <q-btn icon="close" flat round dense v-close-popup />
        </q-card-section>

        <q-card-section style="min-width: 30em">
          <div class="q-mb-md">
            Создание уведомления
          </div>
          <q-input
            class="full-width"
            outlined
            bg-color="deposit-input"
            label-color="white"
            input-class="text-white q-px-sm"
            label="ID Пользователя (необязательно)"
            v-model="notification.userId"
          />
          <q-input
            class="full-width"
            outlined
            bg-color="deposit-input"
            label-color="white"
            input-class="text-white q-px-sm"
            label="Заголовок"
            v-model="notification.title"
          />
          <q-input
            class="full-width"
            outlined
            type="textarea"
            bg-color="deposit-input"
            label-color="white"
            input-class="text-white q-px-sm"
            label="Текс уведомления"
            v-model="notification.body"
          />
          <q-input
            class="full-width"
            outlined
            bg-color="deposit-input"
            label-color="white"
            input-class="text-white q-px-sm"
            label="Картинка (необязательно)"
            v-model="notification.icon"
          />
          <q-input
            class="full-width"
            outlined
            bg-color="deposit-input"
            label-color="white"
            input-class="text-white q-px-sm"
            label="Язык (необязательно)"
            v-model="notification.lang"
          />
          <q-input
            class="full-width"
            outlined
            bg-color="deposit-input"
            label-color="white"
            input-class="text-white q-px-sm"
            label="Ссылка (необязательно)"
            v-model="notification.url"
          />
          <ApolloMutation
            :mutation="require('../graphql/CreatePublicNotification.gql')"
            :variables="notificationVariable"
            @done="
              () => {
                notificationSend = true;
              }
            "
          >
            <template v-slot="{ mutate, loading, error }">
              <q-btn
                class="full-width q-mt-sm"
                label="Отправить"
                size="lg"
                @click="mutate()"
                :loading="loading"
                :disable="!notification.title || !notification.body"
                color="primary"
              >
              </q-btn>
              <div
                class="full-width text-center text-small text-negative q-mt-sm"
                v-if="error"
              >
                {{ error }}
              </div>
              <div
                class="full-width text-center text-small text-positive q-mt-sm"
                v-if="notificationSend"
              >
                Уведомление отправлено
              </div>
            </template>
          </ApolloMutation>
        </q-card-section>
      </q-card>
    </q-dialog>
    <q-fab
      color="grey"
      flat
      class="settings-btn"
      icon="far fa-cog"
      direction="left"
      v-model="fab"
    >
      <template v-if="permission !== 'denied'">
        <q-fab-action
          color="primary"
          flat
          @click="webPushRegister"
          label="Подписаться"
          v-if="!hasWebPush"
        />
        <q-fab-action
          color="negative"
          flat
          @click="webPushUnregister"
          label="Отписаться"
          v-else
        />
      </template>
    </q-fab>
  </ApolloQuery>
</template>

<script>
import scrollBarStyle from "../mixins/scrollBarStyle";
import Loading from "./Loading";
import notification from "../mixins/notification";
import _ from "lodash";
import { mapGetters, mapState } from "vuex";

export default {
  name: "NotificationPopup",
  components: {
    Loading
  },
  mixins: [scrollBarStyle, notification],
  data() {
    return {
      notificationSend: false,
      notification: {
        userId: "",
        title: "",
        body: "",
        lang: "",
        icon: "",
        url: ""
      },
      showCreateNotificationDialog: false,
      defaultIcon: "/img/icons/android-chrome-192x192.png",
      hasMore: true,
      notifications: [],
      hasWebPush: false,
      permission: "default",
      fab: false
    };
  },
  created() {
    this.hasWebPushCheck();

    if (Notification) {
      this.permission = Notification.permission;
    } else {
      this.permission = "denied";
    }
  },
  methods: {
    async webPushRegister() {
      await this.requestPermission();
      this.hasWebPush = true;
    },

    async webPushUnregister() {
      let subscription = await this.getSubscription();
      await this.$apollo.mutate({
        mutation: require("@/graphql/WebPushUnregister.gql"),
        variables: {
          subscription: {
            endpoint: subscription.endpoint,
            keys: subscription.keys
          }
        }
      });
      this.hasWebPush = false;
    },
    async hasWebPushCheck() {
      let subscription = await this.getSubscription();
      if (!subscription) {
        this.hasWebPush = false;
        return null;
      }

      let {
        data: { hasWebPush }
      } = await this.$apollo.query({
        query: require("@/graphql/HasWebPush.gql"),
        variables: {
          subscription: {
            endpoint: subscription.endpoint,
            keys: subscription.keys
          }
        }
      });
      this.hasWebPush = hasWebPush;
    },
    async loadMore(index, done) {
      if (!this.notifications.length) {
        this.hasMore = false;
        return null;
      }
      let {
        data: { getNotifications }
      } = await this.$apollo.query({
        query: require("@/graphql/GetNotifications.gql"),
        variables: { afterId: _.last(this.notifications)._id }
      });
      if (getNotifications.length) {
        this.notifications = this.notifications.concat(getNotifications);
      } else {
        this.hasMore = false;
      }
      done();
    },
    onResult(result) {
      if (result.data) {
        this.notifications = result.data.getNotifications;
      }
      return result;
    }
  },
  computed: {
    notificationVariable() {
      return Object.keys(this.notification).reduce((p, c) => {
        if (this.notification[c]) {
          p[c] = this.notification[c];
        }
        return p;
      }, {});
    },
    ...mapState({
      user: state => state.user
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  }
};
</script>

<style lang="scss">
#notification-popup {
  height: 300px;
  width: 450px;
  background: #1d2027;
  position: relative;
  .settings-btn {
    position: absolute;
    top: 0.5em;
    right: 0.5em;
  }
}
</style>
