<template>
  <q-scroll-area
    class="full-width app-scroll"
    :delay="1200"
    :style="{ height: 100 + 'vh' }"
    :thumb-style="scrollThumbStyle"
    :bar-style="scrollBarStyle"
  >
    <div
      class="full-width relative-position"
      style="height: 100vh;"
      v-if="loading"
    >
      <Loading></Loading>
    </div>
    <router-view></router-view>
  </q-scroll-area>
</template>

<script>
import swUpdate from "./mixins/swUpdate";
import scrollBarStyle from "./mixins/scrollBarStyle";
import Loading from "./components/Loading";

export default {
  mixins: [swUpdate, scrollBarStyle],
  components: { Loading },
  meta: {
    title: "Побеждать легко",
    titleTemplate: title => `${title} | CSGOTUNE.COM`,
    meta: {
      description: {
        name: "description",
        content:
          "Рулетка, где тебя ждут радость и огромные выигрыши. Вноси свои скины и побеждай. Побеждать легко!"
      },
      keywords: {
        name: "keywords",
        content:
          "csgotune,csgo,steam,gambling,crash,jackpot,roulette,blackjack,рулетка,джекпот,краш,ксго,блекджек"
      }
    }
  },
  data() {
    return {
      loading: true
    };
  },
  watch: {
    "$route.name": {
      handler() {
        this.$el.querySelector(".app-scroll .scroll").scrollTop = 0;
      }
    }
  },
  created() {
    this.$q.addressbarColor.set(process.env.VUE_APP_ADDRESSBAR_COLOR);
    this.$root.$on("appLoaded", () => (this.loading = false));
    this.$root.$on("appUnloaded", () => (this.loading = true));
  }
};
</script>

