import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";
import _ from "lodash";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    giveaways: [],
    showGiveawayList: false
  }),
  getters: {},
  mutations: {
    switchGiveawayList(state) {
      state.showGiveawayList = !state.showGiveawayList;
    },
    setGiveaways(state, giveaways) {
      Vue.set(state, "giveaways", giveaways);
    },
    addGiveaways(state, giveaway) {
      Vue.set(state.giveaways, state.giveaways.length, giveaway);
    },
    updateGiveaway(state, giveaway) {
      const giveawayIndex = _.findIndex(state.giveaways, {
        giveawayType: giveaway.giveawayType
      });
      if (giveawayIndex >= 0) {
        Object.keys(giveaway).map(key => {
          if (typeof giveaway[key] !== "undefined") {
            Vue.set(state.giveaways[giveawayIndex], key, giveaway[key]);
          }
        });
      }
    }
  },
  actions: {
    async getGiveaways({ commit, dispatch }) {
      const giveaways = (
        await apolloClient.query({
          query: require("@/graphql/GetGiveaways.gql")
        })
      ).data.getGiveaways;
      commit("setGiveaways", giveaways);
      dispatch("subscribeGiveawayUpdated");
    },
    subscribeGiveawayUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/GiveawayUpdated.gql")
      });
      observer.subscribe({
        next: data => commit("updateGiveaway", data.data.giveawayUpdated)
      });
    }
  }
};
