<template>
  <div
    id="blackjack-hand"
    :style="{ width: `${width}px`, height: `${height}px` }"
  >
    <div
      class="blackjack-hand__score rounded-borders-2x"
      v-show="animatedScore"
      :style="
        this.$q.screen.gt.sm
          ? this.reversed
            ? 'right:0; transform: translate(25%, -25%); top: 0;'
            : 'left:0; transform: translate(-25%, 25%); bottom: 0;'
          : this.reversed
          ? 'left:50%; transform: translate(-50%, -50%); top: 0;'
          : 'left:50%; transform: translate(-50%, -50%); top: 0;'
      "
      :class="{
        'text-negative': animatedScore > 21,
        'text-yellow-13': animatedScore === 21 && animatedCards.length === 2
      }"
    >
      {{ animatedScore }}
    </div>
    <div
      class="blackjack-hand__status"
      :class="animatedStatus"
      :style="{
        width: `${width * ($q.screen.gt.sm ? 1.5 : 1)}px`,
        height: `${height * 0.3}px`,
        right: this.reversed ? '0' : 'auto',
        left: !this.reversed ? '0' : 'auto',
        transform: `translateY(${-height * 0.3 * 1.65}px)`
      }"
    ></div>
    <BlackjackCard
      class="blackjack-hand__card"
      :class="`card-index-${index}`"
      v-for="(card, index) in animatedCards"
      :key="index"
      :card="card"
      :width="width"
      :style="{
        top: `${getCardRelativeY(index)}px`,
        left: `${getCardRelativeX(index)}px`
      }"
    >
    </BlackjackCard>
  </div>
</template>

<script>
import BlackjackCard from "./BlackjackCard";
import anime from "animejs";
import Vue from "vue";

export default {
  name: "BlackjackHand",
  components: {
    BlackjackCard
  },
  props: {
    reversed: {
      type: Boolean,
      default: false
    },
    value: {
      type: Object,
      default: () => ({})
    },
    width: {
      type: Number,
      default: 149
    }
  },
  computed: {
    height() {
      return this.width * (207 / 149);
    }
  },
  data() {
    return {
      animatedCards: [],
      animatedScore: 0,
      animatedStatus: ""
    };
  },
  watch: {
    animatedCards(newValue) {
      this.$emit(`input`, { cards: newValue, score: this.animatedScore });
    },
    animatedScore(newValue) {
      this.$emit(`input`, { score: newValue, cards: this.animatedCards });
    }
  },
  created() {
    this.animatedCards = this.value.cards.slice();
    this.animatedScore = this.value.score;
    this.animatedStatus = this.value.status;
  },
  methods: {
    async animateRemoveStatus({ duration = 500 } = {}) {
      const targets = this.$el.querySelector(".blackjack-hand__status");

      const animation = anime({
        targets,
        duration,
        easing: "easeOutQuad",
        opacity: 0,
        translateY: this.height * 0.3 * -2.25,
        complete: () => {
          this.animatedStatus = "";
        }
      });

      return animation.finished;
    },
    async animateStatus({ status, duration = 1000 }) {
      const targets = this.$el.querySelector(".blackjack-hand__status");

      if (this.animateStatus) {
        duration = duration / 2;
        await this.animateRemoveStatus({ duration });
      }

      const animation = anime({
        targets,
        duration,
        easing: "easeInQuad",
        opacity: [
          { value: 0, duration: 0 },
          { value: 1, duration }
        ],
        translateY: [
          { value: 0, duration: 0 },
          { value: this.height * 0.3 * -1.65, duration }
        ],
        begin: () => {
          this.animatedStatus = status;
        }
      });

      return animation.finished;
    },
    async animateScore({ score, duration = 750 }) {
      const animation = anime({
        targets: this,
        animatedScore: score,
        round: 1,
        easing: "easeOutQuad",
        duration
      });
      return animation.finished;
    },
    async setCard({ card, index = 0 } = {}) {
      Vue.set(this.animatedCards, index, card);
      await this.$nextTick();
    },
    async animateReverse({ duration = 750, index = 0 } = {}) {
      const targets = this.$el.querySelector(`.card-index-${index}`);
      targets.classList.remove("reversed");

      const animation = anime({
        targets,
        duration,
        easing: "easeInOutQuad",
        translateX: [
          {
            value: 0,
            duration: duration * 0.25
          },
          {
            value: "-30%",
            duration: duration * 0.25,
            easing: "cubicBezier(0.39, 0.575, 0.565, 1)"
          },
          {
            value: 0,
            duration: duration * 0.5
          }
        ],
        rotateY: [
          {
            value: "0deg",
            duration: 0
          },
          {
            value: "-180deg",
            duration: duration
          }
        ]
      });
      return animation.finished;
    },
    async animateRemove({ duration = 400 } = {}) {
      const targets = this.$el.querySelectorAll(".blackjack-hand__card");
      const translateXValue = this.reversed
        ? this.width * -10
        : this.width * 10;
      const translateYValue = this.reversed
        ? this.height * -20
        : this.height * -20;
      const animation = anime({
        targets,
        duration,
        easing: "linear",
        translateX: translateXValue + "px",
        translateY: translateYValue + "px",
        rotateY: [
          {
            value: "-180deg",
            duration: 0
          }
        ],
        begin: () => {
          this.animatedScore = 0;
          this.animateRemoveStatus();
        },
        complete: () => {
          this.animatedCards = [];
        }
      });
      return animation.finished;
    },
    getCardRelativeX(index) {
      if (this.$q.screen.lt.md) {
        return this.getCardRelativeXMobile(index);
      }
      if (index >= 6) {
        index = index - 6;
        return (
          0.2 * this.width + 0.5 * this.width * index * (this.reversed ? -1 : 1)
        );
      } else {
        return 0.5 * this.width * index * (this.reversed ? -1 : 1);
      }
    },
    getCardRelativeY(index) {
      if (this.$q.screen.lt.md) {
        return this.getCardRelativeYMobile(index);
      }
      if (index >= 6) {
        index = index - 6;
        return index % 2 === 0 ? 0.2 * this.height : -0.2 * 0.2 * this.height;
      } else {
        return index % 2 === 0 ? 0 : -0.2 * this.height;
      }
    },
    getCardRelativeXMobile(index) {
      if (index > 2) {
        index = 2;
      }
      return index === 0
        ? 0
        : this.width * (this.reversed ? 0.035 : -0.035) * index;
    },
    getCardRelativeYMobile(index) {
      if (index > 2) {
        index = 2;
      }
      return index * (0.25 * this.height);
    },
    getCardRelativeCoordinates(index) {
      return {
        x: this.getCardRelativeX(index),
        y: this.getCardRelativeY(index)
      };
    },
    getCardAbsoluteCoordinates(index) {
      let coord = this.getCardRelativeCoordinates(index);
      let rect = this.$el.getBoundingClientRect();
      coord.x += rect.left;
      coord.y += rect.top;
      /* Фикс относительно скролбара */
      coord.y += document.querySelector(".app-scroll .scroll").scrollTop;
      return coord;
    }
  }
};
</script>

<style lang="scss">
#blackjack-hand {
  position: relative;
  z-index: 1;
  .blackjack-hand__card {
    position: absolute;
    will-change: top, left, transform;
  }
  .blackjack-hand__score {
    position: absolute;
    background: #25272d;
    padding: 0.35em 0.5em;
    border: 0.1em solid #2e2f36;
    z-index: 1;
    width: 3em;
    text-align: center;
    font-size: 0.9em;
  }
  .blackjack-hand__status {
    position: absolute;
    top: 0;
    will-change: opacity, transform;
    &.victory {
      background: url("./assets/victory.png") center center / contain no-repeat;
    }
    &.defeat {
      background: url("./assets/defeat.png") center center / contain no-repeat;
    }
    &.draw {
      background: url("./assets/draw.png") center center / contain no-repeat;
    }
    &.bust {
      background: url("./assets/bust.png") center center / contain no-repeat;
    }
    &.blackjack {
      background: url("./assets/blackjack.png") center center / contain
        no-repeat;
    }
  }
}
</style>
