<template>
  <div id="not-found" class="absolute full-width">
    <div class="not-found__container">
      <q-img
          basic
        :width="$q.screen.lt.md ? '80%' : '543px'"
        src="/img/404.png"
        class="not-found__img"
        alt="404"
      />

      <p class="text-center">
        Возникли непредвиденные проблемы. Вы можете перейти на главную.
      </p>

      <q-btn
        class="q-px-lg q-py-sm"
        @click="toMainPage"
        outline
        style="color: #3585ff;"
        :loading="loading"
        no-caps
      >
        <span class="text-white text-font-weigh-300">
          На главную
        </span>
      </q-btn>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "NotFound",
  data: () => ({
    loading: false
  }),
  meta: {
    title: "Страница не найдена"
  },
  created() {
    this.$root.$emit("appLoaded");
  },
  methods: {
    async toMainPage() {
      this.loading = true;
      await this.$router.push(`/`);
    }
  }
};
</script>

