<template>
  <q-table
    :data="filteredBets"
    row-key="name"
    :columns="columns"
    dark
    id="bet-list"
    class="rounded-borders-2x"
    :rows-per-page-options="[0]"
    :pagination.sync="pagination"
    :hide-pagination="true"
    virtual-scroll
    :virtual-scroll-slice-size="15"
    :virtual-scroll-item-size="54"
    style="max-height: 400px;"
  >
    <template v-slot:header="props">
      <q-tr :props="props">
        <q-th
          v-for="col in props.cols"
          :key="col.name"
          :props="props"
          :auto-width="['user', 'betUserItem'].includes(col.name)"
        >
          {{ col.label }}
        </q-th>
      </q-tr>
    </template>
    <template v-slot:body="props">
      <Bet :props="props" :key="props.row._id"> </Bet>
    </template>
    <template v-slot:no-data>
      <div
        class="full-width flex justify-center items-center"
        style="font-size: 1.15em"
      >
        <q-icon name="far fa-clock" left class="text-highlight"></q-icon>
        <span>Ожидание игроков...</span>
      </div>
    </template>
  </q-table>
</template>

<script>
import Bet from "./Bet";
import { mapState } from "vuex";
export default {
  name: "BetList",
  components: {
    Bet
  },
  data() {
    return {
      pagination: { page: 1, rowsPerPage: 0 },
      loading: false,
      stopped: false
    };
  },
  props: ["value", "gameType", "showOnlyMyBets", "offset"],
  mounted() {
    this.$el.querySelector(".q-virtual-scroll").onscroll = evt => {
      this.onScroll(evt);
    };
  },
  methods: {
    setStop(value) {
      this.stopped = value;
    },
    onScroll({ target }) {
      const scrollElement = target;

      if (
        !this.loading &&
        !this.stopped &&
        scrollElement.scrollHeight -
          scrollElement.scrollTop -
          scrollElement.clientHeight <=
          (this.offset || 10)
      ) {
        this.loading = true;
        this.$emit("load", {
          done: () => {
            this.loading = false;
          },
          stop: () => {
            this.stopped = true;
          }
        });
      }
    }
  },
  computed: {
    ...mapState({
      user: state => state.user
    }),
    filteredBets() {
      let result = this.value;

      if (this.showOnlyMyBets) {
        result = (result || []).filter(
          bet =>
            bet.userId === this.user._id ||
            (bet.bet && bet.bet.userId === this.user._id)
        );
      }

      return result;
    },
    columns() {
      let result = [];
      switch (this.gameType) {
        case "deposit-history":
          result = [
            {
              name: "depositHistoryCurrency",
              label: "Способ",
              required: true,
              align: "left",
              field: row => row.currency
            },
            {
              name: "depositHistoryAmountMoney",
              label: "К оплате",
              required: true,
              align: "center",
              field: row => row.depositAmountMoney
            },
            {
              name: "depositHistoryAmount",
              label: "К получению",
              required: true,
              align: "center",
              field: row => row.depositAmount
            },
            {
              name: "depositHistoryPromocode",
              label: "Промо",
              required: true,
              align: "center",
              field: row => row.promocode
            },
            {
              name: "depositHistoryStatus",
              label: "Статус",
              required: true,
              align: "right",
              field: row => row.status
            }
          ];
          break;
        case "withdraw-history":
          result = [
            {
              name: "depositHistoryCurrency",
              label: "Способ",
              required: true,
              align: "left",
              field: row => row.currency
            },
            {
              name: "withdrawHistoryAmountMoney",
              label: "Получено",
              required: true,
              align: "center",
              field: row => row.withdrawAmountMoney
            },
            {
              name: "depositHistoryAmount",
              label: "Списано",
              required: true,
              align: "center",
              field: row => row.withdrawAmount
            },
            {
              name: "withdrawHistoryRecipient",
              label: "Реквизиты",
              required: true,
              align: "center",
              field: row => row.recipient
            },
            {
              name: "withdrawHistoryStatus",
              label: "Статус",
              required: true,
              align: "right",
              field: row => row.status
            }
          ];
          break;
        case "blackjack":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.bet.user
            },
            {
              name: "betUserItem",
              align: "left",
              field: row => row.bet.betUserItem
            },
            {
              name: "profitAmount",
              label: "Выплата",
              required: true,
              align: "center",
              field: row => row.bet.profitAmount
            },
            {
              name: "blackjackDealerScore",
              label: "Очки диллера",
              required: true,
              align: "center",
              field: row => row.dealerScore
            },
            {
              name: "blackjackUserScore",
              label: "Очки игрока",
              required: true,
              align: "center",
              field: row => row.userScore
            },
            {
              name: "betAmount",
              sortable: true,
              label: "Ставка",
              required: true,
              align: "right",
              field: row => row.bet.betAmount
            }
          ];
          break;
        case "royal-battle":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.user
            },
            {
              name: "betUserItem",
              align: "left",
              field: row => row.betUserItem
            },
            {
              name: "betAmount",
              sortable: true,
              label: "Ставка",
              required: true,
              align: "right",
              field: row => row.betAmount
            }
          ];
          break;
        case "royal-battle-players":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.user,
              style: { maxWidth: "150px" }
            },

            {
              name: "royalBattleChance",
              label: "Шанс",
              required: true,
              align: "center",
              field: row => row
            },
            {
              name: "betAmount",
              sortable: true,
              label: "Сумма ставок",
              required: true,
              align: "right",
              field: row => row.sumOfBets
            }
          ];
          break;
        case "dice":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.bet.user
            },
            {
              name: "betUserItem",
              align: "left",
              field: row => row.bet.betUserItem
            },
            {
              name: "betAmount",
              sortable: true,
              label: "Ставка",
              required: true,
              align: "center",
              field: row => row.bet.betAmount
            },
            {
              name: "diceMultiplier",
              label: "Множитель",
              required: true,
              align: "center",
              field: row => row.multiplier
            },
            {
              name: "diceGame",
              label: "Игра",
              required: true,
              align: "center",
              field: row => row
            },
            {
              name: "diceNumber",
              label: "Результат",
              required: true,
              align: "center",
              field: row => row.number
            },
            {
              name: "profitAmount",
              label: "Выплата",
              required: true,
              sortable: true,
              align: "right",
              field: row => row.bet.profitAmount
            }
          ];
          break;
        case "roulette":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.user,
              style: { maxWidth: "120px" }
            },
            {
              name: "betUserItem",
              align: "left",
              field: row => row.betUserItem
            },
            {
              name: "betAmount",
              label: "Ставка",
              required: true,
              sortable: true,
              align: "right",
              field: row => row.betAmount
            }
          ];
          break;
        case "hilo":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.user
            },
            {
              name: "betUserItem",
              align: "left",
              field: row => row.betUserItem
            },
            {
              name: "hiloType",
              label: "Тип ставки",
              required: true,
              align: "center",
              field: row => row.hiloBet.type
            },
            {
              name: "hiloMultiplier",
              label: "Множитель",
              required: true,
              align: "center",
              field: row => row.hiloBet.multiplier
            },
            {
              name: "profitAmount",
              label: "Выплата",
              required: true,
              sortable: true,
              align: "center",
              field: row => row.profitAmount
            },
            {
              name: "betAmount",
              sortable: true,
              label: "Ставка",
              required: true,
              align: "center",
              field: row => row.betAmount
            }
          ];
          break;
        case "crash":
          result = [
            {
              name: "user",
              label: "Игрок",
              required: true,
              align: "left",
              field: row => row.user
            },
            {
              name: "betUserItem",
              align: "left",
              field: row => row.betUserItem
            },
            {
              name: "crashMultiplier",
              label: "Множитель",
              required: true,
              align: "center",
              field: row => row.crashBet
            },
            {
              name: "crashPayout",
              label: "Выплата",
              sortable: true,
              required: true,
              align: "center",
              field: row => row.crashBet
            },
            {
              name: "betAmount",
              sortable: true,
              label: "Ставка",
              required: true,
              align: "center",
              field: row => row.betAmount
            }
          ];
          break;
      }
      return result;
    }
  }
};
</script>

<style lang="scss">
#bet-list {
  background: rgba(#26272d, 0.5);
  .q-virtual-scroll {
    &::-webkit-scrollbar {
      width: 9px;
      border-radius: 4px;
      opacity: 0.15;
      right: 2px;
      background: transparent;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 1px;
      background-color: rgba(117, 117, 117, 0.5);
      opacity: 0.75;
      width: 5px;
    }
  }
  .q-table__bottom {
    border: none;
    padding: 0;
    margin: 0;
    height: 3.77142857143em;
  }
  th {
    color: #757678;
  }
  tr:nth-child(even) {
    background: rgba(#282930, 0.5);
  }
  td,
  th {
    border-color: rgba(255, 255, 255, 0.075) !important;
  }
}
