<template>
  <div id="admin" class="wrapper column">
    <div class="row">
      <q-input
        :value="fromDate + ' - ' + toDate"
        readonly
        class="col "
        bg-color="deposit-input"
        label-color="grey"
        label="Период"
        input-style="color: white;"
        outlined
      >
        <template v-slot:append>
          <q-icon name="event" class="cursor-pointer" color="white">
            <q-popup-proxy
              ref="qDateProxy"
              transition-show="scale"
              transition-hide="scale"
            >
              <q-date
                dark
                v-model="dateModel"
                :today-btn="true"
                :minimal="true"
                :range="true"
              >
                <div class="row items-center justify-end">
                  <q-btn v-close-popup label="Close" color="primary" flat />
                </div>
              </q-date>
            </q-popup-proxy>
          </q-icon>
        </template>
      </q-input>
      <q-select
        options-dark
        class="col"
        outlined
        bg-color="deposit-input"
        label-color="white"
        input-class="text-white q-px-sm"
        label="Статус депозита"
        v-model="depositStatus"
        :emit-value="true"
        :options="[
          { label: 'Любой', value: null },
          { label: 'Оплачен', value: 'FINISHED' },
          { label: 'Отменен', value: 'CANCELED' },
          { label: 'С ошибкой', value: 'FAILED' },
          { label: 'Создан', value: 'CREATED' }
        ]"
      />
      <q-select
        options-dark
        class="col"
        outlined
        bg-color="deposit-input"
        label-color="white"
        input-class="text-white q-px-sm"
        label="Статус вывода"
        v-model="withdrawStatus"
        :emit-value="true"
        :options="[
          { label: 'Любой', value: null },
          { label: 'Выведен', value: 'PAYED' },
          { label: 'В обработке', value: 'PAYOUT' },
          { label: 'Отменен', value: 'CANCELED' },
          { label: 'С ошибкой', value: 'FAILED' },
          { label: 'В обработке', value: 'IN_PROGRESS' },
          { label: 'Создан', value: 'CREATED' }
        ]"
      />
    </div>

    <ApolloQuery
      class="col"
      style="height: 1px"
      :variables="{ startDate, endDate, depositStatus, withdrawStatus }"
      :query="
        gql => gql`
          query getInvoicesList(
            $startDate: Date
            $endDate: Date
            $depositStatus: String
            $withdrawStatus: String
          ) {
            getDepositsList(
              startDate: $startDate
              endDate: $endDate
              status: $depositStatus
            ) {
              _id
              userId
              depositAmount
              depositAmountMoney
              status
              createdAt
              currency
            }
            getWithdrawalsList(
              startDate: $startDate
              endDate: $endDate
              status: $withdrawStatus
            ) {
              _id
              userId
              withdrawAmount
              withdrawAmountMoney
              status
              createdAt
              currency
            }
              getDepositsStats(startDate: $startDate, endDate: $endDate) {
              depositAmount
            }
            getWithdrawalsStats(startDate: $startDate, endDate: $endDate) {
              withdrawAmount
            }
          }
        `
      "
      @result="onInvoicesListResult"
    >
      <template v-slot="{ result: { data, loading, error }, query }">
        <q-table
                v-if="!loading && !error"
          :data="invoices"
          :title="`Депозитов на сумму: ${((data || {}).getDepositsStats || {depositAmount: 0}).depositAmount} монет, выводов на сумму: ${((data || {}).getWithdrawalsStats || {withdrawAmount: 0}).withdrawAmount} монет`"
          row-key="name"
          :columns="[
            {
              name: '_id',
              align: 'left',
              label: 'ID',
              field: '_id',
              sortable: true
            },
            {
              name: 'userId',
              align: 'center',
              label: 'Пользователь',
              field: 'userId',
              sortable: true
            },
            {
              name: 'type',
              align: 'center',
              label: 'Тип',
              field: 'type',
              sortable: true
            },
            {
              name: 'amount',
              align: 'center',
              label: 'Сумма (в монетах)',
              field: row => row.depositAmount || row.withdrawAmount,
              sortable: true
            },
            {
              name: 'amountMoney',
              align: 'center',
              label: 'Сумма (в валюте)',
              field: row => row.depositAmountMoney || row.withdrawAmountMoney,
              sortable: true
            },
            {
              name: 'currency',
              align: 'center',
              label: 'Валюта',
              field: 'currency',
              sortable: true
            },
            {
              name: 'status',
              align: 'center',
              label: 'Статус',
              field: 'status',
              sortable: true
            },
            {
              name: 'createdAt',
              align: 'right',
              label: 'Время операции',
              field: row =>
                $options.filters.moment(row.createdAt, 'YYYY/MM/DD - hh:mm:ss'),
              sortable: true
            }
          ]"
          dark
          class="rounded-borders-2x"
          style="max-height: 100%;"
          :rows-per-page-options="[0]"
          :pagination="{ page: 1, rowsPerPage: 0 }"
          :hide-pagination="true"
          virtual-scroll
          :virtual-scroll-slice-size="30"
        />
      </template>
    </ApolloQuery>
  </div>
</template>

<script>
import _ from "lodash";

export default {
  name: "InvoicesStats",
  data() {
    return {
      startDate: new Date().setHours(0, 0, 0, 0),
      endDate: Date.now() + 24 * 60 * 60 * 1000,
      depositStatus: 'FINISHED',
      withdrawStatus: 'PAYED',
      getDepositsList: [],
      getWithdrawalsList: []
    };
  },
  methods: {
    onInvoicesListResult({ data, loading, error }) {
      if (!loading && !error) {
        this.getDepositsList = data.getDepositsList || [];
        this.getWithdrawalsList = data.getWithdrawalsList || [];
      }
    }
  },
  computed: {
    invoices() {
      return this.getDepositsList
        .map(v => Object.assign(v, { type: "Пополнение" }))
        .concat(
          this.getWithdrawalsList.map(v => Object.assign(v, { type: "Вывод" }))
        )
        .sort((a, b) => b.createdAt - a.createdAt);
    },
    dateModel: {
      get() {
        return [{ from: this.fromDate, to: this.toDate }];
      },
      set(value) {
        if (!value) {
          this.startDate = undefined;
          this.endDate = undefined;
        } else {
          let { from, to } = value;
          if (from && to) {
            this.fromDate = from;
            this.toDate = to;
          }
        }
      }
    },
    fromDate: {
      get() {
        return this.$options.filters.moment(this.startDate, "YYYY/MM/DD");
      },
      set(value) {
        this.startDate = new Date(value).getTime();
      }
    },
    toDate: {
      get() {
        return this.$options.filters.moment(this.endDate, "YYYY/MM/DD");
      },
      set(value) {
        this.endDate = new Date(value).getTime();
      }
    }
  }
};
</script>

<style scoped></style>
