import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";

const apolloClient = apolloProvider.defaultClient;
let subscription;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    dealerHand: ["kh", "7s"],
    userHand: ["ks", "ah"],
    gameResult: "victory",
    active: false,
    dealerScore: 17,
    userScore: 21,
    bet: {},
    history: []
  }),
  getters: {},
  mutations: {
    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },
    unshiftHistory(state, game) {
      let delay = 750 + (game.dealerHand.length - 2) * (350 + 750);
      setTimeout(() => {
        Vue.set(state, "history", [game, ...state.history]);
      }, delay);
    },
    updateValues(state, game) {
      if (game) {
        Object.keys(game).map(key => {
          if (typeof game[key] !== "undefined") {
            if (key === "bet" && game[key] === null) {
              return;
            }
            state[key] = game[key];
          }
        });
      }
    },
    reset(state){

      state.history = [];
      state._id = undefined;
    },
  },
  actions: {
    async loadMoreHistory({ commit }, { afterId }) {
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetBlackjackHistory.gql"),
          variables: { afterId }
        })
      ).data.getBlackjackHistory;

      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },

    async getGame({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetBlackjackGame.gql")
      });
      const game = data.data.getBlackjackGame;
      const history = data.data.getBlackjackHistory;
      commit("updateValues", game);
      commit("setHistory", history);
      dispatch("subscribeGameAdded");
    },
    reset({commit}){
      subscription.unsubscribe();
      commit('reset');
    },
    subscribeGameAdded({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/BlackjackAdded.gql")
      });
      subscription = observer.subscribe({
        next: data => {
          commit("unshiftHistory", data.data.blackjackAdded);
        }
      });
    }
  }
};
