<template>
  <div id="dice" class="q-pt-lg q-pb-lg q-px-xs-md q-px-md-none">
    <div class="wrapper-secondary game-title">
      <GameTitle
        :game="animatedGameData"
        gameName="Кости"
        :gameIcon="DiceIcon"
        :betAmount="animatedGameData.bet.betAmount"
        :lastProfitAmount="animatedGameData.bet.profitAmount"
      >
      </GameTitle>
    </div>
    <div class="dice__inner wrapper flex column items-center">
      <div class="dice-main flex column q-mb-xl">
        <div
          class="dice-main__bet-setup shadow-5 q-py-lg  q-px-md-lg"
          :class="{ 'full-width': $q.screen.lt.md }"
        >
          <div class="row">
            <div class="col q-px-md q-px-md-lg">
              <div
                class="full-width text-small text-grey q-mb-md flex justify-center justify-md-start items-center"
              >
                <span>Коэфф.</span>
              </div>
              <q-input
                bg-color="dice-setup-input"
                filled
                :value="multiplierInputValue"
                @change="onMultiplierInputChange"
                class="rounded-borders-2x input-multiplier"
              >
                <template v-slot:prepend>
                  <q-icon name="fas fa-times" color="primary"></q-icon>
                </template>
              </q-input>
            </div>
            <div class="col q-px-md-lg q-px-md">
              <div
                class="full-width text-small text-grey q-mb-md flex justify-center justify-md-start items-center"
              >
                <span>{{
                  { lower: "Меньше", higher: "Больше" }[betMakerProps.type]
                }}</span>
              </div>
              <q-input
                bg-color="dice-setup-input"
                filled
                v-model.number.lazy="betMakerProps.userNumber"
                class="rounded-borders-2x "
              >
                <template v-slot:append>
                  <q-icon
                    name="fas fa-exchange"
                    color="primary"
                    class="cursor-pointer dice-setup-input__icon"
                    size="sm"
                    @click="toggleType"
                  />
                </template>
              </q-input>
            </div>
            <div class="col q-px-md-lg q-px-md">
              <div
                class="full-width text-small text-grey q-mb-md flex justify-center justify-md-start items-center"
              >
                <span>Шанс</span>
              </div>
              <q-input
                bg-color="dice-setup-input"
                filled
                :value="chanceInputValue"
                @focus="onChanceInputFocus"
                @blur="onChanceInputBlur"
                @change="onChanceInputChange"
                class="rounded-borders-2x "
              >
              </q-input>
            </div>
          </div>
        </div>
        <div
          class="full-width dice-user-number__container flex items-center justify-center q-pt-lg q-mb-xl"
        >
          <div class="dice-user-number text-font-montserrat">
            {{ animationNumber }}
          </div>
          <div class="dice-user-number__bg">
            <q-icon v-html="diceCubeIcon" color="grey"></q-icon>
          </div>
        </div>
        <div class="row q-mb-lg q-mt-lg">
          <div class="offset-md-3"></div>
          <div class="col-md-6 col-xs-12 q-px-md">
            <DiceSlider
              :height="7"
              :type="betMakerProps.type"
              :animation-number="dice.number || animationNumber"
              v-model="betMakerProps.userNumber"
              :total-numbers-count="totalNumbersCount"
            ></DiceSlider>
          </div>
          <div class="offset-md-3"></div>
        </div>
        <div
          class="dice-history__container q-my-lg"
          :class="{ 'full-width': $q.screen.lt.md }"
        >
          <div class="dice__history">
            <VirtualHistory
              :items="dice.history"
              :virtual-scroll-item-size="70"
              :virtual-scroll-slice-size="30"
              class="history__inner full-width"
              :offset="500"
              @load="loadMoreHistory"
            >
              <template v-slot="{ item }">
                <div class="history__item-container">
                  <div
                    class="history__item"
                    :key="item._id"
                    :class="{
                      item__positive: item.victory,
                      item__negative: !item.victory
                    }"
                  >
                    {{ item.multiplier.toFixed(2) }}x
                  </div>
                </div>
              </template>
            </VirtualHistory>
          </div>
        </div>
        <div class="row flex justify-center">
          <div class="col-md-5 col-xs-12">
            <BetMaker
              v-model="betMakerProps"
              game-type="dice"
              class="full-width"
              style="position: relative"
              @bet="createDiceGame"
            ></BetMaker>
          </div>
        </div>
      </div>
      <BetList
        :showOnlyMyBets="betMakerProps.showOnlyMyBets"
        @load="loadMoreHistory"
        :offset="100"
        :debounce="250"
        :value="dice.history"
        game-type="dice"
        ref="betList"
        style="position: relative; z-index:5"
        class="wrapper-secondary full-width"
      ></BetList>
    </div>
  </div>
</template>

<script>
import store from "@/store";
import { mapState } from "vuex";
import diceCubeIcon from "./assets/dice-cube.svg";
import DiceSlider from "./DiceSlider";
import BetMaker from "@/components/BetMaker";
import anime from "animejs";
import BetList from "@/components/BetList";
import _ from "lodash";
import VirtualHistory from "../../components/VirtualHistory";
import GameTitle from "../../components/GameTitle";
import DiceIcon from "@/../public/img/game-icons/dice-icon.svg";
import {
  dice,
  dice1,
  dice2,
  dice3,
  stopAll,
  pickone,
  betSound
} from "@/howler";

export default {
  name: "Dice",
  components: {
    DiceSlider,
    BetMaker,
    BetList,
    VirtualHistory,
    GameTitle
  },
  meta: {
    title: "Кости"
  },
  data() {
    return {
      DiceIcon,
      diceCubeIcon,
      loadingHistory: false,
      betMakerProps: {
        betAmount: 100,
        type: "lower",
        userNumber: 5000,
        multiplier: 1.95,
        showOnlyMyBets: false,
        chance: 50
      },
      animationNumber: 5000,
      chanceInputValue: "50.00%",
      multiplierInputValue: "1.90",
      diceCommission: 0.05,
      totalNumbersCount: 10000,
      animatedGameData: null
    };
  },
  created() {
    if (typeof this.dice.number === `number`) {
      this.animationNumber = this.dice.number;
      this.betMakerProps.userNumber = this.dice.userNumber;
      this.betMakerProps.type = this.dice.type;
    }
    this.animatedGameData = _.cloneDeep(this.dice);
  },
  beforeDestroy() {
    stopAll([dice, dice1, dice2, dice3]);
  },
  watch: {
    "betMakerProps.showOnlyMyBets": {
      handler(value) {
        if (value) {
          this.$refs.betList.setStop(true);
        } else {
          this.$refs.betList.setStop(false);
        }
      }
    },
    "dice.history": {
      handler() {
        betSound.play();
      }
    },
    ["betMakerProps.userNumber"]() {
      this.onUserNumberChanged();
    },
    ["betMakerProps.type"]() {
      this.onUserNumberChanged();
    },
    ["betMakerProps.chance"](newValue) {
      this.chanceInputValue = newValue.toFixed(2) + "%";
    },
    ["betMakerProps.multiplier"](newValue) {
      this.multiplierInputValue = newValue.toFixed(2);
    }
  },
  methods: {
    async loadMoreHistory({ done = () => {}, stop = () => {} } = {}) {
      this.loadingHistory = true;

      let history = this.dice.history.length
        ? await this.$store.dispatch("dice/loadMoreHistory", {
            afterId: _.last(this.dice.history)._id
          })
        : [];

      if (history.length) {
        this.loadingHistory = false;
      } else {
        stop();
      }

      done();
    },
    async createDiceGame({ betAmount, userNumber, type, betUserItemId }) {
      const game = await this.$store.dispatch("dice/createGame", {
        betAmount,
        userNumber,
        type,
        betUserItemId
      });
      this.animatedGameData.bet.betAmount = game.bet.betAmount;
      const animation = anime({
        targets: this,
        animationNumber: game.number,
        round: 1,
        easing: "linear",
        duration: 1000
      });
      pickone([dice, dice1, dice2, dice3]).play();
      await animation.finished;
      this.animatedGameData = _.cloneDeep(game);
    },
    onUserNumberChanged() {
      if (this.betMakerProps.type === "lower") {
        if (this.betMakerProps.userNumber < 1) {
          return (this.betMakerProps.userNumber = 1);
        } else if (this.betMakerProps.userNumber > 9400) {
          return (this.betMakerProps.userNumber = 9400);
        }
      }
      if (this.betMakerProps.type === "higher") {
        if (this.betMakerProps.userNumber < 599) {
          return (this.betMakerProps.userNumber = 599);
        } else if (this.betMakerProps.userNumber > 9998) {
          return (this.betMakerProps.userNumber = 9998);
        }
      }

      let userNumbersCount = this.betMakerProps.userNumber;

      if (this.betMakerProps.type === "higher") {
        userNumbersCount =
          this.totalNumbersCount - 1 - this.betMakerProps.userNumber;
      }

      const odds = Number(
        (userNumbersCount / this.totalNumbersCount).toFixed(4)
      );
      let multiplier = 1 / odds;
      multiplier = Number(
        (multiplier - multiplier * this.diceCommission).toFixed(2)
      );
      this.betMakerProps.multiplier = multiplier;
      this.betMakerProps.chance = Number((odds * 100).toFixed(2));
    },
    onChanceInputBlur() {
      if (Number(this.chanceInputValue) === this.betMakerProps.chance) {
        this.chanceInputValue += "%";
      }
    },
    onChanceInputFocus() {
      this.chanceInputValue = this.chanceInputValue.replace("%", "");
    },
    onChanceInputChange({ target: { value } }) {
      let chance = Number(value) || 0;
      chance = chance < 0.01 ? 0.01 : chance > 94 ? 94 : chance;
      this.probabilityToUserNumber(chance / 100);
    },
    probabilityToUserNumber(probability) {
      let userNumber = Math.ceil(probability * this.totalNumbersCount);
      if (this.betMakerProps.type === "higher") {
        userNumber = this.totalNumbersCount - 1 - userNumber;
      }
      this.betMakerProps.userNumber = userNumber;
    },
    onMultiplierInputChange({ target: { value } }) {
      let multiplier = Number(value) || 0;
      multiplier =
        multiplier < 1.01 ? 1.01 : multiplier > 9500 ? 9500 : multiplier;
      multiplier = multiplier / (1 - this.diceCommission);
      this.probabilityToUserNumber(1 / multiplier);
    },
    toggleType() {
      if (this.betMakerProps.type === "lower") {
        this.betMakerProps.type = "higher";
      } else {
        this.betMakerProps.type = "lower";
      }
    }
  },
  async beforeRouteEnter(to, from, next) {
    if (!store.state.dice._id) {
      await store.dispatch("dice/getGame");
    }

    next();
  },
  beforeRouteLeave(to, from, next) {
    store.dispatch("dice/reset");
    next();
  },
  computed: {
    ...mapState({
      user: state => state.user,
      dice: state => state.dice
    })
  }
};
</script>

