import { apolloProvider } from "@/vue-apollo";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    name: undefined,
    avatar: undefined,
    balance: undefined,
    levelPoints: 0,
    level: 0,
    partnerId: undefined,
    provider: undefined,
    tradeUrl: undefined,
    publicChatBanBefore: undefined,
    recoup: undefined,
    lastLevelReward: 0,
    hasBattlePass: undefined
  }),
  getters: {
    isAuthenticated(state) {
      return !!state._id;
    }
  },
  mutations: {
    unsetPublicChatBanBefore(state) {
      state.publicChatBanBefore = undefined;
    },
    setHasBattlePass(state, value) {
      state.hasBattlePass = value;
    },
    authenticate(state, user) {
      if (user) {
        Object.keys(user).map(key => {
          if (typeof user[key] !== "undefined" && user[key] !== null) {
            state[key] = user[key];
          }
        });
      }
    }
  },
  actions: {
    async authenticate({ commit, dispatch }) {
      const user = (
        await apolloClient.query({
          query: require("@/graphql/IsAuthenticated.gql")
        })
      ).data.isAuthenticated;
      commit("authenticate", user);
      if (user) dispatch("subscribeUserUpdated");
    },
    subscribeUserUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/UserUpdated.gql")
      });
      observer.subscribe({
        next: data => {
          setTimeout(() => {
            commit("authenticate", data.data.userUpdated.user);
          }, data.data.userUpdated.delay || 0);
        }
      });
    }
  }
};
