<template>
  <div
    id="blackjack-card"
    :style="{ width: width + 'px', height: height + 'px' }"
    :class="{ reversed: !!card }"
  >
    <div
      class="forward"
      :style="{ zoom: width / 149 }"
      :class="`bg-${card}`"
    ></div>

    <div class="backward" :style="{ zoom: width / 149 }"></div>
  </div>
</template>

<script>
export default {
  name: "BlackjackCard",
  props: {
    card: {
      type: String,
      default: null
    },
    width: {
      type: Number,
      default: 149
    }
  },
  computed: {
    height() {
      return this.width * (207 / 149);
    }
  }
};
