<template>
  <span>
    <template v-for="(part, i) in parts">
      <template v-if="part.type === 'text'">
        <span :key="i">{{ part.text }}</span>
      </template>
       <router-link
               v-else-if="part.type === 'profileLink'"
               tag="span"
               :to="'/profile/' + part.args[0]"
               class="profile-link"
               :key="i"
       >@{{ part.args[1] }}</router-link>
      <template v-else-if="part.type === 'colorText'">
        <span :key="i" v-if="['administrator', 'bot'].includes(role)" :style="{color: part.args[0]}">{{ part.args[1] }}</span>
        <span v-else :key="i">{{part.args[1]}}</span>
      </template>
      <template v-else-if="part.type === 'externalLink'">
        <a :href="part.args[0]" v-if="['administrator', 'bot'].includes(role)" :key="i" target="_blank">{{ part.args[1] }}</a>
        <span v-else :key="i">{{part.args[1]}}</span>
      </template>
    </template>
  </span>
</template>

<script>
import _ from "lodash";

const types = ["profileLink", "colorText", "externalLink"];
const matchers = ["@\\[(\\d+)\\|([^\\]]+)\\]", "color\\[(#\\w{6})\\|([^\\]]+)\\]", "externalLink\\[(https?:\\/\\/.+)\\|([^\\]]+)\\]"];
const regexpMatch = new RegExp(matchers.join("|"), "g");
const regexpMatchers = matchers.map(match => new RegExp(match));

export default {
  name: "MessageText",
  props: ["value", "role"],
  data: () => ({
    parts: []
  }),
  created() {
    this.parts = this.breakTextIntoParts(this.value);
  },
  methods: {
    breakTextIntoParts(value = "") {
      let result = [];
      let lastIndex = 0;
      let matches = value.match(regexpMatch) || [];

      matches.forEach((text, arrayIndex) => {
        value = value.slice(lastIndex, value.length);
        lastIndex = 0;

        const startIndex = value.indexOf(text);
        const endIndex = startIndex + text.length;
        const regexpMatcherIndex = _.findIndex(regexpMatchers, function(
          regexpMatcher
        ) {
          return regexpMatcher.test(text);
        });

        if (lastIndex < startIndex - 1) {
          result.push({
            type: "text",
            text: value.slice(lastIndex, startIndex)
          });
        }

        const regexpExecResult = regexpMatchers[regexpMatcherIndex].exec(text);
        const part = {
          type: types[regexpMatcherIndex],
          text,
          args: regexpExecResult.slice(1)
        }
        result.push(part);
        this.$emit('type-match', part);

        lastIndex = endIndex;

        if (matches.length - 1 === arrayIndex && value.length > lastIndex) {
          result.push({
            type: "text",
            text: value.slice(lastIndex, value.length)
          });
        }
      });

      return matches.length ? result : [{ type: "text", text: value }];
    }
  }
};
</script>

<style scoped>
.profile-link {
  color: #65b7fd;
  cursor: pointer;
}
a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: #65b7fd;
}
</style>
