<template>
  <div class=" wrap justify-center" id="header-links">
    <div
      :key="i"
      class="link__wrapper"
      v-for="(link, i) in links"
      :class="{ 'router-link-active': subIsActive(link.href) }"
    >
      <div class="link__hover-bg" v-if="$q.screen.gt.sm"></div>
      <template v-if="typeof link.href !== 'function'">
        <router-link
          tag="li"
          class="cursor-pointer q-pt-sm q-pt-xs-none"
          :class="`${link.class}`"
          :to="link.href"
        >
          <q-btn :size="$q.screen.lt.md ? 'md' : ''">
            <q-icon v-html="link.icon"> </q-icon>
            <div class="bottom-arrow" v-if="$q.screen.gt.sm"></div>
          </q-btn>
          <q-badge v-if="link.new" style="transform: translate(25%, -25%)" color="negative" class="absolute absolute-top-right">
            new
          </q-badge>
          <div class="link__name" v-if="$q.screen.gt.sm">
            {{ link.name }}
          </div>
        </router-link>
      </template>
      <template v-else>
        <li
          class="cursor-pointer q-pt-sm q-pt-xs-none"
          :class="`${link.class}`"
          @click="link.href"
        >
          <q-btn :size="$q.screen.lt.md ? 'md' : ''">
            <q-icon v-html="link.icon"> </q-icon>
            <div class="bottom-arrow" v-if="$q.screen.gt.sm"></div>
          </q-btn>
          <div class="link__name" v-if="$q.screen.gt.sm">
            {{ link.name }}
          </div>
        </li>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: "HeaderLinks",
  props: ["links"],
  methods: {
    subIsActive(input) {
      const paths = Array.isArray(input) ? input : [input];

      return paths.some(path => {
        return this.$route.path.indexOf(path) === 0; // current path starts with this path string
      });
    }
  }
};
</script>

