<template>
  <q-virtual-scroll
    id="virtual-history"
    ref="scroll"
    :items="items"
    :virtual-scroll-horizontal="horizontal"
    :virtual-scroll-slice-size="virtualScrollSliceSize"
    :virtual-scroll-item-size="virtualScrollItemSize"
    v-touch-pan.prevent.mouse="handlePan"
  >
    <template v-slot="{ item, index }">
      <slot v-bind:item="item" v-bind:index="index"></slot>
    </template>
  </q-virtual-scroll>
</template>

<script>
import panScroll from "../mixins/panScroll";
export default {
  name: "VirtualHistory",
  mixins: [panScroll],
  props: {
    virtualScrollItemSize: {
      type: Number
    },
    virtualScrollSliceSize: {
      type: Number,
      default: 30
    },
    items: {
      type: Array,
      default: () => []
    },
    horizontal: {
      type: Boolean,
      default: true
    },
    offset: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      lastScrollSize: 0,
      loading: false,
      stopped: false,
      lastScrollDelta: 0,
      scrollMomentumID: null,
      animateScrollPositionID: null,
      inAnimation: false
    };
  },
  watch: {
    items(newValue, oldValue) {
      /* Unshift condition */
      if (oldValue[0] && oldValue[0]._id !== newValue[0]._id) {
        this.animateUnshift();
      }
    }
  },
  mounted() {
    this.$el.onscroll = this.scroll.bind(this);
  },
  beforeDestroy() {
    this.cancelMomentumTracking();
    this.cancelAnimateScrollPosition();
  },
  methods: {
    animateUnshift() {
      if (this.getScrollPosition() === 0) {
        let scrollPos = this.$el.querySelector(
          ".q-virtual-scroll__content .history__item-container:first-child"
        );
        if (scrollPos) {
          scrollPos = scrollPos.offsetWidth;
          this.setScrollPosition(scrollPos);
          this.setScrollPositionAnimate(0, 500);
        }
      }
    },
    scroll({ target }) {
      this.$emit("scroll", target);

      let isScrollReachedEnd = this.horizontal
        ? target.scrollWidth - target.scrollLeft - target.clientWidth <=
          this.offset
        : target.scrollHeight - target.scrollTop - target.clientHeight <=
          this.offset;

      if (isScrollReachedEnd && !this.loading && !this.stopped) {
        this.load(target);
      }
    },
    async load() {
      if (this.loading) {
        return;
      }
      this.loading = true;

      this.$emit("load", {
        done: async () => {
          this.$refs.scroll.refresh(-1);
          await this.$nextTick();
          this.loading = false;
        },
        stop: async () => {
          this.$refs.scroll.refresh(-1);
          await this.$nextTick();
          this.stopped = true;
          this.loading = false;
        }
      });
    },
    momentumLoop() {
      const currentScrollPosition = this.getScrollPosition();
      this.setScrollPosition(currentScrollPosition - this.lastScrollDelta);

      this.lastScrollDelta *= 0.96;

      if (Math.abs(this.lastScrollDelta) > 0.75) {
        this.scrollMomentumID = requestAnimFrame(this.momentumLoop);
      } else {
        this.lastScrollDelta = 0;
      }
    },
    cancelMomentumTracking() {
      cancelAnimationFrame(this.scrollMomentumID);
    },
    cancelAnimateScrollPosition() {
      this.inAnimation = false;
      cancelAnimationFrame(this.animateScrollPositionID);
    },
    scrollTo(...args) {
      this.$refs.scroll.scrollTo(...args);
    },
    setScrollPositionAnimate(
      to = 0,
      duration = 500,
      startedAt = Date.now(),
      from = this.getScrollPosition()
    ) {
      this.inAnimation = true;
      let ration = (Date.now() - startedAt) / duration;
      if (ration >= 1) {
        this.inAnimation = false;
        return this.setScrollPosition(to);
      }

      let distance = to - from;
      this.setScrollPosition(from + distance * ration);
      this.animateScrollPositionID = requestAnimFrame(
        this.setScrollPositionAnimate.bind(this, to, duration, startedAt, from)
      );
    },
    setScrollPosition(value) {
      this.$el.scrollTo(
        this.horizontal ? value : 0,
        !this.horizontal ? value : 0
      );
    },
    getScrollPosition() {
      return this.horizontal ? this.$el.scrollLeft : this.$el.scrollTop;
    },
    getScrollSize() {
      return this.horizontal ? this.$el.scrollWidth : this.$el.scrollHeight;
    },
    async handlePan({ direction, delta, isFinal }) {
      this.cancelAnimateScrollPosition();

      const deltaAxis =
        direction === "up" || direction === "down" ? delta.y : delta.x;
      this.cancelMomentumTracking();

      const currentScrollPosition = this.getScrollPosition();
      this.setScrollPosition(currentScrollPosition - deltaAxis);

      if (isFinal) {
        // отключено из-за фризов, ищи решения
        // this.scrollMomentumID = requestAnimFrame(this.momentumLoop);
      } else {
        this.lastScrollDelta = deltaAxis;
      }
    }
  }
};
</script>

<style lang="scss">
#virtual-history {
  &.q-virtual-scroll {
    &::-webkit-scrollbar {
      display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox */
    & {
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
    }
  }
}
</style>
