      class="text-white text-highlight text-font-montserrat text-weight-medium info__countdown text-right q-pl-xs-md q-pl-md-none "
      :class="{ absolute: $q.screen.gt.sm }"
      :style="
        $q.screen.gt.sm
          ? 'top:2.25em; transform: translateY(-100%); left: 56px;'
