                            currentLevelProgressPoints
                          )} / ${Math.ceil(currentTotalLevelProgressPoints)}`
                        "
                      />
                    </div>
                  </q-linear-progress>
                </div>
              </template>
            </VirtualHistory>
          </div>

          <div
            class="ladder__container relative-position"
