                          >
                            <q-popup-proxy
                              content-class="bg-transparent"
                              :auto-close="false"
                              anchor="center middle"
                              self="center middle"
                            >
                              <q-banner dark class="q-pa-none">
                                <UserItem
                                  :key="
                                    battlePassCollectedPrizes[index].prize._id
                                  "
                                  v-model="
                                    battlePassCollectedPrizes[index].prize
                                  "
