                          "
                          name="fas fa-unlock-alt"
                          size="xs"
                        >
                        </q-icon>
                        <q-icon v-else name="fas fa-check" size="xs"> </q-icon>
                      </div>
                      <div
                        class="item-text"
                        v-if="!battlePassCollectedPrizes[index]"
                      >
                        <template v-if="prize.type === 'Transfer'">
                          <span class="text-white text-highlight">
                            Баланс до
                            <span class="text-amber-9">
                              ${{ (prize.value * 5) | coinsToDollars }}
                            </span>
                          </span>
                        </template>
                        <template v-else-if="prize.type === 'Promocode'">
                          <span class="text-white text-highlight">
                            Промокод до
                            <span class="text-amber-9 text-highlight">
                              ${{ (prize.value * 5) | coinsToDollars }}
                            </span>
                          </span>
                        </template>
                        <template v-else-if="prize.type === 'UserItem'">
                          <span class="text-white text-highlight">
                            Скин до
                            <span class="text-amber-9 ">
                              ${{ (prize.value * 5) | coinsToDollars }}
                            </span>
                          </span>
                        </template>
                      </div>
                      <div class="item-text" v-else>
                        <span class="text-white text-highlight">
                          ${{
                            (battlePassCollectedPrizes[index].type ===
                            "UserItem"
                              ? battlePassCollectedPrizes[index].prize.soldPrice
                              : battlePassCollectedPrizes[index].type ===
                                "Promocode"
                              ? battlePassCollectedPrizes[index].prize.value
                              : battlePassCollectedPrizes[index].prize
                                  .transferAmount) | coinsToDollars
                          }}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
            </VirtualHistory>
            <div
              class="battlepass__buy-btn flex items-center justify-center column absolute "
