          </div>
        </div>
      </div>
    </div>
    <div class="battlepass-landing full-width">
      <div class="landing__header full-width flex justify-center items-center ">
        <div
          class="flex justify-center column items-center q-py-lg wrapper-secondary text-center"
        >
          <h2 class="q-pa-none q-ma-none text-weight-medium q-mb-sm">
            Получите премиум доступ
          </h2>
          <span class="text-h5 q-pa-none q-ma-none q-px-md">
            Боевой пропуск CSGOTUNE дает уникальный опыт пользования сайтом, в
            том числе <br v-if="$q.screen.gt.sm" />
            эксклюзивные бонусы и промокоды.
          </span>
          <q-btn
