          </q-card>
        </div>
        <div class="col-md-6 col-12 q-pa-md">
          <q-card
            dark
            bordered
            class="landing-card card-3 justify-between flex column full-height"
          >
            <q-card-section>
              <div
                class="text-h4 text-weight-bold"
                :class="{ 'text-h5': $q.screen.lt.md }"
              >
                Уникальные промокоды на депозит
              </div>
            </q-card-section>
            <div>
              <q-separator dark inset />

              <q-card-section class="text-font-montserrat text-big">
                Повышая свой уровень, Вы будете получать промокоды, которые дают
                Вам приятный бонус к пополнению, либо к Вашему текущему балансу.
              </q-card-section>
            </div>
          </q-card>
        </div>
        <div class="col-md-6 col-12  q-pa-md">
          <q-card
            dark
            bordered
            class="landing-card card-2 justify-between flex column full-height"
          >
            <q-card-section>
              <div
                class="text-h4 text-weight-bold"
                :class="{ 'text-h5': $q.screen.lt.md }"
              >
                Премиум-скины
              </div>
            </q-card-section>
            <div>
              <q-separator dark inset />

              <q-card-section class="text-font-montserrat text-big">
                В награду за полученный уровень боевого пропуска Вам случайным
                образом выпадут скины стоимостью до 50 долларов.
              </q-card-section>
            </div>
          </q-card>
        </div>

        <div class="col-md-6 col-12 q-pa-md">
          <q-card
            dark
            bordered
            class="landing-card card-4 justify-between flex column full-height"
          >
            <q-card-section>
              <div
                class="text-h4 text-weight-bold"
                :class="{ 'text-h5': $q.screen.lt.md }"
              >
                Кэшбэк
              </div>
            </q-card-section>
            <div>
              <q-separator dark inset />

              <q-card-section class="text-font-montserrat text-big">
                Шаг за шагом Вы будете получать все больший бонус к Вашему
                балансу, который Вы можете потратить на игры / приобретение
                скинов / вывод средств.
              </q-card-section>
            </div>
          </q-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loading from "../components/Loading";
import store from "@/store";
import { mapGetters, mapState } from "vuex";
import coins from "../mixins/coins";
import UserItem from "../components/UserItem";
import Promocode from "../components/Promocode";
import _ from "lodash";
import VirtualHistory from "../components/VirtualHistory";
import dialogs from "../mixins/dialogs";
import padStart from "../mixins/padStart";
import UserName from "../components/UserName";

const levelPointsPrizeCommission = 0.0035;
const levelConstant = 0.35;
const countLevelGenerate = 30;

export default {
  components: {
    Loading,
    UserItem,
    UserName,
    Promocode,
    VirtualHistory
  },
  mixins: [coins, dialogs, padStart],
  name: "BattlePass",
  meta: {
    title: "Боевой пропуск"
  },
  data() {
    return {
      battlePassLadderLoading: true,
      battlePassLadder: null,
      ladderItemWidth: 179,
      ladderItemPaddingX: 2,
      takePrizeLoading: false,
      levelRewards: [],
      takeLevelRewardLoading: false,
      progressHeight: 20,
      battlePassBuyLoading: false,
      seasonCountdown: 9999999
    };
  },
  created() {
    this.levelRewards = this.generateLevelRewards(
      1,
      this.level + countLevelGenerate
    );
  },
  methods: {
    async seasonEnd() {
      let date = new Date(this.battlePassLadder.expireAt);
      this.seasonCountdown =
        new Date(date.getFullYear(), date.getMonth() + 1, 1).getTime() -
        this.ts.now() +
        5000;
      await this.getBattlePassLadder();
    },
    buyBattlePass() {
      this.battlePassBuyLoading = true;
      this.$q
        .dialog({
          title: "Приобретение боевого пропуска",
          message: `Вы действительно хотите приобрести боевой пропуск, который будет действовать до ${this.$options.filters.moment(
            this.battlePassLadder.expireAt,
            "D MMMM"
          )}? Пропуск стоит ${
            this.battlePassLadder.price
          } монет, деньги спишутся с вашего баланса.`,
          cancel: true,
          persistent: true,
          dark: true
        })
        .onOk(async () => {
          if (!this.isAuthenticated) {
            this.battlePassBuyLoading = false;
            return this.$root.$emit("authorizationDialog");
          }
          try {
            await this.$store.dispatch("battlePass/buy");
          } catch {
            this.toggleDepositDialog(true);
          }
          this.battlePassBuyLoading = false;
        })
        .onCancel(() => {
          this.battlePassBuyLoading = false;
        });
    },
    async takeLevelReward(level) {
      this.takeLevelRewardLoading = true;
      await this.$apollo.mutate({
        mutation: require("@/graphql/GetLevelReward.gql")
      });
      this.takeLevelRewardLoading = false;
      this.$refs.levelRewardsScroll.setScrollPositionAnimate(
        (level - 1) * (this.ladderItemWidth + this.ladderItemPaddingX * 2),
        500
      );
    },
    addLevelRewards({ done }) {
      this.levelRewards = this.levelRewards.concat(
        this.generateLevelRewards(this.levelRewards.length + 1)
      );
      done();
    },
    levelToLevelPoints(level) {
      return Math.pow(level / levelConstant, 2) * 1000;
    },
    generateLevelRewards(startLevel, endLevel) {
      if (!endLevel) {
        endLevel = startLevel + countLevelGenerate - 1;
      }
      return _.range(startLevel, endLevel + 1).reduce((p, level) => {
        const levelPoints = this.levelToLevelPoints(level);
        const prevLevelPoints = this.levelToLevelPoints(level - 1);
        p.push({
          value: Math.round(
            (levelPoints - prevLevelPoints) * levelPointsPrizeCommission
          ),
          levelPoints
        });
        return p;
      }, []);
    },
    async takePrize(level) {
      this.takePrizeLoading = true;
      await this.$store.dispatch("battlePass/takePrize", level);
      this.takePrizeLoading = false;
      this.$refs.battlePassScroll.setScrollPositionAnimate(
        (level - 1) * (this.ladderItemWidth + this.ladderItemPaddingX * 2),
        500
      );
    },
    battlePassLadderPrizeStatus(level) {
      let isTook = !!this.battlePass.collectedPrizes[level - 1];
      return isTook
        ? "took"
        : this.battlePass.level >= level
        ? "unlocked"
        : "locked";
    },
    async getBattlePassLadder() {
      this.battlePassLadderLoading = true;
      this.battlePassLadder = (
        await this.$apollo.query({
          query: require("@/graphql/GetBattlePassLadder.gql")
        })
      ).data.getBattlePassLadder;
      this.battlePassLadderLoading = false;
    }
  },
  async mounted() {
    await this.getBattlePassLadder();
    this.seasonCountdown =
      this.battlePassLadder.expireAt - this.ts.now() + 5000;
    await this.$nextTick();
    await this.$nextTick();

    this.$refs.battlePassScroll.scrollTo(
      this.battlePass.collectedPrizes.length - 1,
      "start-force"
    );
    this.$refs.levelRewardsScroll.scrollTo(
      this.lastLevelReward - 1,
      "start-force"
    );
  },
  async beforeRouteEnter(to, from, next) {
    if (!store.state.battlePass._id) {
      await store.dispatch("battlePass/get");
    }

    next();
  },
  computed: {
    battlePassCollectedPrizes() {
      return this.battlePass.collectedPrizes;
    },
    currentBattlePassLevelPoints() {
      return this.battlePass.levelPoints;
    },
    currentBattlePassLevel() {
      return this.battlePass.level;
    },

    currentBattlePassTotalLevelProgressPoints() {
      return (
        this.battlePass.levelPointsForNextLevel -
        this.battlePass.levelPointsForCurrentLevel
      );
    },

    currentBattlePassLevelProgressPoints() {
      return (
        this.currentBattlePassLevelPoints -
        this.battlePass.levelPointsForCurrentLevel
      );
    },

    currentBattlePassLevelProgress() {
      return (
        this.currentBattlePassLevelProgressPoints /
        this.currentBattlePassTotalLevelProgressPoints
      );
    },

    currentTotalLevelProgressPoints() {
      return (
        this.levelToLevelPoints(this.level + 1) -
        this.levelToLevelPoints(this.level)
      );
    },

    currentLevelProgressPoints() {
      return this.levelPoints - this.levelToLevelPoints(this.level);
    },

    currentLevelProgress() {
      return (
        this.currentLevelProgressPoints / this.currentTotalLevelProgressPoints
      );
    },
    levelPoints() {
      return this.user.levelPoints;
    },
    lastLevelReward() {
      return this.user.lastLevelReward;
    },
    level() {
      return this.user.level;
    },
    ...mapState({
      user: state => state.user,
      battlePass: state => state.battlePass,
      ts: state => state.time.ts
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated",
