<template>
  <div id="user-item-list" class="relative-position">
    <q-scroll-area
      ref="scrollArea"
      :id="scrollId"
      class="user-item-list__scroll full-width q-pr-md full-height"
      :visible="true"
      :thumb-style="scrollThumbStyle"
      :bar-style="scrollBarStyle"
    >
      <template v-if="items.length">
        <q-infinite-scroll
          @load="onScrollReachedBottom"
          :offset="10"
          :debounce="0"
          class=" full-width"
          :disable="!items.length || !hasMore"
          :reverse="false"
          ref="infiniteScroll"
          :scroll-target="`#${scrollId} > .scroll`"
        >
          <q-virtual-scroll
            ref="virtualScroll"
            :items="itemsByRows"
            :scroll-target="`#${scrollId} > .scroll`"
            :virtual-scroll-slice-size="6"
            :virtual-scroll-item-size="virtualScrollItemSize"
            class="full-width "
          >
            <template v-slot="{ index }">
              <div class="row" :key="index">
                <UserItem
                  @click="selectItem(userItem)"
                  :mode="mode"
                  :class="
                    `col-${col} ${
                      selectedItems.findIndex(
                        item => userItem._id === item._id
                      ) !== -1
                        ? 'active'
                        : ''
                    }`
                  "
                  :value="userItem"
                  v-for="userItem in itemsByRows[index]"
                  :key="userItem._id"
                ></UserItem>
              </div>
            </template>
          </q-virtual-scroll>

          <template v-slot:loading>
            <div class="row justify-center q-my-md">
              <q-spinner color="primary" size="xl" />
            </div>
          </template>
        </q-infinite-scroll>
      </template>
      <template v-else>
        <div
          class="absolute absolute-full flex items-center justify-center"
          style="z-index: 1"
        >
          <div
            class="text-center flex column empty-block"
            v-if="mode !== 'market' && mode !== `skinsPlus`"
          >
            <div
              :class="{ 'q-mb-sm': mode !== 'deposit-mini' }"
              style="font-size: 1.25em"
            >
              Ваш инвентарь пуст
            </div>
            <q-btn
              v-if="mode !== 'deposit-mini'"
              color="primary"
              size="md"
              class="rounded-borders-2x"
              @click="toggleBuySkinDialog(true)"
              icon="fab fa-steam-symbol"
              label="Приобрести скины"
            >
            </q-btn>
          </div>
          <div class="text-center flex column" v-else>
            <div style="font-size: 1.25em">
              По вашему запросу ничего не было найдено
            </div>
          </div>
        </div>
        <div class="row full-width" style="filter: blur(6px)">
          <div
            :class="`col-${col}`"
            v-for="n in emptySlots"
            style="padding: 0.35em"
            :key="n"
          >
            <q-responsive :ratio="172.8 / 118.81">
              <q-skeleton type="rect" animation="none" />
            </q-responsive>
          </div>
        </div>
      </template>
    </q-scroll-area>
  </div>
</template>

<script>
import UserItem from "@/components/UserItem";
import dialogs from "../mixins/dialogs";
import _ from "lodash";
import scrollBarStyle from "../mixins/scrollBarStyle";
import Chance from "chance";

const chance = new Chance();

export default {
  name: "UserItemList",
  mixins: [dialogs, scrollBarStyle],
  components: { UserItem },
  props: {
    hasMore: {
      type: Boolean,
      default: true
    },
    items: {
      type: Array,
      default: () => []
    },
    virtualScrollItemSize: {
      type: Number,
      default: 120
    },
    col: {
      type: Number,
      default: 2
    },
    emptySlots: {
      type: Number,
      default: 12
    },
    mode: {
      type: String,
      default: "normal"
    },
    selectedItems: {
      type: Array,
      default: () => []
    }
  },
  data: () => ({
    scrollId: chance.hash()
  }),
  computed: {
    itemsByRows() {
      return this.itemRows.map(index => {
        return this.items.slice(
          index * this.itemInRows,
          (index + 1) * this.itemInRows
        );
      });
    },
    itemInRows() {
      return 12 / this.col;
    },
    itemRows() {
      return _.range(
        this.items.length ? Math.ceil(this.items.length / this.itemInRows) : 0
      );
    }
  },
  methods: {
    selectItem(item) {
      this.$emit("selectItem", item);
    },
    onScrollReachedBottom(index, done) {
      this.$emit("onLoadMore", done);
    }
  }
};
</script>

<style lang="scss">
#user-item-list {
}
</style>
