<template>
  <div id="dice-slider">
    <div
      class="dice-slider__inner flex no-wrap items-stretch full-width cursor-pointer"
      :style="{ height: `${height}px` }"
      @click="onSliderClick"
    >
      <div class="outer-numbers">
        <div
          class="outer-number absolute"
          v-for="(n, index) in [0, 25, 50, 75, 100]"
          :key="n"
          :style="{ left: `${index * 25}%` }"
        >
          {{ n }}
        </div>
      </div>
      <div
        :class="type === 'lower' ? 'inner-positive' : 'inner-negative'"
        :style="{ width: `${sliderPosition}%` }"
      ></div>
      <div
        :class="type === 'higher' ? 'inner-positive' : 'inner-negative'"
        :style="{ width: `${100 - sliderPosition}%` }"
      ></div>
      <div
        class="slider-dot cursor-pointer"
        :style="{
          left: `${sliderPosition}%`,
          width: `calc(${height}px + 1.65em)`
        }"
        v-touch-pan.horizontal.prevent.mouse="handlePan"
      ></div>
      <div
        class="animation-dot"
        :style="{ left: `${sliderAnimationPosition}%` }"
      >
        <q-icon v-html="animationDotIcon" size="sm" class="q-pb-sm"> </q-icon>
      </div>
    </div>
    <q-resize-observer @resize="resize" />
  </div>
</template>

<script>
import animationDotIcon from "./assets/dice-animation-icon.svg";

export default {
  name: "DiceSlider",
  props: ["value", "height", "totalNumbersCount", "type", "animationNumber"],
  data() {
    return {
      valueCopy: this.value,
      width: 0,
      animationDotIcon
    };
  },
  watch: {
    value(newValue) {
      this.valueCopy = newValue;
    }
  },
  methods: {
    resize({ width }) {
      this.width = width;
    },
    onSliderClick({ layerX }) {
      this.$emit(
        "input",
        Math.round((layerX / this.width) * this.totalNumbersCount)
      );
    },
    handlePan({ delta }) {
      let currentPosition = (this.sliderPosition / 100) * this.width;
      currentPosition = currentPosition + delta.x;
      currentPosition =
        currentPosition < 0
          ? 0
          : currentPosition > this.width
          ? this.width
          : currentPosition;
      this.$emit(
        "input",
        Math.round((currentPosition / this.width) * this.totalNumbersCount)
      );
    }
  },
  computed: {
    sliderAnimationPosition() {
      return (this.animationNumber / this.totalNumbersCount) * 100;
    },
    sliderPosition() {
      return (this.valueCopy / this.totalNumbersCount) * 100;
    }
  }
};
</script>

<style lang="scss">
#dice-slider {
  position: relative;
  z-index: 1;
  width: 100%;
  $size: 0.8em;
  border-radius: 1em;
  &:before {
    position: absolute;
    top: -$size;
    left: -$size;
    bottom: -$size;
    right: -$size;
    content: "";
    border-radius: inherit;
    border: 0.35em solid #35383f;
  }
  .dice-slider__inner {
    position: relative;
    .inner-positive,
    .inner-negative {
      transition: background 1s;
      border-radius: 1em;
    }
    .inner-positive {
      background: #50cf0c;
    }
    .inner-negative {
      background: #ff2323;
    }
    .slider-dot {
      z-index: 1;
      position: absolute;
      top: -$size;
      bottom: -$size;
      background: url("./assets/dice-slider-dot@2x.png") center center / 100%
        100% no-repeat;
      transform: translateX(-50%);
      will-change: left;
    }
    .animation-dot {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 0.25em;
      transform: translateX(-50%);
      background: white;
      transition: left 1s;
      will-change: left;
      .q-icon {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, -100%);
        will-change: left;
      }
    }
    .outer-numbers {
      position: absolute;
      top: -$size * 1.75;
      left: 0;
      right: 0;
      .outer-number {
        transform: translate(-50%, -100%);
        font-weight: 500;
        font-size: 0.85em;
        &:before {
          content: "";
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translate(-50%, 100%);
          width: 0.65em;
          height: 1em;
          background: #35383f;
          clip-path: polygon(50% 0%, 0% 85%, 100% 85%);
        }
      }
    }
  }
}
</style>
