<template>
  <div
    id="crash-chart"
    :style="{ width: this.width + 'px', height: this.height + 'px' }"
  >
    <div
      class="crash-chart__bg"
      :style="{
        top: paddingY - this.strokeWidth / 2 + 'px',
        bottom: paddingY - this.strokeWidth / 2 + 'px'
      }"
    ></div>
    <transition-group name="y-axis" tag="div" class="crash-chart__y-axis">
      <div class="y-axis__item" v-for="yAxis in yAxes" :key="yAxis">
        <span>x{{ yAxis.toFixed(2) }}</span>
      </div>
    </transition-group>
    <div
      class="crash-chart__multiplier"
      :class="{ 'multiplier-crashed': this.endedAt }"
      v-if="multiplier"
    >
      x{{ multiplier }}
    </div>
    <div class="crash-chart__timer" v-else>
      {{ ((this.startedAt - this.now) / 1000).toFixed(1) }}s
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import _ from "lodash";
import { SVG } from "@svgdotjs/svg.js";
import {crash,crash1,crash2,crash3,crash4, crashEnd, stopAll, pickoneByHash} from "@/howler"

export default {
  data() {
    return {
      svgElement: null,
      curveAnimationId: null,
      lastGeneratedPath: {},
      paddingY: 7,
      pointSize: 7,
      strokeWidth: 4,
      inAnimation: false,
      yAxes: [],
      nextYAxesOffset: 1
    };
  },
  props: ["width", "height", "createdAt", "startedAt", "endedAt", "number", "id"],
  watch: {
    width(newWidth, oldWidth) {
      this.update({ newWidth, oldWidth });
    },
    height(newHeight, oldHeight) {
      this.update({ newHeight, oldHeight });
    },
    now(newValue) {
      if (!this.inAnimation && newValue > this.startedAt && !this.endedAt) {
        this.animateCurve();
      }
    },
    endedAt(newValue) {
      if (newValue) {
        this.cancelAnimation();

      } else {
        this.reset();
      }
    }
  },
  created() {
    this.resetYAxes();
  },
  async mounted() {
    await this.$nextTick();
    this.draw();
    if (this.now - this.startedAt > 0) {
      this.animateCurve();

    }
    if (this.endedAt) {
      this.cancelAnimation();

    }
  },
  beforeDestroy() {
    cancelAnimationFrame(this.curveAnimationId);
    stopAll([crash, crash1, crash2, crash3, crash4]);
  },
  methods: {
    resetYAxes() {
      this.yAxes = [6, 4.2, 3, 2.15, 1.5, 1];
      this.nextYAxesOffset = 1;
      this.updateYAxes(this.multiplier);
    },
    generatePathZ(opts = {}) {
      return this.generatePath(opts) + `V${this.height} H0`;
    },
    generatePath({
      my = this.height - this.paddingY,
      cx0 = this.width / 2,
      cy0 = this.height - this.paddingY,
      cx1 = this.width / 2,
      cy1 = this.height - this.paddingY,
      cx = this.width,
      cy = this.height - this.paddingY
    } = {}) {
      if (this.svgElement) {
        this.svgElement.circle2.attr({
          cy: cy - this.pointSize / 2 + this.strokeWidth,
          cx: this.width - this.pointSize / 2
        });
        this.svgElement.circle1.attr({
          cy: my - this.pointSize / 2 + this.strokeWidth,
          cx: this.pointSize / 2
        });
      }
      this.lastGeneratedPath = {
        my,
        cx0,
        cy0,
        cx1,
        cy1,
        cx,
        cy
      };
      return `M0,${my} C${cx0},${cy0} ${cx1},${cy1} ${cx},${cy}`;
    },
    updateYAxes(multiplier) {
      const firstYAxis = _.first(this.yAxes);
      if (firstYAxis + this.nextYAxesOffset < multiplier) {
        this.yAxes.unshift(firstYAxis + this.nextYAxesOffset);
        this.yAxes.splice(this.yAxes.length - 2, 1);
        this.nextYAxesOffset = this.timeToNumber(
          this.now - this.startedAt + 2000
        );
      }
    },
    update({ oldWidth, newWidth, newHeight, oldHeight } = {}) {
      if (!this.svgElement) {
        return;
      }
      this.svgElement.rect.attr({ height: this.height, width: this.width });
      const ratioX = newWidth ? newWidth / oldWidth : 1;
      const ratioY = newHeight ? newHeight / oldHeight : 1;
      const pathOpts = Object.keys(this.lastGeneratedPath).reduce((p, c) => {
        if (c.indexOf("x") !== -1) {
          p[c] = this.lastGeneratedPath[c] * ratioX;
        } else if (c.indexOf("y") !== -1) {
          p[c] = this.lastGeneratedPath[c] * ratioY;
        }
        return p;
      }, {});
      this.svgElement.path1.attr({ d: this.generatePathZ(pathOpts) });
      this.svgElement.path2.attr({ d: this.generatePath(pathOpts) });
    },
    reset() {
      this.svgElement.rect.fill(this.svgElement.gradient1);
      this.svgElement.path1.attr({ d: this.generatePathZ() });
      this.svgElement.path2.attr({ d: this.generatePath() });
      this.svgElement.path2.attr({
        stroke: "var(--q-color-primary)"
      });
      this.svgElement.circle1.fill("var(--q-color-primary)");
      this.resetYAxes();
    },
    draw() {
      let draw = SVG()
        .addTo(this.$el)
        .size("100%", "100%");
      let defs = draw.defs();
      let gradient1 = defs.gradient("linear", function(add) {
        add.stop(0, "rgba(68,121,251,.9)");
        add.stop(1, "rgba(68,121,251,0)");
      });
      gradient1.from(0, 0).to(0, 1);
      let gradient2 = defs.gradient("linear", function(add) {
        add.stop(0, "rgba(211,46,65,.9)");
        add.stop(1, "rgba(211,46,65,0)");
      });
      gradient2.from(0, 0).to(0, 1);
      let path1 = draw.path(this.generatePathZ()).attr({
        "stroke-width": this.strokeWidth + "px",
        fill: "none"
      });
      let clip = defs.clip().add(path1);
      let rect = draw.rect(this.width, this.height).clipWith(clip);
      rect.fill(gradient1);
      let path2 = draw.path(this.generatePath()).attr({
        "stroke-width": this.strokeWidth + "px",
        fill: "none",
        stroke: "var(--q-color-primary)"
      });
      let circle1 = draw
        .circle(this.pointSize)
        .fill("var(--q-color-primary)")
        .attr({
          cy: this.lastGeneratedPath.cy - this.pointSize / 2 + this.strokeWidth,
          cx: this.pointSize / 2
        });
      let circle2 = draw
        .circle(this.pointSize)
        .fill("#ffffff")
        .attr({
          cy: this.lastGeneratedPath.cy - this.pointSize / 2 + this.strokeWidth,
          cx: this.width - this.pointSize / 2
        });
      this.svgElement = {
        gradient1,
        gradient2,
        rect,
        path1,
        path2,
        circle1,
        circle2
      };
    },
    cancelAnimation({ svgElement = this.svgElement } = {}) {
      this.inAnimation = false;
      cancelAnimationFrame(this.curveAnimationId);
      svgElement.rect.fill(svgElement.gradient2);
      svgElement.path2.attr({
        stroke: "var(--q-color-negative)"
      });
      this.svgElement.circle1.fill("var(--q-color-negative)");
     stopAll([crash, crash1, crash2, crash3, crash4]);
      crashEnd.play();
    },
    animateCurve({
      startedAt = this.startedAt,
      lastYAxesNumber = 6,
      svgElement = this.svgElement
    } = {}) {
      this.inAnimation = true;
      let duration = this.numberToTime(lastYAxesNumber);
      let audio = pickoneByHash([crash,crash1,crash2,crash3,crash4], this.id);
      let id = audio.play();
      audio.seek((this.now - this.startedAt)/1000, id);

      const update = () => {
        let offset = this.ts.now() - startedAt;
        offset = offset < 0 ? 0 : offset > duration ? duration : offset;
        const ratio = offset / duration;
        const cy1 = (this.height - this.paddingY) * (1 - ratio);
        let cy = (this.height - this.paddingY) * (1 - ratio) + this.paddingY;
        cy =
          cy > this.height - this.paddingY ? this.height - this.paddingY : cy;
        svgElement.path1.attr({ d: this.generatePathZ({ cy, cy1 }) });
        svgElement.path2.attr({ d: this.generatePath({ cy, cy1 }) });
        if (ratio < 1) {
          this.curveAnimationId = requestAnimFrame(update);
        }
      };

      update();
    },
    timeToNumber(ms = 0) {
      return Number(Math.exp(ms / 10000).toFixed(2));
    },
    numberToTime(number = 1) {
      return Math.floor(Math.log(number) * 10000);
    }
  },
  computed: {
    ...mapState({
      ts: state => state.time.ts,
      now: state => state.time.now
    }),
    multiplier() {
      let multiplier = 0;
      if (this.endedAt) {
        multiplier = this.number.toFixed(2);
      } else if (this.now >= this.startedAt) {
        multiplier = this.timeToNumber(this.now - this.startedAt).toFixed(2);
      }
      this.updateYAxes(multiplier);
      return multiplier;
    }
  }
};
</script>

<style lang="scss">
#crash-chart {
  position: relative;
  user-select: none;
  z-index: 1;
  .y-axis__item {
    transition: all 1s;
  }
  .y-axis-enter {
    transform: translateY(-100%);
  }
  .y-axis-leave-to {
    opacity: 0;
    transform: translateY(100%);
  }
  .y-axis-leave-active {
    position: absolute;
    bottom: 0;
  }
  .crash-chart__bg {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
    background: url("./assets/crash-bg.png") center / 100% 100% no-repeat;
  }
  .crash-chart__y-axis {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    transform: translateX(-100%);
    padding-right: 0.5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    mask: linear-gradient(
      to bottom,
      transparent 9%,
      #fff 18% 100%,
      transparent 100%
    );
    mask-size: 120% 120%;
    mask-position: center;
    mask-repeat: no-repeat;
    mask-composite: intersect;
    .y-axis__item {
      text-align: right;
      font-size: 0.75em;
      color: #c1c4cd;
    }
  }
  .crash-chart__multiplier,
  .crash-chart__timer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 6.5em;
    color: white;
    font-weight: 600;
    &.multiplier-crashed {
      color: var(--q-color-negative);
    }
  }
}
</style>
