          <q-icon
            name="fas fa-angle-right"
            class="q-ml-xs"
            :class="{ 'fa-flip-horizontal': showGiveawayList }"
          ></q-icon
        ></span>
        <div class="switcher-description flex justify-start">раздача</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "GiveawaySwitcher",
  methods: {
    switchGiveawayList() {
      this.$store.commit("giveaway/switchGiveawayList");
    }
  },
  computed: {
    ...mapState({
      showGiveawayList: state => state.giveaway.showGiveawayList
    })
  }
};
</script>

