export default {
  namespaced: true,
  state: () => ({
    depositDialogShow: false,
    giveawayHistoryDialog: false,
    giveawayHistoryDialogType: "hourly",
    authorizationDialogShow: false,
    jackpotDialogShow: false,
    chatBanDialogShow: false,
    buySkinDialogShow: false,
    chatBanDialogUser: null,
    fairControlDialogGame: null,
    fairControlDialogShow: false
  }),
  mutations: {
    toggleDepositDialog(state, show) {
      state.depositDialogShow =
        typeof show === "boolean" ? show : !state.depositDialogShow;
    },
    toggleJackpotDialog(state, show) {
      state.jackpotDialogShow =
        typeof show === "boolean" ? show : !state.jackpotDialogShow;
    },
    toggleGiveawayHistoryDialog(state, show) {
      state.giveawayHistoryDialog =
        typeof show === "boolean" ? show : !state.giveawayHistoryDialog;
    },
    toggleBuySkinDialog(state, show) {
      state.buySkinDialogShow =
        typeof show === "boolean" ? show : !state.buySkinDialogShow;
    },
    toggleAuthorizationDialog(state, show) {
      state.authorizationDialogShow =
        typeof show === "boolean" ? show : !state.authorizationDialogShow;
    },
    toggleChatBanDialog(state, show) {
      state.chatBanDialogShow =
        typeof show === "boolean" ? show : !state.chatBanDialogShow;
    },
    setChatBanDialogUser(state, user) {
      state.chatBanDialogUser = user;
    },
    toggleFairControlDialog(state, show) {
      state.fairControlDialogShow =
        typeof show === "boolean" ? show : !state.fairControlDialogShow;
    },
    setFairControlDialogGame(state, game) {
      state.fairControlDialogGame = game;
    },
    setGiveawayHistoryDialogType(state, type) {
      state.giveawayHistoryDialogType = type;
    }
  }
};
