<template>
  <q-banner dark id="user-item-list-deposit-popup" class="rounded-borders">
    <div class="flex justify-between q-mb-md">
      <span class="flex items-center">
        <q-icon
          name="icon-icomoon-csgo-icon"
          color="white"
          class=" q-mr-sm"
        ></q-icon>
        {{
          $q.screen.gt.sm
            ? "Выберите скины на сумму не менее 1000 монет"
            : "Депозит от 1000 монет"
        }}
      </span>
      <q-btn icon="close" flat round dense v-close-popup />
    </div>
    <div class="row q-mb-lg">
      <div class="col-6 q-pr-lg">
        <div
          class="bet-amount rounded-borders-2x flex items-center q-pl-lg text-highlight "
        >
          <q-icon name="fas fa-sack-dollar" class="q-mr-sm"> </q-icon>
          <span>
            {{ totalDepositAmount | coinsWithoutShort }}
          </span>
        </div>
      </div>
      <div class="row col-6">
        <q-btn
          label="Пополнить"
          style="font-size: 1.1em"
          class="rounded-borders-2x col-9"
          color="positive"
          padding="md xs"
          @click="redirectToDepositPage"
          :disable="totalDepositAmount < 1000"
        >
        </q-btn>
        <div class="col-3 full-height">
          <q-btn
            flat
            :loading="loading"
            @click="refetch"
            icon="fas fa-redo-alt"
            color="primary"
            class="rounded-borders-2x full-width full-height"
          >
          </q-btn>
        </div>
      </div>
    </div>
    <ApolloQuery
      :query="require('@/graphql/GetUserSteamItems.gql')"
      fetchPolicy="no-cache"
      @result="onResult"
      class="items__container rounded-borders-2x "
      ref="query"
    >
      <template v-slot="{ result: { error, data, loading } }">
        <!-- Loading -->
        <Loading v-if="loading"></Loading>
        <!-- Result -->
        <UserItemList
          :items="filteredUserItems"
          v-else
          @onLoadMore="loadMoreUserItems"
          :hasMore="hasMore"
          :col="$q.screen.gt.sm ? 4 : 6"
          class="full-height"
          mode="deposit-mini"
          :emptySlots="9"
          @selectItem="selectItem"
          :virtual-scroll-item-size="82"
          :selected-items="selectedItems"
        >
        </UserItemList>
      </template>
    </ApolloQuery>
  </q-banner>
</template>

<script>
import UserItemList from "./UserItemList";
import scrollBarStyle from "../mixins/scrollBarStyle";
import Loading from "./Loading";
import _ from "lodash";
import { openURL } from "quasar";
import gql from "graphql-tag";

export default {
  name: "UserItemListDepositPopup",
  mixins: [scrollBarStyle],
  components: {
    UserItemList,
    Loading
  },
  props: {},
  data() {
    return {
      loading: false,
      userItems: [],
      selectedItems: [],
      queryLoaded: false,
      hasMore: true
    };
  },
  computed: {
    totalDepositAmount() {
      return this.selectedItems.reduce((p, c) => {
        return p + c.item.price;
      }, 0);
    },
    filteredUserItems() {
      return this.userItems.filter(v => !!v.item);
    }
  },
  methods: {
    async refetch() {
      this.loading = true;
      try {
        await this.$apollo.mutate({
          mutation: gql`
            mutation refreshSteamInventory {
              refreshSteamInventory {
                _id
              }
            }
          `
        });
        let { data } = await this.$apollo.query({
          query: require("@/graphql/GetUserSteamItems.gql")
        });

        this.userItems = data.getUserSteamItems;
        this.selectedItems = [];
        this.hasMore = true;
      } catch {}
      this.loading = false;
    },
    redirectToDepositPage() {
      openURL(`${process.env.VUE_APP_API_ENDPOINT}/deposit/skinsplus`);
    },
    selectItem(item) {
      let index = this.selectedItems.findIndex(
        v => !!v.item && v._id === item._id
      );
      if (index === -1) {
        this.selectedItems.push(item);
      } else {
        this.selectedItems.splice(index, 1);
      }
    },
    async loadMoreUserItems(done) {
      let lastUserItem = _.last(this.userItems);
      if (lastUserItem) {
        let { data } = await this.$apollo.query({
          query: require("@/graphql/GetUserSteamItems.gql"),
          variables: {
            afterId: lastUserItem._id
          }
        });
        if (data.getUserSteamItems.length) {
          this.userItems = [...this.userItems, ...data.getUserSteamItems];
        } else {
          this.hasMore = false;
        }
      } else {
        this.hasMore = false;
      }
      done();
    },
    onResult(result) {
      if (result.data && !this.queryLoaded) {
        this.userItems = result.data.getUserSteamItems;
        this.queryLoaded = true;
      }
      return result;
    }
  }
};
</script>

