<template>
  <div id="crash" class="q-pt-lg q-pb-lg q-px-xs-md q-px-md-none">
    <div class="wrapper-secondary">
      <GameTitle
        :game="crash"
        :countdown="countdown"
        :sumOfBets="sumOfBets"
        gameName="Краш"
        :gameIcon="crashIcon"
        class="q-mb-lg"
      >
      </GameTitle>
    </div>
    <div class="crash__inner wrapper">
      <div class="crash__history flex full-width">
        <VirtualHistory
          :items="crash.history"
          :virtual-scroll-item-size="70"
          :virtual-scroll-slice-size="35"
          class="history__inner full-width"
          :offset="500"
          @load="loadMoreHistory"
        >
          <template v-slot="{ item }">
            <div class="history__item-container" :key="item._id">
              <div
                class="history__item list-complete-item"
                :class="{
                  item__gold: item.number >= 25,
                  item__pink: item.number >= 7 && item.number < 25,
                  item__purple: item.number >= 3 && item.number < 7,
                  item__blue: item.number >= 1.2 && item.number < 3,
                  item__red: item.number < 1.2
                }"
              >
                {{ item.number.toFixed(2) }}x
              </div>
            </div>
          </template>
        </VirtualHistory>
      </div>

      <div class="crash__body wrapper-secondary q-pt-sm">
        <div class="row">
          <div
            class="col-md-4 col-xs-12 order-xs-last order-md-none q-py-xl q-pb-xs-sm q-pb-md-none"
          >
            <BetMaker
              v-model="betMakerProps"
              gameType="crash"
              @bet="createCrashBet"
              @stop="stopCrashBet"
            ></BetMaker>
          </div>
          <div
            class="col-md-8 col-xs-12 items-stretch flex q-pt-md"
            :style="{
              paddingLeft: $q.screen.lt.md ? '2.5em' : '8em',
              height: $q.screen.lt.md ? '250px' : 'auto'
            }"
          >
            <div class="crash-chart__container full-width">
              <CrashChart
                :width="chartWidth"
                :height="chartHeight"
                :startedAt="crash.startedAt"
                :id="crash._id"
                :endedAt="crash.endedAt"
                :createdAt="crash.createdAt"
                :number="crash.number"
              >
              </CrashChart>
              <q-resize-observer @resize="updateChartSize" />
            </div>
          </div>
        </div>
        <div class="row q-mt-lg">
          <div class="col">
            <BetList
              :value="crash.bets"
              gameType="crash"
              :showOnlyMyBets="betMakerProps.showOnlyMyBets"
            ></BetList>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BetList from "../../components/BetList";
import store from "@/store";
import { mapGetters, mapState } from "vuex";
import BetMaker from "@/components/BetMaker";
import CrashChart from "./CrashChart";
import _ from "lodash";
import { apolloProvider } from "@/vue-apollo";
import VirtualHistory from "../../components/VirtualHistory";
import GameTitle from "../../components/GameTitle";
import crashIcon from "@/../public/img/game-icons/crash-icon.svg";
import {crash, crash1, crash2, crash3, crash4, crashEnd, crashTimer, betSound, stopAll} from "@/howler"


const apolloClient = apolloProvider.defaultClient;

export default {
  name: "Crash",
  components: {
    BetMaker,
    BetList,
    GameTitle,
    CrashChart,
    VirtualHistory
  },
  meta: {
    title: "Краш"
  },
  data: () => ({
    chartHeight: 0,
    chartWidth: 0,
    betMakerProps: {
      autoStop: 0,
      betAmount: 100,
      showOnlyMyBets: false
    },
    loadingHistory: false,
    countdown: 0,
    crashIcon,
    lastBetsCount: 0,
  }),
  beforeDestroy() {
    stopAll([crash, crash1, crash2, crash3, crash4, betSound, crashTimer, crashEnd]);
  },
  watch: {
    'crash.bets': {
      handler(newValue = []) {
        if (this.lastBetsCount < newValue.length) {
          betSound.play();
        }
        this.lastBetsCount = newValue.length;
      }
    },
    "crash.startedAt": {
      immediate: true,
      handler: function(newValue) {
        if (newValue) {
          let result = this.crash.startedAt - this.now;
          this.countdown = result < 0 ? 0 : result;
          if(this.countdown){
            let id = crashTimer.play();
            crashTimer.seek(10- this.countdown/1000, id);
          }
        }
      }
    },
  },
  mounted() {},
  beforeRouteLeave(to, from, next){
    //store.dispatch("time/stopUpdateInterval");
    store.dispatch("crash/reset");
    next();
  },
  async beforeRouteEnter(to, from, next) {
    //store.dispatch("time/startUpdateInterval");
    if (!store.state.crash._id) {
      await store.dispatch("crash/getGame");
    }

    next();
  },
  computed: {
    ...mapState({
      user: state => state.user,
      crash: state => state.crash,
      now: state => state.time.now
    }),
    ...mapGetters({
      sumOfBets: "crash/sumOfBets"
    })
  },
  methods: {
    async createCrashBet({ betAmount, autoStop, betUserItemId }) {
      await apolloClient.mutate({
        mutation: require("@/graphql/CreateCrashBet.gql"),
        variables: { betAmount, autoStop, betUserItemId }
      });
    },
    async stopCrashBet() {
      await apolloClient.mutate({
        mutation: require("@/graphql/StopCrashBet.gql")
      });
    },
    updateChartSize({ width, height }) {
      this.chartWidth = width;
      this.chartHeight = height;
    },
    async loadMoreHistory({ done, stop }) {
      let history = await this.$store.dispatch("crash/loadMoreHistory", {
        afterId: _.last(this.crash.history)._id
      });

      if (history.length) {
        this.loadingHistory = false;
      } else {
        stop();
      }

      done();
    }
  }
};
</script>

