      mask-composite: intersect;
      &.left__mask {
        mask: linear-gradient(
          to left,
          transparent 9%,
          #fff 18% 82%,
          transparent 91%
        );
        mask-size: 120% 120%;
        mask-position: center;
        mask-repeat: no-repeat;
        mask-composite: intersect;
      }

      .q-scrollarea__thumb {
        display: none;
      }

      .history__inner {
        display: inline-flex;
        .history__item-container {
          padding-right: 0.5em;
        }
        .history__item {
          min-width: 5em;
          border: 0.15em;
          border-style: solid;
          border-radius: 0.6em;
          text-align: center;
          padding: 0.25em 0.75em;
          font-weight: 500;
          font-size: 0.9em;
          flex-basis: auto;
          color: white;
          &.item__gold {
            $color: #ffc500;
            border-color: $color;
            color: $color;
          }
          &.item__pink {
            border-color: #ff00ff;
          }
          &.item__purple {
