<template>
  <q-tr :props="props" style="height: 3.75em">
    <template v-for="col in props.cols">
      <q-td :auto-width="true" v-if="col.name === 'user'" :key="col.name">
        <UserName
          :value="col.value"
          :showAvatar="true"
          :style="col.style"
        ></UserName>
      </q-td>
      <q-td v-else-if="col.name === 'crashMultiplier'" :key="col.name">
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          {{
            col.value.stopped
              ? `x${col.value.multiplier}`
              : isAutoStopped(col.value.autoStop)
              ? `x${col.value.autoStop}`
              : "-"
          }}
        </div>
      </q-td>
      <q-td v-else-if="col.name === 'royalBattleChance'" :key="col.name">
        <div
          class="flex items-center full-width "
          :class="getFlexAlignClass(col.align)"
        >
          <div
            class="royal-battle-chance q-mr-sm"
            :style="{
              boxShadow: `0px 0px 1em 2px rgba(${hexToRgb(col.value.color)},1)`,
              backgroundColor: col.value.color,
              width: '1.25em',
              height: '1.25em',
              borderRadius: '50%'
            }"
          ></div>
          <span> {{ col.value.chance.toFixed(2) }}% </span>
        </div>
      </q-td>

      <q-td v-else-if="col.name === 'hiloMultiplier'" :key="col.name">
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          {{ col.value ? `x${col.value}` : "-" }}
        </div>
      </q-td>
      <q-td v-else-if="col.name === 'diceGame'" :key="col.name">
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          <span
            :class="
              col.value.type === 'lower' ? 'text-positive' : 'text-negative'
            "
          >
            {{ getDiceTypeText(col.value.type) }}
          </span>
          {{ col.value.userNumber }}
        </div>
      </q-td>
      <q-td v-else-if="col.name === 'diceNumber'" :key="col.name">
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          {{ col.value }}
        </div>
      </q-td>
      <q-td v-else-if="col.name === 'diceMultiplier'" :key="col.name">
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          {{ col.value ? `x${col.value}` : "-" }}
        </div>
      </q-td>

      <q-td v-else-if="col.name === 'hiloType'" :key="col.name">
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          {{ hiloTypeNames[col.value] }}
        </div>
      </q-td>
      <q-td v-else-if="col.name === 'crashPayout'" :key="col.name">
        <div
          class="flex items-baseline "
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1.2em"
        >
          <q-icon
            name="fas fa-sack-dollar"
            class="text-highlight q-mr-xs"
            v-if="col.value.stopped || isAutoStopped(col.value.autoStop)"
          >
          </q-icon>
          <span class="text-weight-bold text-highlight">
            {{
              col.value.stopped
                ? `${$options.filters.coins(col.value.payout)}`
                : isAutoStopped(col.value.autoStop)
                ? `${$options.filters.coins(col.value.autoStopPayout)}`
                : "-"
            }}
          </span>
        </div>
      </q-td>
      <q-td
        v-else-if="
          col.name === 'blackjackDealerScore' ||
            col.name === 'blackjackUserScore'
        "
        :key="col.name"
      >
        <div class="flex items-center" :class="getFlexAlignClass(col.align)">
          {{ col.value }}
        </div>
      </q-td>

      <q-td v-else-if="col.name === 'profitAmount'" :key="col.name">
        <div
          class="flex items-baseline "
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1.2em"
        >
          <q-icon
            name="fas fa-sack-dollar"
            class="text-highlight q-mr-xs"
            v-if="col.value"
          >
          </q-icon>
          <span class="text-weight-bold text-highlight">
            {{ (col.value ? `${col.value}` : "-") | coins }}
          </span>
        </div>
      </q-td>

      <q-td v-else-if="col.name === 'betAmount'" :key="col.name">
        <div
          class="flex items-baseline no-wrap"
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1.2em"
        >
          <q-icon name="fas fa-sack-dollar" class="text-highlight q-mr-xs">
          </q-icon>
          <span class="text-weight-bold text-highlight">
            {{ col.value | coins }}
          </span>
        </div>
      </q-td>

      <q-td
        v-else-if="col.name === 'betUserItem'"
        :key="col.name"
        :auto-width="true"
      >
        <template v-if="col.value">
          <q-img basic :src="col.value.item.image" alt="userItem" width="46px">
            <q-tooltip content-class="bg-modal" content-style="font-size: 1em;">
              {{ col.value.item._id }}
            </q-tooltip>
          </q-img>
        </template>
      </q-td>

      <q-td v-else-if="col.name === 'depositHistoryCurrency'" :key="col.name">
        <div
          :class="getFlexAlignClass(col.align)"
          class="flex overflow-hidden"
          style="text-overflow: ellipsis; max-width: 75px;"
        >
          {{ depositHistoryCurrencyToDepositOption(col.value).name }}
        </div>
      </q-td>

      <q-td
        v-else-if="col.name === 'depositHistoryStatus'"
        :key="col.name"
        class="text-right"
      >
        <template v-if="col.value === 'CREATED' || col.value === 'PAYIN'">
          <a
            :href="props.row.payLink"
            target="_blank"
            class="text-primary"
            style="text-decoration: none"
          >
            {{ depositHistoryStatusToText(col.value) || col.value }}
          </a>
        </template>
        <template v-else>
          {{ depositHistoryStatusToText(col.value) || col.value }}
        </template>
      </q-td>

      <q-td
        v-else-if="col.name === 'withdrawHistoryStatus'"
        :key="col.name"
        class="text-right"
      >
        {{ withdrawHistoryStatusToText(col.value) || col.value }}
      </q-td>

      <q-td v-else-if="col.name === 'withdrawHistoryRecipient'" :key="col.name">
        <div class="flex justify-center">
          <q-btn flat label="...">
            <q-popup-proxy content-class="bg-transparent">
              <q-banner dark>
                {{ col.value || "Реквизиты не были сохранены" }}
              </q-banner>
            </q-popup-proxy>
          </q-btn>
        </div>
      </q-td>

      <q-td
        v-else-if="col.name === 'depositHistoryAmountMoney'"
        :key="col.name"
      >
        <div
          class="flex items-baseline no-wrap"
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1em"
        >
          <q-icon
            :name="
              depositHistoryCurrencyToDepositOption(props.row.currency)
                .currencyIcon
            "
            class="text-highlight q-mr-xs"
          >
          </q-icon>
          <span class="text-weight-bold text-highlight">
            {{ col.value }}
          </span>
        </div>
      </q-td>
      <q-td
        v-else-if="col.name === 'withdrawHistoryAmountMoney'"
        :key="col.name"
      >
        <div
          class="flex items-baseline no-wrap"
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1em"
        >
          <template v-if="props.row.skinsPlusItem">
            <q-btn flat label="...">
              <q-popup-proxy content-class="bg-transparent">
                <q-banner dark>
                  {{ props.row.skinsPlusItem.itemId }}
                </q-banner>
              </q-popup-proxy>
            </q-btn>
          </template>
          <template v-else-if="props.row.withdrawAmountMoney">
            <q-icon
              :name="
                depositHistoryCurrencyToDepositOption(props.row.currency)
                  .currencyIcon
              "
              class="text-highlight q-mr-xs"
            >
            </q-icon>
            <span class="text-weight-bold text-highlight">
              {{ col.value }}
            </span>
          </template>
          <template v-else>
            <q-icon name="fas fa-sack-dollar" class="text-highlight q-mr-xs">
            </q-icon>
            <span class="text-weight-bold text-highlight">
              {{ props.row.withdrawAmount }}
            </span>
          </template>
        </div>
      </q-td>

      <q-td v-else-if="col.name === 'depositHistoryAmount'" :key="col.name">
        <div
          class="flex items-baseline no-wrap"
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1em"
        >
          <q-icon name="fas fa-sack-dollar" class="text-highlight q-mr-xs">
          </q-icon>
          <span class="text-weight-bold text-highlight">
            {{ col.value }}
          </span>
        </div>
      </q-td>

      <q-td v-else-if="col.name === 'depositHistoryPromocode'" :key="col.name">
        <div
          class="flex items-baseline no-wrap"
          :class="getFlexAlignClass(col.align)"
          style="font-size: 1em"
        >
          <q-icon
            v-if="col.value"
            name="fas fa-badge-dollar"
            class="text-highlight  cursor-pointer"
          >
            <q-popup-proxy
              content-class="bg-transparent"
              content-style="border-radius: 0"
            >
              <Promocode
                :value="col.value"
                :active="true"
                style="min-width: 23em;"
              >
              </Promocode>
            </q-popup-proxy>
          </q-icon>
          <span v-else>
            -
          </span>
        </div>
      </q-td>
    </template>
  </q-tr>
</template>

<script>
import UserName from "./UserName";
import { mapGetters, mapState } from "vuex";
import color from "../mixins/color";
import depositOptions from "../mixins/depositOptions";
import withdrawOptions from "../mixins/withdrawOptions";
import Promocode from "./Promocode";

export default {
  name: "Bet",
  props: ["props"],
  mixins: [depositOptions, withdrawOptions, color],
  data() {
    return {
      hiloTypeNames: {
        red: "Красный",
        black: "Черный",
        numbers: "2-9",
        letters: "J,Q,K,A",
        ka: "K,A",
        a: "A",
        joker: "Джокер",
        hi: "Hi",
        lo: "Lo"
      }
    };
  },
  components: {
    UserName,
    Promocode
  },
  methods: {
    getFlexAlignClass(align) {
      let classes = {
        center: "justify-center",
        right: "justify-end",
        left: "justify-start"
      };
      return classes[align] || classes.center;
    },
    getDiceTypeText(value) {
      return value === "lower" ? `<` : `>`;
    },
    isAutoStopped(autoStop) {
      return autoStop && autoStop <= this.currentMultiplier;
    }
  },
  computed: {
    ...mapState({
      crash: state => state.crash
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated",
      currentMultiplier: "crash/currentMultiplier"
    })
  }
};
</script>

<style lang="scss"></style>
