<script>
import { dom } from "quasar";
import { openURL } from "quasar";
const { height, style, width } = dom;
import { mapGetters, mapState } from "vuex";
import Chat from "./Chat";
import store from "@/store";
import GiveawaySwitcher from "./GiveawaySwitcher";
import GiveawayList from "./GiveawayList";
import DepositDialog from "./DepositDialog";
import JackpotDialog from "./JackpotDialog";
import ChatBanDialog from "./ChatBanDialog";
import FairControlDialog from "./FairControlDialog";
import WebPush from "./WebPush";
import NotificationPopup from "./NotificationPopup";
import BuySkinDialog from "./BuySkinDialog";
import gameLinks from "../mixins/gameLinks";
import battlepassIcon from "@/assets/img/battlepass-icon.svg";
import socialNetworkingBonusIcon from "@/assets/img/gift-icon.svg";
import marketIcon from "@/assets/img/shopping-cart-solid.svg";
import dialogs from "../mixins/dialogs";
import HeaderLinks from "./HeaderLinks";
import { mute, unmute } from "../howler";
import GiveawayHistory from "./GiveawayHistory";
import FirstDepositBonusDialog from "./FirstDepositBonusDialog";

export default {
  components: {
    Chat,
    HeaderLinks,
    ChatBanDialog,
    GiveawayList,
    GiveawaySwitcher,
    DepositDialog,
    JackpotDialog,
    FairControlDialog,
    WebPush,
    NotificationPopup,
    BuySkinDialog,
    GiveawayHistory,
    FirstDepositBonusDialog
  },
  mixins: [gameLinks, dialogs],
  data() {
    return {
      prevPageName: this.$route.name,
      audioDisabled: false,
      showRightMenu: true,
      additionalHeaderCenterLinks: [
        {
          href: store.getters["user/isAuthenticated"]
            ? "/battlepass"
            : this.showAuthorizationDialog,
          name: "Battle Pass",
          icon: battlepassIcon,
          class: "battlepass"
        },
        {
          href: "/social-networking-bonus",
          name: "Бонусная система",
          new: true,
          icon: socialNetworkingBonusIcon,
          class: "social-networking-bonus"
        },
        {
          href: store.getters["user/isAuthenticated"]
            ? this.toggleBuySkinDialog
            : this.showAuthorizationDialog,
          name: "Маркет",
          icon: marketIcon,
          class: "market"
        }
      ],
      openURL,
      logOutUrl: process.env.VUE_APP_API_ENDPOINT + "/auth/logout",
      authorizationDialog: false,
      layoutMinHeight: 0,
      headerHeight: 0,
      footerHeight: 0,
      footerLinks: [
        { href: "/privacy-policy", name: "Политика конфиденциальности" },
        { href: "/terms-of-service", name: "Условия и положение" },
        { href: "/faq", name: "FAQ" }
      ],
      authorizationLinks: [
        {
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/steam`,
          icon: "fab fa-steam",
          colors: ["#2a406c", "#303646"],
          hover: "Steam"
        },
        {
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/vkontakte`,
          icon: "fab fa-vk",
          colors: ["#2987f4", "#1f64b4"],
          hover: "Вконтакте"
        },
        {
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/yandex`,
          icon: "fab fa-yandex",
          iconColor: "negative",
          colors: ["#ffffff", "#ffffff"],
          hover: "Яндекс"
        },
        {
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/google`,
          icon: "fab fa-google",
          colors: ["#FF0000", "#de0505"],
          hover: "Google"
        },
        /*{
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/tiktok`,
          icon: "fab fa-tiktok",
          colors: ["#000000", "#000000"]
        },
        {
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/instagram`,
          icon: "fab fa-instagram",
          colors: ["#DA2C7A", "#D15361"]
        },*/
        {
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/facebook`,
          icon: "fab fa-facebook",
          colors: ["#3A5A92", "#3A5A92"],
          hover: "Facebook"
        }
        /*{
          href: `${process.env.VUE_APP_API_ENDPOINT}/auth/instagram`,
          icon: "fab fa-twitter",
          colors: ["#53ADF0", "#53ADF0"]
        }*/
      ]
    };
  },
  watch: {
    ['$route'](value, oldValue){
      this.prevPageName = (oldValue || value).name;
    },
    audioDisabled: {
      handler(value) {
        if (value) {
          this.$q.cookies.set("audioDisabled", true, {
            expires: new Date(Date.now() + 360 * 24 * 60 * 60 * 1000)
          });
          mute();
        } else {
          this.$q.cookies.remove("audioDisabled");
          unmute();
        }
      }
    }
  },
  created() {
    this.showRightMenu = this.$q.screen.gt.sm;
    this.$root.$emit("appLoaded");
    this.$root.$on("authorizationDialog", () => {
      this.showAuthorizationDialog();
    });
    ym(69160060, "init", {
      params: {userId: this.isAuthenticated ? this.user._id : null},
      clickmap: true,
      trackLinks: true,
      accurateTrackBounce: true,
      webvisor: true
    });
  },
  mounted() {
    this.audioDisabled = !!this.$q.cookies.get("audioDisabled") || false;

  },
  methods: {
    isGamePageAnimation(pageName){
      const isPrevGamePageName = this.isGamePageName(this.prevPageName)
      let result = this.isGamePageName(pageName ) && isPrevGamePageName
      if(this.isGamePage && !isPrevGamePageName){
        setTimeout(()=>{
          this.prevPageName = pageName
        }, 1)
      }
      return result;
    },
    isGamePageName(pageName){
      return ['Crash', 'Roulette', 'Hilo', 'RoyalBattle', 'Blackjack', 'Dice'].includes(pageName);
    },
    toggleRightMenu(value) {
      this.showRightMenu =
        typeof value === "boolean" ? value : !this.showRightMenu;
      if (!this.showRightMenu && this.chatOpened) {
        this.toggleChat();
      }
    },
    showAuthorizationDialog() {
      this.authorizationDialog = true;
      ym(69160060, "reachGoal", "auth");
    },

    showDepositDialog() {
      if (this.isAuthenticated) {
        this.$store.commit("dialog/toggleDepositDialog", true);
        ym(69160060, "reachGoal", "Deposit_Start");
      } else {
        this.showAuthorizationDialog();
      }
    },
    toggleChat() {
      this.$store.dispatch("chat/toggleChat");
    },
    async updateHeights() {
      await this.$nextTick();
      this.headerHeight = height(this.$refs.header.$el);
      this.footerHeight = height(this.$refs.footer.$el);
      this.$store.commit("chat/changeChatWidth", width(this.$refs.chat.$el));
      this.layoutMinHeight = Number(
        style(this.$refs.layout.$el, "min-height").replace("px", "")
      );
    }
  },
  computed: {
    isGamePage(){
      return this.isGamePageName(this.$route.name);
    },
    bodyBgImage() {
      let result = {
        Crash: "/img/game-body-bg/bg-crash.jpg",
        Roulette: "/img/game-body-bg/bg-roulette.jpg",
        Hilo: "/img/game-body-bg/bg-hilo.jpg",
        RoyalBattle: "/img/game-body-bg/bg-royal-battle.jpg",
        Blackjack: "/img/game-body-bg/bg-blackjack.jpg",
        BattlePass: "/img/game-body-bg/bg-battlepass.jpg",
        SocialNetworkingBonus: "/img/game-body-bg/bg-bonus.jpg"
      }[this.$route.name];

      return this.$q.screen.lt.md ? "none" : result || "/img/app-bg.jpg";
    },
    ...mapState({
      user: state => state.user,
      ts: state => state.time.ts,
      usersCount: state => state.online.usersCount,
      chatOpened: state => state.chat.chatOpened,
      chatWidth: state => state.chat.chatWidth,
      giveaways: state => state.giveaway.giveaways,
      buySkinDialogShow: state => state.dialog.buySkinDialogShow
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  },
  async beforeRouteEnter(to, from, next) {
    let response;

    try {
      response = await fetch(`${process.env.VUE_APP_API_ENDPOINT}/healthcheck`);
    } catch {
      response = null;
    }

    if (!response || response.status !== 200) {
      return next({ name: "500" });
    }

    await Promise.all([
      store.dispatch("user/authenticate"),
      store.dispatch("online/getUsersOnlineCount")
    ]);

    await Promise.all([
      store.dispatch("giveaway/getGiveaways"),
      store.dispatch("jackpot/getJackpot")
    ]);

    store.dispatch("time/startUpdateInterval");

    if (store.getters["user/isAuthenticated"]) {
      store.dispatch("online/startSetUserOnlineStatusInterval");
      await store.dispatch("online/setUserOnlineStatus");

    }

    await store.dispatch("chat/getMessages", { chatId: "public" });

    next();
  }
};
</script>

