  background-color: #0f1318 !important;
  padding-left: 10em;
  position: relative;
  overflow: visible !important;
  max-width: 850px !important;
  @media (max-width: $breakpoint-sm-max) {
    & {
      padding-left: 0 !important;
      &:before {
        display: none;
      }
