  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: -5em;
    bottom: 0;
    width: 21.43em;
    background: url(/img/authorization-bg.png) center center / 100% 100%
      no-repeat;
  }
  .q-btn {
    background: linear-gradient(to bottom, #1c75fa, #1862d2);
    border-radius: 0.1em;
