<template>
  <div id="hilo-deck" :style="{ width: width + 'px', height: height + 'px' }">
    <div class="cards-deck">
      <HiloCard
        number="2-red"
        :width="width * (1 - i * 0.1)"
        :height="height"
        class="card"
        v-for="(n, i) in deckCount"
        :key="i"
      ></HiloCard>
    </div>

    <div
      class="cards-center"
      :style="{
        width: width + 'px',
        height: height + 'px'
      }"
    >
      <!--Основная анимация-->
      <HiloCard
        ref="card"
        :width="width"
        :height="height"
        :number="currentNumber"
        class="main-card reversed"
      ></HiloCard>
    </div>
  </div>
</template>

<script>
import HiloCard from "./HiloCard";

export default {
  name: "HiloDeck",
  components: {
    HiloCard
  },
  props: {
    width: {
      type: Number,
      default: 320
    },
    height: {
      type: Number,
      default: 380
    },
    deckCount: {
      type: Number,
      default: 3
    },
    number: {
      type: String
    },
    previousNumber: {
      type: String
    },
    startedAt: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      isMounted: false,
      inAnimation: false,
      currentNumber: "joker"
    };
  },
  created() {
    this.currentNumber = this.number || this.previousNumber;
  },
  mounted() {
    this.isMounted = true;
  },
  watch: {
    isMounted(newValue) {
      if (newValue && this.number) {
        this.animate({ number: this.number });
      }
    },
    number: {
      handler: function(newValue) {
        if (newValue) {
          this.animate({ number: newValue });
        }
      }
    }
  },
  methods: {
    async animate({
      startedAt = this.startedAt,
      number = "2-red",
      duration = 2500
    } = {}) {
      const targets = this.$refs.card;
      this.inAnimation = true;
      duration = duration / 2;

      this.currentNumber = this.previousNumber;
      await targets.disappear({ duration, startedAt });

      this.currentNumber = number;
      await targets.appear({ duration, startedAt: startedAt + duration });

      this.inAnimation = false;
    }
  },
  computed: {
    color() {
      return String(this.previousNumber).split("-")[1] || "joker";
    }
  }
};
</script>

<style lang="scss">
#hilo-deck {
  position: relative;

  .cards-center {
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;

    .main-card {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      box-shadow: none;
      will-change: transform;
    }

    .card-progress {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 1;
      transform: translate(-50%, -50%);
      -webkit-transform: translate(-50%, -50%);

      &.color-black {
        .circle {
          border-color: black !important;
        }
      }

      &.color-joker {
        .circle {
          border-color: darken(#0062f4, 10%) !important;
        }
      }

      &.color-red {
        .circle {
          border-color: #dd4e46 !important;
        }
      }

      .countdown-progress_bg,
      .countdown-progress > .circle {
        border-width: 2px !important;
      }

      .countdown-progress_bg {
        border-color: darken(white, 10%);
      }
    }
  }

  .cards-deck {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 0;

    .card {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }

    @for $i from 1 through 20 {
      .card:nth-child(#{$i}) {
        z-index: 9999 - $i;
        top: ($i - 1) * 1em;
      }
    }
  }
}
</style>
