<template>
  <div
    id="roulette"
    class="q-pt-lg q-pb-lg q-px-xs-md q-px-md-none"
    style="overflow-x: hidden;"
  >
    <div class="wrapper-secondary game-title">
      <GameTitle
        :game="roulette"
        :countdown="countdown"
        :sumOfBets="sumOfBets"
        gameName="Рулетка"
        :gameIcon="rouletteIcon"
      >
      </GameTitle>
    </div>
    <div class="roulette__inner wrapper-secondary">
      <div class="row">
        <div
          class="col-md-4 col-xs-6 q-pr-md-xl order-xs-last order-md-none flex items-center"
          :style="{ paddingLeft: $q.screen.gt.sm ? '10em' : '', zIndex: 1 }"
          v-if="$q.screen.gt.sm"
        >
          <div
            class="flex q-pa-lg bg-bet-maker-roulette rounded-borders-2x column full-width"
          >
            <div
              class="flex q-py-sm items-center"
              v-for="value in sumOfBetsByType"
              :key="value.type"
            >
              <div
                class="roulette-color q-mr-md"
                :class="`color-${value.type}`"
              ></div>
              <div
                class="text-weight-medium text-highlight text-big flex items-baseline justify-center"
              >
                <q-icon name="fas fa-sack-dollar" class="q-mr-sm"> </q-icon>
                <span class="text-weight-bold">
                  {{ value.sum }}
                </span>
              </div>
            </div>
          </div>
        </div>
        <div
          class="col-md-4 col-xs-12 items-stretch flex"
          style="padding-top: 1.5em"
        >
          <div
            class="roulette__wrapper full-width"
            :style="{ maxHeight: wheelSize + 'px' }"
          >
            <Wheel :size="wheelSize" :numbersCount="15" ref="wheel"></Wheel>
            <div
              class="absolute text-center roulette__history-title"
              :style="{
                width: (wheelSize * 135) / 280 + 'px',
                height: (wheelSize * 50) / 280 + 'px'
              }"
            >
              История побед
            </div>
            <div
              class="roulette__history bg-bet-maker-roulette rounded-borders-2x"
              :style="{
                width: (wheelSize * 135) / 280 + 'px',
                height: (wheelSize * 50) / 280 + 'px'
              }"
            >
              <VirtualHistory
                :items="roulette.history"
                :virtual-scroll-item-size="63"
                :virtual-scroll-slice-size="7"
                class="history__inner"
                :offset="500"
                @load="loadMoreHistory"
              >
                <template v-slot="{ item }">
                  <div class="history__item-container">
                    <div
                      class="history__item roulette-color"
                      :key="item._id"
                      :class="`color-${item.type}`"
                    >
                      {{ item.number }}
                    </div>
                  </div>
                </template>
              </VirtualHistory>
            </div>
            <q-resize-observer @resize="updateWheelSize" />
          </div>
        </div>
        <div
          class="col-md-4 col-xs-12 q-pl-md-xl roulette__bet-maker flex items-center q-pt-md q-pt-md-none"
          :style="{ paddingRight: $q.screen.gt.sm ? '8em' : '' }"
        >
          <BetMaker
                  ref="betMaker"
            v-model="betMakerProps"
            class="full-width"
            game-type="roulette"
            @bet="createRouletteBet"
          ></BetMaker>
        </div>
      </div>
      <div class="row roulette__bets q-pt-xl">
        <div
          class="col-md make-bet-container relative-position col-xs-12 q-px-md-md"
        >
          <BetList
            class="roulette__bets-red"
            :value="betsByType.red"
            gameType="roulette"
          />

          <q-btn
            :disable="!betAllowed"
            @click="
              createRouletteBet({
                type: 'red',
                betAmount: betMakerProps.betAmount
              })
            "
            label="Сделать ставку"
            size="md"
            no-caps
            flat
            class="make-bet-btn "
          />
        </div>
        <div
          class="col-md make-bet-container col-xs-12 q-px-md-md q-pt-lg q-pt-md-none"
        >
          <BetList
            class="roulette__bets-green"
            :value="betsByType.green"
            gameType="roulette"
          >
          </BetList>
          <q-btn
            :disable="!betAllowed"
            @click="
              createRouletteBet({
                type: 'green',
                betAmount: betMakerProps.betAmount
              })
            "
            label="Сделать ставку"
            size="md"
            no-caps
            flat
            class="make-bet-btn q-mt-lg q-mt-md-none"
          />
        </div>
        <div
          class="col-md make-bet-container col-xs-12 q-px-md-md q-pt-lg q-pt-md-none"
        >
          <BetList
            class="roulette__bets-black"
            :value="betsByType.black"
            gameType="roulette"
          >
          </BetList>
          <q-btn
            :disable="!betAllowed"
            @click="
              createRouletteBet({
                type: 'black',
                betAmount: betMakerProps.betAmount
              })
            "
            label="Сделать ставку"
            size="md"
            no-caps
            flat
            class="make-bet-btn q-mt-lg q-mt-md-none"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import Wheel from "./Wheel";
import store from "@/store";
import { mapState, mapGetters } from "vuex";
import BetMaker from "../../components/BetMaker";
import BetList from "../../components/BetList";
import rouletteIcon from "@/../public/img/game-icons/roulette-icon.svg";
import VirtualHistory from "../../components/VirtualHistory";
import GameTitle from "@/components/GameTitle";
import { rouletteProcess, betSound } from "@/howler";

export default {
  name: "Roulette",
  meta: {
    title: "Рулетка"
  },
  beforeDestroy() {
    rouletteProcess.stop();
  },
  data() {
    return {
      rouletteIcon,
      wheelSize: 0,
      isMounted: false,
      countdown: 0,
      loadingHistory: false,
      lastBetsCount: 0,
      betMakerProps: {
        type: this.$store.state.roulette.rouletteBetType,
        betAmount: 100
      }
    };
  },
  components: {
    Wheel,
    BetMaker,
    BetList,
    VirtualHistory,
    GameTitle
  },
  watch: {
    "roulette.bets": {
      handler(newValue = []) {
        if (this.lastBetsCount < newValue.length) {
          betSound.play();
        }
        this.lastBetsCount = newValue.length;
      }
    },
    "roulette.startedAt": {
      immediate: true,
      handler: function(newValue) {
        if (newValue) {
          let result = this.roulette.startedAt - this.now;
          this.countdown = result < 0 ? 0 : result;
        }
      }
    },
    isMounted() {
      let lastGame = _.first(this.roulette.history);

      if (typeof this.roulette.number === "number") {
        this.$refs.wheel.rotate({
          number: this.roulette.number,
          endedAt: this.roulette.endedAt,
          hash: this.roulette._id
        });
      } else {
        this.$refs.wheel.rotate({
          number: lastGame.number,
          hash: lastGame._id
        });
      }
    },
    ["roulette.number"](newValue) {
      if (newValue !== null && this.isMounted) {
        this.$refs.wheel.rotate({
          number: this.roulette.number,
          endedAt: this.roulette.endedAt,
          hash: this.roulette._id
        });
      }
    }
  },
  async mounted() {
    this.isMounted = true;
  },
  methods: {
    async loadMoreHistory({ done, stop }) {
      let history = await this.$store.dispatch("roulette/loadMoreHistory", {
        afterId: _.last(this.roulette.history)._id
      });

      if (!history.length) {
        stop();
      }

      done();
    },
    updateWheelSize({ width }) {
      this.wheelSize = width;
    },
    async createRouletteBet({ type, betAmount, betUserItemId }) {
      await this.$apollo.mutate({
        mutation: require("@/graphql/CreateRouletteBet.gql"),
        variables: { betAmount, type, betUserItemId }
      });
    }
  },
  beforeRouteLeave(to, from, next){
    //store.dispatch("time/stopUpdateInterval");
    store.dispatch("roulette/reset");
    next();
  },
  async beforeRouteEnter(to, from, next) {
    //store.dispatch("time/startUpdateInterval");

    if (!store.state.roulette._id) {
      await store.dispatch("roulette/getGame");
    }

    next();
  },
  computed: {
    betAllowed(){
      return this.isMounted ? this.$refs.betMaker.betAllowed && this.$refs.betMaker.rouletteBetAllowed : false;
    },
    ...mapState({
      user: state => state.user,
      roulette: state => state.roulette,
      now: state => state.time.now
    }),
    ...mapGetters({
      sumOfBets: "roulette/sumOfBets",
      sumOfBetsByType: "roulette/sumOfBetsByType",
      betsByType: "roulette/betsByType"
    })
  }
};
</script>
