<template>
  <div
    id="blackjack-deck"
    :style="{ width: `${height}px`, height: `${width}px` }"
  >
    <div
      class="blackjack-deck__card-container"
      v-for="(card, index) in 6"
      :key="index"
      :class="{ 'card-animate': index === 0 }"
      :style="index === 1 ? `z-index:3` : ``"
    >
      <BlackjackCard
        class="blackjack-deck__card"
        :class="{ 'no-shadow': index === 0 }"
        :width="index === 0 ? animatedWidth : width"
        :style="index > 1 ? `margin-right:${index - 1}px` : ``"
      >
      </BlackjackCard>
    </div>
  </div>
</template>

<script>
import BlackjackCard from "./BlackjackCard";
import anime from "animejs";

export default {
  name: "BlackjackDeck",
  components: {
    BlackjackCard
  },
  props: {
    handCardWidth: {
      type: Number,
      default: 149
    },
    width: {
      type: Number,
      default: 149
    }
  },
  data() {
    return {
      animatedWidth: this.width
    };
  },
  computed: {
    height() {
      return this.width * (207 / 149);
    }
  },
  methods: {
    animate({ x, y, duration = 350 }) {
      let rect = this.$el.getBoundingClientRect();
      let targets = this.$el.querySelector(".card-animate");
      anime({
        targets: this,
        animatedWidth: this.handCardWidth,
        round: 1,
        easing: "easeOutQuad",
        duration: duration,
        complete: () => {
          this.animatedWidth = this.width;
        }
      });
      let animation = anime({
        targets,
        duration,
        easing: "easeOutQuad",
        width: this.handCardWidth,
        top: [
          { value: rect.top, duration: 0 },
          { value: y, duration }
        ],
        left: [
          { value: rect.left, duration: 0 },
          { value: x, duration }
        ],
        rotate: [
          { value: -90, duration: 0 },
          { value: 0, duration }
        ],
        translateX: [
          { value: "-100%", duration: 0 },
          { value: 0, duration }
        ],
        complete() {
          targets.style.transform = "rotate(-90deg) translateX(-100%)";
          targets.style.top = null;
          targets.style.left = null;
          targets.style.width = null;
        }
      });
      return animation.finished;
    }
  }
};
</script>

<style lang="scss">
#blackjack-deck {
  .blackjack-deck__card-container {
    position: absolute;
    z-index: 2;
    transform: rotate(-90deg) translateX(-100%);
    transform-origin: left top;

    &.card-animate {
      z-index: 4;
      will-change: left, top, transform;
    }
  }
}
</style>
