import { apolloProvider } from "@/vue-apollo";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    usersCount: 0
  }),
  getters: {},
  mutations: {
    setUsersOnlineCount(state, count) {
      state.usersCount = count;
    }
  },
  actions: {
    startSetUserOnlineStatusInterval({ dispatch }) {
      setTimeout(() => {
        dispatch("setUserOnlineStatus");
        dispatch("startSetUserOnlineStatusInterval");
      }, 180000);
    },
    async setUserOnlineStatus() {
      return (
        await apolloClient.mutate({
          mutation: require("@/graphql/SetUserOnlineStatus.gql")
        })
      ).data.setUserOnlineStatus;
    },
    async getUsersOnlineCount({ commit, dispatch }) {
      const usersCount = (
        await apolloClient.query({
          query: require("@/graphql/GetUsersOnlineCount.gql")
        })
      ).data.getUsersOnlineCount;

      commit("setUsersOnlineCount", usersCount);
      dispatch("subscribeUsersOnlineUpdated");
      return usersCount;
    },
    subscribeUsersOnlineUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/UsersOnlineUpdated.gql")
      });

      observer.subscribe({
        next: data =>
          commit("setUsersOnlineCount", data.data.usersOnlineUpdated)
      });
    }
  }
};
