import { apolloProvider } from "@/vue-apollo";
import Vue from "vue";
import _ from "lodash";

const apolloClient = apolloProvider.defaultClient;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    number: undefined,
    salt: undefined,
    hash: undefined,
    active: undefined,
    createdAt: undefined,
    startedAt: undefined,
    endedAt: undefined,
    betAllowed: undefined,
    bets: [],
    history: []
  }),
  getters: {
    betAllowed(state, getters, rootState) {
      return rootState.time.now < state.startedAt;
    },
    currentMultiplier(state, getters, rootState) {
      const offset =
        rootState.time.now -
        (state.startedAt ? state.startedAt : rootState.time.now + 1);
      return state.number
        ? state.number
        : offset < 0
        ? 0
        : Number(Math.exp(offset / 10000).toFixed(2));
    },
    userBet(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"]
        ? state.bets.find(bet => bet.userId === rootState.user._id)
        : undefined;
    },
    isUserHaveBet(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"] ? !!getters.userBet : false;
    },
    isUserBetStopped(state, getters, rootState, rootGetters) {
      return rootGetters["user/isAuthenticated"]
        ? !!(getters.userBet
            ? (getters.userBet.crashBet.autoStop &&
                getters.userBet.crashBet.autoStop <
                  getters.currentMultiplier) ||
              getters.userBet.crashBet.stopped
            : false)
        : false;
    },
    sumOfBets(state) {
      return _.sumBy(state.bets, bet => bet.betAmount);
    }
  },
  mutations: {
    addBet(state, bet){
      state.bets.push(bet)
    },
    reset(state){
      state.history = state.history.slice(0,30);
    },
    updateBet(state, bet){
      let index = state.bets.findIndex((o)=>o._id === bet._id)
      Vue.set(state.bets, index, bet)
    },
    setHistory(state, history) {
      state.history = history;
    },
    addHistory(state, history) {
      Vue.set(state.history, state.history.length, history);
    },
    unshiftHistory(state, game) {
      if (
        state.history.length &&
        game.number &&
        game._id !== state.history[0]._id
      ) {
        Vue.set(state, "history", [
          { _id: game._id, number: game.number },
          ...state.history
        ]);
      }
    },
    updateValues(state, game) {
      if (game) {
        Object.keys(game).map(key => {
          if (typeof game[key] !== "undefined") {
            if(key === 'bets' && game[key] === null){
              return;
            }
            state[key] = game[key];
          }
        });
      }
    }
  },
  actions: {
    async loadMoreHistory({ commit }, { afterId }) {
      const histories = (
        await apolloClient.query({
          query: require("@/graphql/GetCrashHistory.gql"),
          variables: { afterId }
        })
      ).data.getCrashHistory;

      histories.map(history => {
        commit("addHistory", history);
      });

      return histories;
    },
    async getGame({ commit, dispatch }) {
      const data = await apolloClient.query({
        query: require("@/graphql/GetCrashGame.gql")
      });
      const game = data.data.getCrashGame;
      const history = data.data.getCrashHistory;
      commit("updateValues", game);
      commit("setHistory", history);
      if (game) dispatch("subscribeGameUpdated");
    },
    reset({commit}){
      commit('reset');
    },
    subscribeGameUpdated({ commit }) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/CrashUpdated.gql")
      });
      observer.subscribe({
        next: data => {
          commit("updateValues", data.data.crashUpdated);
          commit("unshiftHistory", data.data.crashUpdated);
        }
      });
      const observer1 = apolloClient.subscribe({
        query: require("@/graphql/CrashBetAdded.gql")
      });
      observer1.subscribe({
        next: data => {
          commit("addBet", data.data.crashBetAdded);
        }
      });
      const observer2 = apolloClient.subscribe({
        query: require("@/graphql/CrashBetUpdated.gql")
      });
      observer2.subscribe({
        next: data => {
          commit("updateBet", data.data.crashBetUpdated);
        }
      });
    }
  }
};
