export default {
  methods: {
    toggleDepositDialog(show) {
      this.$store.commit("dialog/toggleDepositDialog", show);
    },
    toggleJackpotDialog(show) {
      this.$store.commit("dialog/toggleJackpotDialog", show);
    },
    toggleChatBanDialog(show) {
      this.$store.commit("dialog/toggleChatBanDialog", show);
    },
    setChatBanDialogUser(user) {
      this.$store.commit("dialog/setChatBanDialogUser", user);
    },
    toggleFairControlDialog(show) {
      this.$store.commit("dialog/toggleFairControlDialog", show);
    },
    setFairControlDialogGame(game) {
      this.$store.commit("dialog/setFairControlDialogGame", game);
    },
    toggleBuySkinDialog(show) {
      this.$store.commit("dialog/toggleBuySkinDialog", show);
    },
    toggleGiveawayHistoryDialog(show, type = "hourly") {
      this.$store.commit("dialog/setGiveawayHistoryDialogType", type);
      this.$store.commit("dialog/toggleGiveawayHistoryDialog", show);
    }
  }
};
