import * as timesync from "timesync";

const ts = timesync.create({
  repeat: 5,
  delay: 1000,
  server: `${process.env.VUE_APP_API_ENDPOINT}/timesync`
});
let interval;

export default {
  namespaced: true,
  state: () => ({
    now: ts.now(),
    ts
  }),
  mutations: {
    update(state) {
      state.now = ts.now();
    }
  },
  actions: {
    startUpdateInterval({ state, dispatch }) {
      interval = setTimeout(() => {
        // commit("update");
        state.now = ts.now();
        dispatch("startUpdateInterval");
      }, 1000 / 10);
    },
    stopUpdateInterval(){
      clearTimeout(interval);
      interval = null;
    }
  }
};
