<template>
  <q-dialog v-model="show" persistent>
    <q-card class="app-dialog flex column">
      <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
        <q-space> </q-space>
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-card-section style="flex: 1" class="">
        <ApolloQuery
          :query="require('@/graphql/GetFirstDepositBonus.gql')"
          class="full-width full-height"
          v-if="isAuthenticated"
        >
          <template v-slot="{ result: { error, data, loading } }">
            <template v-if="data && data.getFirstDepositBonus">
              <div class="full-width text-center" style="padding-top: 57px">
                  <span class="text-h5 text-center text-weight-medium">
                    Добро пожаловать на CSGOTUNE!
                  </span>
                <p
                        class="text-grey text-font-montserrat q-my-md"
                        style="font-size: 14px"
                >
                  Благодарим Вас за регистрацию и дарим Вам специальный
                  промокод, активировать его можно, нажав "+" в правом меню!
                </p>
              </div>
              <div class="absolute absolute-bottom q-px-lg q-pb-md">
                <Promocode
                  :value="data.getFirstDepositBonus"
                  class="q-mb-md"
                  :no-expire-at="true"
                  style="height: 4.5em"
                ></Promocode>

                <countdown
                  :time="data.getFirstDepositBonus.expireAt - ts.now()"
                  class="text-weight-regular"
                  style="font-size: 1.5em;"
                  tag="div"
                  @end="show = false"
                >
                  <template slot-scope="props">
                    {{ padStart(props.minutes) }}:{{ padStart(props.seconds) }}
                  </template>
                </countdown>
                <div
                  class="text-amber-8 text-weight-bold text-uppercase"
                  style="font-size: 0.82em"
                >
                  Скоро предложение будет недоступно
                </div>
              </div>
            </template>
            <Loading v-else-if="loading"></Loading>
            <div v-else-if="error" class="text-negative">
              {{ error }}
            </div>
          </template>
        </ApolloQuery>
        <template v-else>
          <div class="full-width text-center " style="padding-top: 57px">
            <span class="text-h5 text-center text-weight-medium ">
              Не заинтересовало?
            </span>
            <p
              class="text-grey text-font-montserrat q-my-md"
              style="font-size: 14px"
            >
              У нас есть особое предложение для Вас, авторизуйтесь на нашем
              сайте сейчас и Вы получите интересный бонус для Вашего аккаунта!
              Поторопитесь, предложение ограниченно. Только для новых
              пользователей.
            </p>
            <q-btn
              class="q-mx-auto q-mt-xs"
              outline
              style="color: #3585ff;"
              padding="0.75em lg"
              no-caps
              @click="clickAuth"
            >
              <span class="text-white text-font-weigh-300 text-highlight">
                <q-icon :name="'fas fa-user'" left></q-icon>
                Авторизация
              </span>
            </q-btn>
          </div>

          <div class="absolute absolute-bottom q-px-lg q-pb-md">
            <countdown
              :time="120000"
              class="text-weight-regular"
              style="font-size: 1.5em;"
              tag="div"
              @end="show = false"
            >
              <template slot-scope="props">
                {{ padStart(props.minutes) }}:{{ padStart(props.seconds) }}
              </template>
            </countdown>
            <div
              class="text-amber-8 text-weight-bold text-uppercase"
              style="font-size: 0.82em"
            >
              Скоро предложение будет недоступно
            </div>
          </div>
        </template>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import Promocode from "./Promocode";
import Loading from "./Loading";
import { mapGetters, mapState } from "vuex";
import padStart from "../mixins/padStart";
import Chance from "chance";

const chance = new Chance();

export default {
  name: "FirstDepositBonusDialog",
  components: {
    Promocode,
    Loading
  },
  mixins: [padStart],
  data() {
    return {
      show: false,
      showed: false
    };
  },
  computed: {
    ...mapState({
      user: state => state.user,
      ts: state => state.time.ts
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  },
  methods: {
    promoteUser() {
      if (chance.bool() && this.$q.screen.gt.sm) {
        document.addEventListener("mouseout", e => {
          e = e ? e : window.event;
          let from = e.relatedTarget || e.toElement;
          if ((!from || from.nodeName == "HTML") && !this.showed) {
            this.showPromotion();
            ym(69160060, "reachGoal", "auth_modal_out");
          }
        });
      } else {
        setTimeout(() => {
          this.showPromotion();
          ym(69160060, "reachGoal", "auth_modal_time");
        }, 10000);
      }
    },
    showPromotion() {
      this.$q.cookies.set("authPromotion", "true", {
        expires: new Date(Date.now() + 360 * 24 * 60 * 60 * 1000)
      });
      this.show = true;
      this.showed = true;
    },
    clickAuth() {
      this.$q.cookies.set("authPromotionClick", "true", {
        expires: new Date(Date.now() + 360 * 24 * 60 * 60 * 1000)
      });
      this.$root.$emit("authorizationDialog");
    }
  },
  mounted() {
    if (
      this.isAuthenticated &&
      this.ts.now() - this.user.createdAt < 5 * 60 * 1000 &&
      (this.user.partnerId || this.$q.cookies.get("authPromotionClick"))
    ) {
      this.show = true;
    }
    if (!this.isAuthenticated && !this.$q.cookies.get("authPromotion")) {
      this.promoteUser();
    }
  }
};
</script>

<style scoped lang="scss">
.app-dialog {
  height: 505px - 76px;
  width: 363px;
  overflow: visible;
  position: relative;
  &:before {
    content: "";
    background: url("../assets/img/first-deposit-bonus-bg.png") center center /
      363px 505px no-repeat;
    position: absolute;
    top: -76px;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
</style>
