import { Howl, Howler } from "howler";
import Chance from 'chance'

const chance = new Chance();

export function pickone(arr) {
  return chance.pickone(arr)
}

export function pickoneByHash(arr, hash) {
  let chance = new Chance(hash);
  return chance.pickone(arr)
}

export function stopAll(arr) {
  return arr.map((a)=>a.stop());
}

export function mute() {
  return Howler.mute(true);
}
export function unmute() {
  return Howler.mute(false);
}

export const volume = 0.3;
Howler.volume(volume);

