<template>
  <div
    id="jackpot"
    :class="{ 'cursor-pointer': !!lastJackpot }"
    @click="toggleHistory"
  >
    <q-tooltip
      content-style="font-size: 1em"
      content-class="bg-modal"
      v-if="lastJackpot"
    >
      Нажмите для просмотра последних победителей
    </q-tooltip>

    <div class="flex items-center column justify-between full-width">
      <span class="text-grey text-small" v-if="$q.screen.gt.sm">
        Текущий джекпот:
      </span>
      <AnimatedNumber
        tag="div"
        class="flex items-center text-big"
        content-class="text-weight-medium text-highlight"
        :filter="$options.filters.coinsWithoutShort"
        :number="jackpot.sumOfJackpot"
      >
        <template v-slot:prepend>
          <q-icon
            :name="$q.screen.lt.md ? 'fas fa-trophy' : 'fas fa-sack-dollar'"
            class="text-highlight q-mr-md-xs q-mr-xs-sm"
          />
        </template>
      </AnimatedNumber>
    </div>

    <q-dialog v-model="historyOpened" v-if="lastJackpot">
      <q-card class="jackpot-dialog">
        <div class="jackpot-dialog__title">
          <q-card-section
            class="row items-center q-pb-none q-pt-none q-px-none"
          >
            <q-btn
              to="/faq?id=jackpot"
              flat
              round
              icon="fas fa-info-circle"
              color="white"
              v-close-popup
            >
              <q-tooltip
                content-class="bg-modal"
                content-style="font-size: 1em"
                anchor="top middle"
                self="center middle"
              >
                Что это?
              </q-tooltip></q-btn
            >
            <q-space />
            <q-btn icon="close" flat round dense v-close-popup />
          </q-card-section>
          <div class="flex column items-center q-pb-xl q-pt-md">
            <h5 class="text-weight-medium q-ma-none q-mb-sm">
              ДЖЕКПОТ
            </h5>
            <span class="text-grey text-small">
              Последний срыв джекпота
              {{ lastJackpot.updatedAt | moment("from", "now") }}:
            </span>
          </div>
        </div>

        <q-card-section class="q-py-xl flex items-center justify-center column">
          <div
            class="flex jackpot-sum column items-center q-px-lg q-pb-lg q-pt-xs q-mb-sm relative-position"
          >
            <UserName
              :value="lastJackpot.winnerUser"
              class="absolute text-center full-width"
              style="font-size: 0.4em;top:-1em;z-index: 2;"
            />
            <router-link
              v-close-popup
              tag="div"
              :to="`/profile/${lastJackpot.winnerUser._id}`"
              class="jackpot-avatar q-pt-xs cursor-pointer relative-position"
            >
              <!--<q-icon
                name="fas fa-sack-dollar"
                size="xs"
                class="text-highlight absolute absolute-bottom"
                style="z-index: 2; left:50%;transform: translateX(-50%)"
              />-->
              <q-avatar square rounded size="2em">
                <q-img
                    basic
                  :src="lastJackpot.winnerUser.avatar"
                  placeholder-src="/img/avatar-fallback.png"
                  width="100%"
                  height="100%"
                />
              </q-avatar>
            </router-link>
            <div class="flex items-center">
              <span class="text-weight-medium text-highlight">
                {{ lastJackpot.sumOfJackpot | coinsWithoutShort }}
              </span>
            </div>
          </div>
          <q-btn
            v-if="jackpot.history.length > 1"
            label="Предыдущие победители"
            flat
            color="grey"
            class="absolute"
            style="bottom: 0.5em; left: 50%; transform: translateX(-50%)"
          >
            <q-popup-proxy
              content-class="bg-transparent"
              anchor="center middle"
              self="center middle"
            >
              <q-banner dark class="q-pa-none rounded-borders">
                <div class="jackpot-history rounded-borders">
                  <q-scroll-area
                    class="jackpot-history__scroll full-width"
                    :thumb-style="scrollThumbStyle"
                    :bar-style="scrollBarStyle"
                  >
                    <q-infinite-scroll
                      @load="onScrollReachedBottom"
                      :offset="0"
                      :debounce="0"
                      :disable="!hasMore"
                      scroll-target=".jackpot-history__scroll > .scroll"
                    >
                      <div
                        class="flex justify-between items-center full-width no-wrap q-px-md q-py-md jackpot-history__item"
                        v-for="history in historyWithoutLast"
                        :key="history._id"
                      >
                        <div class="jackpot-avatar without-bg-light q-mr-md">
                          <q-avatar square rounded size="3em">
                            <q-img
                                basic
                              :src="history.winnerUser.avatar"
                              placeholder-src="/img/avatar-fallback.png"
                              width="100%"
                              height="100%"
                            />
                          </q-avatar>
                        </div>
                        <div
                          class="flex no-wrap"
                          style="flex: 0 1 auto; overflow: hidden"
                        >
                          <div
                            class="flex justify-center column overflow-hidden q-pr-md"
                          >
                            <UserName
                              :value="history.winnerUser"
                              class="text-small"
                            >
                            </UserName>
                            <span class="text-small text-grey full-width">
                              {{ history.updatedAt | moment("from", "now") }}
                            </span>
                          </div>
                        </div>
                        <div
                          class="flex text-small column  items-end justify-center no-wrap"
                          style="flex: 1 0 auto;"
                        >
                          <span class="text-small text-grey">Выиграл:</span>
                          <div
                            class="flex items-center no-wrap"
                            style="font-size: 1.05em"
                          >
                            <q-icon
                              name="fas fa-sack-dollar"
                              class="text-highlight q-mr-sm"
                            />
                            <span class="text-weight-medium text-highlight">
                              {{ history.sumOfJackpot | coins }}
                            </span>
                          </div>
                        </div>
                      </div>
                      <template v-slot:loading>
                        <div class="row justify-center q-my-md">
                          <q-spinner color="primary" size="xl" />
                        </div>
                      </template>
                    </q-infinite-scroll>
                  </q-scroll-area>
                </div>
              </q-banner>
            </q-popup-proxy>
          </q-btn>
        </q-card-section>
      </q-card>
    </q-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import AnimatedNumber from "./AnimatedNumber";
import UserName from "./UserName";
import coins from "../mixins/coins";
import scrollBarStyle from "../mixins/scrollBarStyle";

export default {
  name: "Jackpot",
  components: {
    UserName,
    AnimatedNumber
  },
  mixins: [scrollBarStyle, coins],
  data() {
    return {
      hasMore: true,
      historyOpened: false
    };
  },
  created() {},
  methods: {
    toggleHistory() {
      this.historyOpened = !this.historyOpened;
    },
    async onScrollReachedBottom(index, done) {
      let history = await this.$store.dispatch("jackpot/loadMoreHistory");
      if (!history.length) {
        this.hasMore = false;
      }
      done();
    }
  },
  computed: {
    lastJackpot() {
      return this.jackpot.history.length ? this.jackpot.history[0] : null;
    },
    historyWithoutLast() {
      return this.jackpot.history.slice(1);
    },
    ...mapState({
      jackpot: state => state.jackpot
    })
  }
};
</script>

<style lang="scss">
.jackpot-history {
  overflow: hidden;
  min-width: 19em !important;
  background-color: rgba(#202229, 0.5);

  .jackpot-history__scroll {
    height: 4.95em;
  }

  .jackpot-history__item:nth-child(odd) {
    background: rgba(#282930, 0.5);
  }

  .jackpot-history__item {
    height: 4.95em;
    border-bottom: 0.1em solid rgba(255, 255, 255, 0.075) !important;
    &:first-child {
      border-top: 0.1em solid rgba(255, 255, 255, 0.075) !important;
    }
  }
}
#jackpot {
}
</style>
