import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "../components/Layout.vue";
import Home from "../views/Home.vue";
import Crash from "../games/Crash";
import Hilo from "../games/Hilo";
import Dice from "../games/Dice";
import Roulette from "../games/Roulette";
import RoyalBattle from "../games/RoyalBattle";
import Blackjack from "../games/Blackjack";
import NotFound from "../views/NotFound.vue";
import InternalError from "../views/InternalError.vue";
import Profile from "../views/Profile.vue";
import Partner from "../views/Partner.vue";
import BattlePass from "../views/BattlePass.vue";
import SocialNetworkingBonus from "../views/SocialNetworkingBonus.vue";
import FAQ from "../views/FAQ";
import PrivacyPolicy from "../views/PrivacyPolicy";
import TermsOfService from "../views/TermsOfService";
import InvoicesStats from "../views/InvoicesStats";
import PromocodesStats from "../views/PromocodesStats";

Vue.use(VueRouter);

const routes = [
  {
    path: "/500",
    name: "500",
    component: InternalError
  },
  {
    path: "/p/:partnerId",
    name: "Partner",
    component: Partner
  },
  {
    path: "/",
    component: Layout,
    children: [
      {
        path: "/terms-of-service",
        name: "TermsOfService",
        component: TermsOfService
      },
      {
        path: "/faq",
        name: "FAQ",
        component: FAQ
      },
      {
        path: "/privacy-policy",
        name: "PrivacyPolicy",
        component: PrivacyPolicy
      },
      {
        path: "/",
        name: "Home",
        component: Home
      },
      {
        path: "/hilo",
        name: "Hilo",
        component: Hilo
      },
      {
        path: "/blackjack",
        name: "Blackjack",
        component: Blackjack
      },
      {
        path: "/dice",
        name: "Dice",
        component: Dice
      },
      {
        path: "/crash",
        name: "Crash",
        component: Crash
      },
      {
        path: "/profile/:userId",
        name: "Profile",
        component: Profile
      },
      {
        path: "/social-networking-bonus",
        name: "SocialNetworkingBonus",
        component: SocialNetworkingBonus
      },
      {
        path: "/roulette",
        name: "Roulette",
        component: Roulette
      },
      {
        path: "/royalbattle",
        name: "RoyalBattle",
        component: RoyalBattle
      },
      {
        path: "/battlepass",
        name: "BattlePass",
        component: BattlePass
      },
      {
        path: "/invoices-stats",
        name: "InvoicesStats",
        component: InvoicesStats
      },
      {
        path: "/promocodes-stats",
        name: "PromocodesStats",
        component: PromocodesStats
      },
      {
        path: "/_=_",
        redirect: "/"
      },
      {
        path: "*",
        component: NotFound,
        name: "NotFound"
      }
    ]
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  mode: "hash",
  routes
});

export default router;
