<script>
import {mapGetters, mapState} from "vuex";
import BetMakerBtn from "./BetMakerBtn";

const minBetAmount = 10;
const maxBetAmount = 100000;

export default {
  name: "BetMaker",
  props: ["value", "gameType"],
  data: () => ({
    valueCopy: {}
  }),
  components: {
    BetMakerBtn
  },
  created() {
    this.valueCopy = this.value;
    this.setBetAmount();
  },
  watch: {
    valueCopy: {
      deep: true,
      handler(newValue) {
        this.$emit("input", newValue);
      }
    }
  },
  methods: {
    emitBetCreation(item) {
      if (item) {
        this.$emit(
            "bet",
            Object.assign({}, this.valueCopy, {
              betAmount: undefined,
              betUserItemId: item._id
            })
        );
      } else {
        this.$emit("bet", this.valueCopy);
      }
    },
    emitBetStop() {
      this.$emit("stop");
    },
    incrementBetAmount(x) {
      this.valueCopy.betAmount += x;
      this.setBetAmount();
    },
    multiplyBetAmount(x) {
      if (this.valueCopy.betAmount === 0 && x > 1) {
        this.valueCopy.betAmount = 1;
      } else {
        this.valueCopy.betAmount = Math.floor(x * this.valueCopy.betAmount);
      }

      this.setBetAmount();
    },
    setRouletteType(x = this.valueCopy.type) {
      this.valueCopy.type = x;
      this.$store.commit("roulette/setRouletteBetType", x);
    },
    setAutoStop(x = this.valueCopy.autoStop) {
      this.valueCopy.autoStop =
          x > 0 && x <= 1
              ? 0
              : x > 10000
              ? 10000
              : Number(Number(String(x).replace(/,/g, ".")).toFixed(2)) || 0;
    },
    setBetAmount(x = this.valueCopy.betAmount) {
      this.valueCopy.betAmount = x >= 0 ? Math.floor(x) : 0;

      if (this.isAuthenticated) {
        this.valueCopy.betAmount =
            this.valueCopy.betAmount > this.user.balance
                ? this.user.balance
                : this.valueCopy.betAmount;
      } else {
        this.valueCopy.betAmount = 0;
      }

      this.valueCopy.betAmount = Math.min(
          this.valueCopy.betAmount,
          maxBetAmount
      );
    },
    onAutoStopInputChange() {
      this.setAutoStop();
    },
    onBetAmountInputChange() {
      this.setBetAmount();
    }
  },
  computed: {
    betAllowed() {
      return (
          this.valueCopy.betAmount >= minBetAmount &&
          this.valueCopy.betAmount <= maxBetAmount
      );
    },
    ...mapState({
      user: state => state.user,
      crash: state => state.crash,
      blackjack: state => state.blackjack
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated",
      isCrashUserBetStopped: "crash/isUserBetStopped",
      isCrashUserHaveBet: "crash/isUserHaveBet",
      crashBetAllowed: "crash/betAllowed",
      hiloBetAllowed: "hilo/betAllowed",
      rouletteBetAllowed: "roulette/betAllowed",
      royalBattleBetAllowed: "royalBattle/betAllowed",
      currentCrashMultiplier: "crash/currentMultiplier",
      crashUserBet: "crash/userBet"
    })
  }
};
</script>

