<script>
import _ from "lodash";
import Chance from "chance";
import { mapGetters, mapState } from "vuex";
import Tape from "@/components/Tape";
import padStart from "@/mixins/padStart";
import steamItem from "@/mixins/steamItem";
import UserName from "@/components/UserName";
import color from "../mixins/color";
import scrollBarStyle from "../mixins/scrollBarStyle";
import dialogs from "../mixins/dialogs";

export default {
  name: "Giveaway",
  props: ["value"],
  components: { UserName, Tape },
  mixins: [padStart, steamItem, color, scrollBarStyle, dialogs],
  data: () => ({
    chance: null,
    countdown: 0,
    avatarWidth: 50,
    tapeElements: [],
    isMounted: false,
    inAnimation: false
  }),
  watch: {
    isMounted(newValue) {
      if (newValue && this.value["winnerUserId"]) {
        this.animate();
      }
    },
    "value.winnerUserId": {
      handler(newValue) {
        if (this.isMounted && newValue) {
          this.animate();
        }
      }
    }
  },
  methods: {
    async animate() {
      this.generateTapeElements({
        elementsCount: 100,
        winnerElementIndex: 87
      });

      this.inAnimation = true;

      await this.$nextTick();
      await this.$refs.tape.animate({
        endedAt: this.value.endedAt,
        animateToElementWithIndex: 87
      });

      this.inAnimation = false;
    },
    generateTapeElements({ elementsCount = 100, winnerElementIndex = 87 }) {
      let result = [];

      if (this.value["winnerUserId"]) {
        result = _.fill(new Array(elementsCount), null).map(() =>
          this.chance.pickone(this.value.users)
        );
        result[winnerElementIndex] = this.value["winnerUser"];
      }

      this.tapeElements = result;
    }
  },
  async mounted() {
    this.isMounted = true;
  },
  created() {
    this.chance = new Chance(this.value._id);
    this.inAnimation = !!this.value["winnerUserId"];
    this.countdown = this.value.endedAt - Date.now();
    this.countdown = this.countdown > 0 ? this.countdown : 0;
  },
  computed: {
    splittedItemName() {
      return this.splitMarketHashName(this.value.item._id);
    },
    ...mapState({
      user: state => state.user,
      time: state => state.time.now,
      ts: state => state.time.ts
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    }),
    countdownPercentage() {
      const total = this.value.endedAt - this.value.createdAt;
      const k = this.time - this.value.createdAt;
      const result = k / total;
      return (result > 1 ? 1 : result) * 100;
    }
  }
};
</script>

