<template>
  <q-dialog v-model="fairControlDialog" @hide="onHide">
    <q-card class="app-dialog fair-control-dialog" v-if="fairControlDialog">
      <q-card-section class="row items-center q-pb-none q-pt-none q-px-none">
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>

      <q-card-section class="q-pt-none">
        <div class="full-width text-center q-pb-xs">
          <q-tabs
            v-model="tab"
            class="q-mb-md"
            indicator-color="primary"
            dense
            align="justify"
          >
            <q-tab
              class="text-grey"
              name="fairControl"
              no-caps
              label="Честная игра"
            />
            <q-tab
              class="text-grey"
              no-caps
              name="howItWorks"
              label="Как это работает?"
            />
          </q-tabs>
        </div>
        <transition name="fade" mode="out-in" :duration="500">
          <div key="fairControl" v-if="tab === 'fairControl'">
            <div class="text-grey text-small q-mb-sm">
              Хэш раунда
            </div>
            <q-input
              readonly
              class="q-mb-md"
              :value="fairControlDialogGame.hash || 'скрыто'"
              bg-color="deposit-input"
              label-color="white"
              input-style="color: white; padding-left: 0.5em; font-size: 1.2em; text-overflow: ellipsis"
              outlined
            >
              <template v-slot:append>
                <q-btn
                  flat
                  size="md"
                  color="grey"
                  :disable="fairControlDialogGame.hash === null"
                  class="copy-btn cursor-pointer "
                  :label="
                    copySuccess === 'hash' ? 'Скопировано' : 'Скопировать'
                  "
                  @click="copy('hash')"
                >
                </q-btn>
              </template>
            </q-input>
            <div class="text-grey text-small q-mb-sm">
              Соль раунда
            </div>
            <q-input
              readonly
              class="q-mb-md"
              :value="fairControlDialogGame.salt || 'скрыто'"
              bg-color="deposit-input"
              label-color="white"
              input-style="color: white; padding-left: 0.5em; font-size: 1.2em;text-overflow: ellipsis"
              outlined
            >
              <template v-slot:append>
                <q-btn
                  flat
                  size="md"
                  color="grey"
                  :disable="fairControlDialogGame.salt === null"
                  class="copy-btn cursor-pointer "
                  :label="
                    copySuccess === 'salt' ? 'Скопировано' : 'Скопировать'
                  "
                  @click="copy('salt')"
                >
                </q-btn>
              </template>
            </q-input>
            <div class="text-grey text-small q-mb-sm">
              Число раунда
            </div>
            <q-input
              readonly
              class="q-mb-md"
              :value="fairControlDialogGame.number || 'скрыто'"
              bg-color="deposit-input"
              label-color="white"
              input-style="color: white; padding-left: 0.5em; font-size: 1.2em;text-overflow: ellipsis"
              outlined
            >
              <template v-slot:append>
                <q-btn
                  flat
                  size="md"
                  color="grey"
                  :disable="fairControlDialogGame.number === null"
                  class="copy-btn cursor-pointer "
                  :label="
                    copySuccess === 'number' ? 'Скопировано' : 'Скопировать'
                  "
                  @click="copy('number')"
                >
                </q-btn>
              </template>
            </q-input>
            <div class="text-grey text-small q-mb-sm">
              Формула
            </div>
            <q-input
              readonly
              :value="'sha256(salt + \'.\' + number)'"
              bg-color="deposit-input"
              label-color="white"
              input-style="color: white; padding-left: 0.5em; font-size: 1.2em"
              outlined
            >
            </q-input>
            <div class="full-width text-right q-pt-md">
              <q-btn label="Закрыть" color="grey" v-close-popup flat> </q-btn>
              <q-btn
                label="Проверить"
                @click="check"
                :disable="fairControlDialogGame.number === null"
                flat
                color="primary"
              >
              </q-btn>
            </div>
          </div>
          <div
            key="howItWorks"
            v-if="tab === 'howItWorks'"
            class="text-justify  full-width"
            style="font-size: 0.95em; word-break: break-all; hyphens: auto; -moz-hyphens: auto;-webkit-hyphens: auto;-ms-hyphens: auto;"
          >
            В начале каждого раунда сервис генерирует случайное число и соль,
            которые и являются результатом раунда, и шифрует их через алгоритм
            sha256. Результат шифрования виден в начале раунда
            (sha256(соль.число)).
            <br /><br />
            Пример: В начале раунда было зашифровано число 14 и соль
            98b12dc6b2ae37af1e9d214a02d592f01f945f5eaa12bb8432d4b30b1c79f564.
            Применив функцию sha256(соль.число) получим
            5c58560b7fa8a3b97054fe88398b6568f14d13621764b389c75b9e47e6b19975.
            Победным числом было число 14, а хэш раунда вы знали уже в начале
            раунда, что доказывает нашу честности при приеме ставок.
            <br /><br />
            Вы можете самостоятельно проверить правильность определения
            победного сектора. Вы увидите то же значение hash, что и в начале
            раунда. Это означает что результат игры не был подстроен.
          </div>
        </transition>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { mapState } from "vuex";
import { copyToClipboard } from "quasar";

export default {
  name: "FairControlDialog",
  components: {},
  data() {
    return {
      tab: "fairControl",
      fairControlDialog: false,
      copySuccess: ""
    };
  },
  watch: {
    fairControlDialogShow(newValue) {
      this.fairControlDialog = newValue;
    }
  },
  methods: {
    check() {
      window.open(
        `https://md5calc.com/hash/sha256/${this.fairControlDialogGame.salt}.${this.fairControlDialogGame.number}`,
        "_blank"
      );
    },
    async copy(key) {
      await copyToClipboard(this.fairControlDialogGame[key]);
      this.copySuccess = key;
      setTimeout(() => (this.copySuccess = ""), 3000);
    },
    onHide() {
      this.$store.commit("dialog/toggleFairControlDialog", false);
    }
  },
  computed: {
    ...mapState({
      user: state => state.user,
      fairControlDialogShow: state => state.dialog.fairControlDialogShow,
      fairControlDialogGame: state => state.dialog.fairControlDialogGame
    })
  }
};
</script>

<style lang="scss">
.fair-control-dialog {
  width: 27em !important;
  .q-tab--active,
  .q-tab:hover {
    color: white !important;
  }
  .q-tab__label {
    width: 1em;
    display: flex;
    justify-content: center;
    padding: 0.5em 0;
    font-size: 1em;
    font-weight: 400;
  }
}
</style>
