                      :name="historyGameTypeValue.icon || 'fas fa-list'"
                      color="white"
                    >
                    </q-icon>
                  </template>
                  <template v-slot:option="scope">
                    <q-item
                      v-bind="scope.itemProps"
                      v-on="scope.itemEvents"
                      class="bg-deposit-input"
                    >
                      <q-item-section
                        avatar
                        class="q-pr-none"
                        style="min-width: 2.5em;"
                      >
                        <q-icon :name="scope.opt.icon || 'fas fa-list'">
                        </q-icon>
                      </q-item-section>
                      <q-item-section class="">
                        <q-item-label v-html="scope.opt.name" />
                      </q-item-section>
                    </q-item>
                  </template>
                </q-select>
              </div>
            </div>

            <div
              class="flex justify-center order-first order-md-none"
              :class="{ 'full-width': $q.screen.lt.md }"
            >
              <div
                class=" text-uppercase flex justify-center history__title text-highlight q-mb-lg"
              >
                <h6 class="q-ma-none flex items-center">
                  <span>Моя история</span>
                  <q-icon
                    name="fas fa-times"
                    color="amber-8"
                    right
                    @click="changeTab('inventory')"
                    class="cursor-pointer"
                  ></q-icon>
                </h6>
              </div>
            </div>

            <div
              class="col-md col-xs-12 flex justify-between items-center q-pr-md-xl q-pl-md-lg q-py-md-md q-py-xs-lg "
            >
              <div class="flex text-highlight items-center no-wrap">
                <q-checkbox
                  size="xs"
                  color="primary"
                  keep-color
                  v-model="historySortByBetAmount"
                  :label="
                    $q.screen.gt.sm
                      ? 'Сортировать по сумме ставки'
                      : 'По сумме ставки'
                  "
                />
              </div>
              <div class="flex text-highlight items-center no-wrap">
                <q-checkbox
                  size="xs"
                  color="primary"
                  keep-color
                  v-model="historySortByProfitAmount"
                  :label="
                    $q.screen.gt.sm
                      ? 'Сортировать по сумме выигрыша'
                      : 'По сумме выигрыша'
                  "
                />
              </div>
            </div>
          </div>

          <div class="history__container q-px-md-xl full-width">
            <ApolloQuery
              :query="require('@/graphql/GetMyBets.gql')"
              fetch-policy="cache-and-network"
              :variables="{
                gameType: historyGameType,
                sortByBetAmount: historySortByBetAmount,
                sortByProfitAmount: historySortByProfitAmount
              }"
            >
              <template
                v-slot="{ result: { error, data, loading }, isLoading, query }"
              >
                <!-- Loading -->
                <Loading v-if="loading && !data"></Loading>
                <!-- Result -->
                <div
                  v-if="data && data.getMyBets"
                  class="partner__users relative-position"
                  style="height: 21em"
                >
                  <q-scroll-area
                    class="history__scroll full-width q-pr-md"
                    style="height: 100%"
                    ref="historyScroll"
                    :visible="false"
                    :thumb-style="scrollThumbStyle"
                    :bar-style="scrollBarStyle"
                    v-if="data.getMyBets.length"
                  >
                    <q-infinite-scroll
                      @load="(index, done) => loadMoreMyBets(query, done)"
                      :offset="10"
                      :debounce="0"
                      :disable="!historyHasMore"
                      :reverse="false"
                      scroll-target=".history__scroll > .scroll"
                    >
                      <div
                        class="row history__row flex justify-between items-center "
                        v-for="bet in data.getMyBets"
                        :key="bet._id"
                      >
                        <div class="col-md-3 col-xs-4  ">
                          <div class=" flex no-wrap items-center">
                            <q-icon
                              :name="gameLinksObject[bet.gameType].icon"
                              class="q-mr-sm"
                            >
                            </q-icon>
                            <router-link
                              tag="span"
                              :to="gameLinksObject[bet.gameType].href"
                              class="cursor-pointer"
                            >
                              {{ gameLinksObject[bet.gameType].name }}
                            </router-link>
                          </div>
                        </div>
                        <div
                          class="text-small col-3 text-center text-font-montserrat text-weight-medium"
                          v-if="$q.screen.gt.sm"
                        >
                          <span class="q-mr-md" style="color: #4d5666"
                            >Дата ставки:
                          </span>
                          <span style="color: #949fb0">{{
                            bet.createdAt | moment("DD.MM.YYYY HH:mm")
                          }}</span>
                        </div>
                        <div
                          class="text-small col-md-3 col-xs-4  text-center text-font-montserrat text-weight-medium"
                        >
                          <span class="q-mr-md" style="color: #4d5666"
                            >Сумма ставки:
                          </span>
                          <span style="color: #949fb0" v-if="!bet.betUserItem">
                            {{ bet.betAmount | coinsWithoutShort }}</span
                          >
                          <q-img
                              basic
                            v-if="bet.betUserItem"
                            :src="bet.betUserItem.item.image"
                            width="46px"
                            class="cursor-pointer"
                          >
                            <q-popup-proxy
                              content-class="bg-transparent"
                              :auto-close="false"
                              anchor="center middle"
                              self="center middle"
                            >
                              <q-banner dark class="q-pa-none">
                                <UserItem
                                  v-model="bet.betUserItem"
                                  style="width: 200px"
                                >
                                </UserItem>
                              </q-banner>
                            </q-popup-proxy>
                            <q-tooltip
                              content-class="bg-modal"
                              content-style="font-size: 1em;"
                            >
                              {{ bet.betUserItem.item._id }}
                            </q-tooltip>
                          </q-img>
                        </div>
                        <div
                          class="text-small col-md-3 col-xs-4 text-font-montserrat text-weight-medium"
                          :class="{
                            'text-right': $q.screen.gt.sm,
                            'text-center': $q.screen.lt.md
                          }"
                        >
                          <span class="q-mr-md" style="color: #ffffff"
                            >{{
                              $q.screen.gt.sm ? "Сумма выигрыша" : "Выигрыш"
                            }}:<br v-if="$q.screen.lt.md" />
                          </span>
                          <span style="color: #eca907">{{
                            bet.profitAmount | coinsWithoutShort
                          }}</span>
                        </div>
                      </div>

                      <template v-slot:loading>
                        <div class="row justify-center q-my-md">
                          <q-spinner color="primary" size="xl" />
                        </div>
                      </template>
                    </q-infinite-scroll>
                  </q-scroll-area>
                  <template v-else>
                    <div
                      class="absolute absolute-full flex items-center justify-center"
                      style="z-index: 1"
                    >
                      <div class="text-center flex column empty-block">
                        <div class="" style="font-size: 1.25em">
                          Вы еще не делали ставок{{
                            historyGameType ? " в этой игре" : ""
                          }}
                        </div>
                      </div>
                    </div>
                    <div class="full-width" style="filter: blur(6px)">
                      <div class="history__row " v-for="n in 3" :key="n">
                        <q-responsive :ratio="1132.25 / 51">
                          <q-skeleton type="rect" animation="none" />
                        </q-responsive>
                      </div>
                    </div>
                  </template>
                </div>
              </template>
            </ApolloQuery>
          </div>
        </div>
      </div>
    </template>
  </ApolloQuery>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { vTeleport, vTeleportLocation } from "@desislavsd/vue-teleport";
import Loading from "@/components/Loading";
import UserItemList from "@/components/UserItemList";
import { apolloProvider } from "@/vue-apollo";
import _ from "lodash";
import dialogs from "../mixins/dialogs";
import scrollBarStyle from "../mixins/scrollBarStyle";
import UserName from "../components/UserName";
import { copyToClipboard } from "quasar";
import gameLinks from "../mixins/gameLinks";
import UserItem from "../components/UserItem";

const apolloClient = apolloProvider.defaultClient;

export default {
  name: "Profile",
  mixins: [dialogs, scrollBarStyle, gameLinks],
  components: {
    Loading,
    UserItemList,
    UserName,
    UserItem,
    vTeleport,
    vTeleportLocation
  },
  data: () => ({
    historyGameTypeValue: null,
    historyHasMore: true,
    historyGameType: "",
    historySortByBetAmount: false,
    historySortByProfitAmount: false,
    historyPage: 1,
    userItemsHasMore: false,
    copySuccess: false,
    partnerBalanceTransferred: false,
    loadingTransferPartnerBalance: false,
    isLoading: true,
    changeAvatarLoading: false,
    saveTradeUrlSuccess: false,
    userProfile: null,
    userItems: null,
    promoCode: "",
    userStatistics: null,
    tab: "inventory",
    partnerUsersHasMore: true,
    transferBalanceSuccess: false,
    transferBalanceAmount: 100,
    miniFaqs: [
      { title: "Трейд ссылка", text: "Введите trade-url в поле выше" },
      { title: "Баланс", text: "Пополните баланс и начните играть!" }
    ],
    getPartnerUsers: {}
  }),
  meta() {
    return {
      title: `Профиль игрока ${!this.userProfile ? "" : this.userProfile.name}`
    };
  },
  watch: {
    historySortByBetAmount: "resetHistory",
    historySortByProfitAmount: "resetHistory",
    historyGameType: "resetHistory",
    historyGameTypeValue(newValue) {
      this.historyGameType = newValue.value;
    }
  },
  created() {
    this.historyGameTypeValue = this.historyGameTypeValueOptions[0];
  },
  methods: {
    resetHistory() {
      this.historyPage = 1;
      this.historyHasMore = true;
      if (this.$refs.historyScroll) {
        this.$refs.historyScroll.setScrollPosition(0);
      }
    },
    async loadMoreMyBets(query, done) {
      this.historyPage++;
      await query.fetchMore({
        variables: {
          page: this.historyPage
        },
        updateQuery: (prev, { fetchMoreResult }) => {
          if (!fetchMoreResult || fetchMoreResult.getMyBets.length === 0) {
            this.historyHasMore = false;
            return prev;
          }

          return Object.assign({}, prev, {
            getMyBets: [...prev.getMyBets, ...fetchMoreResult.getMyBets]
          });
        }
      });
      done();
    },
    transferBalanceAmountChanged({ target: { value } }) {
      value = Number(value) || 0;
      this.transferBalanceAmount = value < 0 ? 0 : value;
    },
    async transferBalance() {
      try{
        await this.$apollo.mutate({
          mutation: require("@/graphql/TransferBalance.gql"),
          variables: {
            receiverUserId: this.userProfile._id,
            amount: this.transferBalanceAmount
          }
        });
        this.transferBalanceSuccess = true;
        this.$q.notify({
          type: "positive",
          position: "bottom-left",
          message: `${this.transferBalanceAmount} монет успешно переведены`
        });
        setTimeout(() => (this.transferBalanceSuccess = false), 3000);
      }catch(e){
        if(e.message.indexOf("deposit") !== -1){
          this.$q.notify({
            type: "negative",
            html: true,
            multiLine: false,
            position: "bottom-left",
            message:  'Для того чтобы воспользоваться переводом, нужно хотя бы раз пополнить Ваш баланс на сумму от 1000 монет.' ,
            actions: [{ label: "Ок", color: "white", handler: () => {} }]
          });
        }
      }

    },
    async transferPartnerBalance() {
      this.loadingTransferPartnerBalance = true;
      await this.$apollo.mutate({
        mutation: require("@/graphql/TransferPartnerBalanceToUserBalance.gql")
      });
      this.partnerBalanceTransferred = true;
      this.loadingTransferPartnerBalance = false;
    },
    async copyPartnerLink() {
      await copyToClipboard(this.userPartnerLink);
      this.copySuccess = true;
      setTimeout(() => (this.copySuccess = false), 3000);
    },
    findPartnerUserDeposit(userId, deposits) {
      return deposits.find(deposit => deposit.userId === userId) || {};
    },
    async loadMorePartnerUsers(index, done) {
      let afterId = this.getPartnerUsers.users.length
        ? this.getPartnerUsers.users[this.getPartnerUsers.users.length - 1]._id
        : "";

      if (afterId) {
        let { data } = await this.$apollo.query({
          query: require("@/graphql/GetPartnerUsers.gql"),
          variables: {
            afterId
          }
        });
        if (data.getPartnerUsers.users.length) {
          this.getPartnerUsers.users = [
            ...this.getPartnerUsers.users,
            ...data.getPartnerUsers.users
          ];
          this.getPartnerUsers.deposits = [
            ...this.getPartnerUsers.deposits,
            ...data.getPartnerUsers.deposits
          ];
        } else {
          this.partnerUsersHasMore = false;
        }
      } else {
        this.partnerUsersHasMore = false;
      }
      done();
    },
    changeTab(tab) {
      this.tab = tab;
      this.partnerBalanceTransferred = false;
    },
    async onChangeAvatar() {
      this.changeAvatarLoading = true;
      const { avatar } = (
        await apolloClient.mutate({
          mutation: require("@/graphql/ChangeAvatar.gql")
        })
      ).data.changeAvatar;

      this.userProfile.avatar = avatar;
      this.changeAvatarLoading = false;
    },
    async onChangeName() {
      if (this.userProfile.name === "") {
        this.userProfile.name = this.user.name;
        return;
      }
      await apolloClient.mutate({
        mutation: require("@/graphql/ChangeName.gql"),
        variables: { name: this.userProfile.name }
      });
    },
    onPartnerUsersResult(result) {
      if (result.data) {
        this.getPartnerUsers = result.data.getPartnerUsers;
      }

      return result;
    },
    onResult(result) {
      if (result.data) {
        this.userStatistics = result.data.getUserStatisticsByUserId || {};
        this.userProfile = result.data.getUserProfile;
        this.userItems = result.data.getUserItems;
        this.userItemsHasMore = !!(this.userItems && this.userItems.length);
      }
      if (!this.userProfile) {
        return this.$router.push({ name: "NotFound" });
      }
      this.isLoading = false;

      return result;
    },
    onSaveTradeUrl() {
      this.saveTradeUrlSuccess = true;
      setTimeout(() => (this.saveTradeUrlSuccess = false), 1500);
      ym(69160060, "reachGoal", "tradelink");
    },
    async loadMoreItems(done) {
      const userItems = (
        await apolloClient.query({
          query: require("@/graphql/GetUserItems.gql"),
          variables: {
            userId: this.$route.params.userId,
            afterId: this.userItems.length
              ? _.last(this.userItems)._id
              : Date.now()
          }
        })
      ).data.getUserItems;
      if (userItems.length) {
        this.userItems = this.userItems.concat(userItems);
      }
      this.userItemsHasMore = !!userItems.length;
      done();
    }
  },
  computed: {
    historyGameTypeValueOptions() {
      return [
        { name: "Все игры", value: "" },
        ...Object.keys(this.gameLinksObject).reduce((p, c) => {
          p.push({ value: c, ...this.gameLinksObject[c] });
          return p;
        }, [])
      ];
    },
    availableTransferAmount() {
      return Math.min(this.user.recoup, this.user.balance);
    },
    userPartnerLink() {
      return window.location.origin + "/#/p/" + this.user._id;
    },
    ...mapState({
      user: state => state.user
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    }),
    rightMiniStatistics() {
      return this.userStatistics
        ? [
            {
              text: "Поставлено",
              value: this.userStatistics.betAmount || 0
            },
            {
              text: "Выиграно",
              value: this.userStatistics.profitAmount || 0
            }
          ]
        : [];
    },
    bottomStatistics() {
      return this.userStatistics
        ? [
            {
              text: "Поставлено за час",
              value: this.userStatistics.betAmountHourly || 0
            },
            {
              text: "За день",
              value: this.userStatistics.betAmountDaily || 0
            },
            {
              text: "За неделю",
              value: this.userStatistics.betAmountWeekly || 0
            },
            {
              text: "Выиграно всего",
              value: this.userStatistics.profitAmount || 0
            }
          ]
        : [];
    }
  }
};
</script>

