import { apolloProvider } from "@/vue-apollo";

const apolloClient = apolloProvider.defaultClient;
const setTimeout = require("safe-timers").setTimeout;

export default {
  namespaced: true,
  state: () => ({
    _id: undefined,
    collectedPrizes: [],
    expireAt: undefined,
    levelPoints: 0,
    level: 0,
    levelPointsForNextLevel: 1,
    levelPointsForCurrentLevel: 0,
    subscription: null
  }),
  getters: {
    isUserHaveBattlePass(state) {
      return !!state._id;
    }
  },
  mutations: {
    unsetSubscription(state) {
      state.subscription.unsubscribe();
      state.subscription = null;
    },
    setSubscription(state, subscription) {
      state.subscription = subscription;
    },
    reset(state) {
      state._id = undefined;
      state.collectedPrizes = [];
      state.expireAt = undefined;
      state.levelPoints = 0;
    },
    update(state, battlePass) {
      if (battlePass) {
        Object.keys(battlePass).map(key => {
          if (
            typeof battlePass[key] !== "undefined" &&
            battlePass[key] !== null
          ) {
            state[key] = battlePass[key];
          }
        });
      }
    }
  },
  actions: {
    async get({ commit, dispatch }) {
      const battlePass = (
        await apolloClient.query({
          query: require("@/graphql/GetBattlePass.gql")
        })
      ).data.getBattlePass;
      commit("update", battlePass);
      if (battlePass) dispatch("subscribe", battlePass);
    },
    async buy({ commit, dispatch }) {
      const battlePass = (
        await apolloClient.mutate({
          mutation: require("@/graphql/BuyBattlePass.gql")
        })
      ).data.buyBattlePass;
      commit("update", battlePass);
      commit("user/setHasBattlePass", true, { root: true });
      if (battlePass) dispatch("subscribe", battlePass);
    },
    async takePrize({ commit }, level) {
      const battlePass = (
        await apolloClient.mutate({
          mutation: require("@/graphql/BattlePassTakePrize.gql"),
          variables: { level }
        })
      ).data.battlePassTakePrize;
      commit("update", battlePass);
    },
    subscribe({ commit, rootState }, battlePass) {
      const observer = apolloClient.subscribe({
        query: require("@/graphql/BattlePassUpdated.gql")
      });

      let subscription = observer.subscribe({
        next: data => commit("update", data.data.battlePassUpdated)
      });

      commit("setSubscription", subscription);

      let time = battlePass.expireAt - rootState.time.ts.now();

      setTimeout(() => {
        commit("unsetSubscription");
        commit("reset");
        commit("user/setHasBattlePass", false, { root: true });
      }, time);
    }
  }
};
