<template>
  <div id="chat" :style="chatOpened ? `` : `transform: translateX(100%)`">
    <div class="chat__container">
      <div
        class="chat__header flex content-center justify-between q-py-lg q-px-lg"
      >
        <div class="flex content-center justify-start">
          <q-icon size="sm" class="q-pr-md" name="fas fa-comments"> </q-icon>
          <h6 class="q-pa-none q-ma-none text-uppercase">
            Общий чат
          </h6>
        </div>
        <q-btn size="sm" class="chat__header-btn" @click="closeChat">
          <q-icon size="sm" name="fas fa-angle-right"> </q-icon>
        </q-btn>
      </div>

      <q-scroll-area
        ref="scrollArea"
        class="chat__body"
        style="height: 100%"
        :delay="1200"
        :thumb-style="scrollThumbStyle"
        :bar-style="scrollBarStyle"
      >
        <q-infinite-scroll
          @load="onScrollReachedTop"
          :offset="100"
          :debounce="250"
          :reverse="true"
          ref="infiniteScroll"
          scroll-target=".chat__body > .scroll"
        >
          <q-virtual-scroll
            ref="virtualScroll"
            :items="messages"
            scroll-target=".chat__body > .scroll"
            :virtual-scroll-item-size="114"
            :virtual-scroll-slice-size="15"
          >
            <template v-slot="{ item: message }">
              <Message
                class="message"
                :key="message._id"
                :value="message"
                :reply="isAuthenticated ? onReply : undefined"
              >
              </Message>
            </template>
          </q-virtual-scroll>
          <template v-slot:loading>
            <div class="row justify-center q-my-md">
              <q-spinner color="primary" size="xl" />
            </div>
          </template>
        </q-infinite-scroll>
      </q-scroll-area>

      <div class="chat__footer bg-chat-input-color relative-position" v-if="isAuthenticated">
        <div v-if="text.length === 1  && text[0] === '/'" class="chat__commands-list bg-modal absolute absolute-top full-width" style="transform: translateY(-100%)">
          <q-btn  v-for="(command, i) in commands" :key="i" class="full-width" @click="text=command.value" size="md" flat no-caps color="primary" >
            <div class="text-left full-width">
              {{command.label}}
            </div>
          </q-btn>
        </div>
        <countdown
          v-if="user.publicChatBanBefore >= Date.now()"
          :time="user.publicChatBanBefore - Date.now()"
          @end="publicChatBanBeforeCountdownEnd"
          class="full-width text-center q-pa-lg"
          tag="div"
        >
          <template slot-scope="props">
            <div style="font-size: 0.85em">
              Время блокировки:
            </div>
            {{ padStart(props.days) }}:{{ padStart(props.hours) }}:{{
              padStart(props.minutes)
            }}:{{ padStart(props.seconds) }}
          </template>
        </countdown>
        <q-input
          :loading="sendingMessage"
          filled
          v-else
          color="chat-input-color"
          bg-color="chat-input-color"
          v-model="text"
          label="Комментировать..."
          @keyup.enter.native="createPublicMessage"
          maxlength="500"
          ref="chatInput"
          :readonly="sendingMessage"
        >
          <template v-slot:append>
            <q-icon
              name="fas fa-paper-plane"
              color="chat-input-color"
              class="cursor-pointer"
              @click="createPublicMessage"
            />
          </template>
        </q-input>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import Message from "./Message";
import _ from "lodash";
import padStart from "../mixins/padStart";
import gql from "graphql-tag";

export default {
  name: "Chat",
  mixins: [padStart],
  components: {
    Message
  },
  data: () => ({
    inScrollAnimation: true,
    inScrollAnimationTimer: null,
    text: "",
    sendingMessage: false,
    scrollThumbStyle: {
      right: "4px",
      borderRadius: "1px",
      backgroundColor: "rgba(117,117,117,0.5)",
      width: "5px",
      opacity: 0.75
    },
    scrollBarStyle: {
      right: "2px",
      borderRadius: "4px",
      backgroundColor: "rgba(117,117,117,0.79)",
      width: "9px",
      opacity: 0.15
    },
    onceScrolledBottom: false,
    sendRateLimit: false,
    commands: [
      { command: "giveaway", label: "/giveaway [сумма] - создает розыгрыш", value: "/giveaway 100" }
    ]
  }),
  created() {},
  methods: {
    addText(text) {
      if (this.text && _.last(this.text) !== " ") {
        this.text += " ";
      }

      this.text += text;
      this.$refs.chatInput.focus();
    },
    onReply(message) {
      this.addText("@[" + message.user._id + "|" + message.user.name + "] ");
    },
    publicChatBanBeforeCountdownEnd() {
      this.$store.commit("user/unsetPublicChatBanBefore");
    },

    async execChatCommand(text) {
      try {
        let [command, ...args] = text.split(" ");
        if (command === "/giveaway" && args[0] && /^\d+$/.test(args[0])) {
          await this.$apollo.mutate({
            mutation: gql`
              mutation {
                createChatGiveaway(chatId: "public", giveawayAmount: ${Number(
                  args[0]
                )}){
                    _id
                }
              }
            `
          });
        }
      } catch (e) {
        console.error(e);
      }

      this.text = "";
    },

    async createPublicMessage() {
      if (this.text.length && this.text[0] === "/") {
        return await this.execChatCommand(this.text);
      }

      if (!this.text || this.sendingMessage) {
        return;
      }

      const send = async () => {
        this.sendRateLimit = true;

        let err;

        try {
          await this.$store.dispatch("chat/createPublicMessage", {
            text: this.text
          });
        } catch (e) {
          err = e;
        }

        if (!err) {
          this.text = "";
        }

        this.sendingMessage = false;
        this.$refs.chatInput.focus();
        setTimeout(() => (this.sendRateLimit = false), 3100);
      };

      this.sendingMessage = true;

      if (!this.sendRateLimit) {
        await send();
      } else {
        const unwatch = this.$watch("sendRateLimit", async newValue => {
          if (!newValue) {
            await send();
          }
          unwatch();
        });
      }
      ym(69160060, "reachGoal", "chat_open");
    },
    async onScrollReachedTop(index, done) {
      if (!this.onceScrolledBottom) {
        this.$refs.infiniteScroll.stop();
        return done();
      }
      const messages = await this.$store.dispatch("chat/getMessages", {
        chatId: "public",
        afterId: this.messages[0] ? this.messages[0]._id : undefined
      });

      if (!messages.length) {
        this.$refs.infiniteScroll.stop();
      }else{
        await this.$nextTick();
        this.$refs.virtualScroll.refresh();
      }

      done();
    },
    closeChat() {
      this.$store.dispatch("chat/closeChat");
    },
    isScrollOnBottom(absError = 100) {
      const scrollElement = this.$refs.scrollArea.getScrollTarget();
      return (
        scrollElement.scrollHeight -
          scrollElement.scrollTop -
          scrollElement.clientHeight <=
        absError
      );
    },
    async scrollToBottom(duration = 150) {
      this.$refs.virtualScroll.refresh(this.messages.length - 1);
       await this.$nextTick();
      //
      // const scrollElement = this.$refs.scrollArea.getScrollTarget();
      // const scrollPosition =
      //   scrollElement.scrollHeight - scrollElement.clientHeight;

      // const lastMessageEl = this.$el.querySelector(".message:last-child");
      //
      // if (lastMessageEl) {
      //   this.$refs.scrollArea.setScrollPosition(
      //     scrollPosition - lastMessageEl.clientHeight
      //   );
      // }

      // this.$refs.scrollArea.setScrollPosition(scrollPosition, duration);
      //this.$refs.scrollArea.setScrollPosition(scrollPosition);

      this.inScrollAnimation = true;
      clearTimeout(this.inScrollAnimationTimer);

      this.inScrollAnimationTimer = setTimeout(() => {
        this.inScrollAnimation = false;
        if (!this.onceScrolledBottom) {
          this.onceScrolledBottom = true;
          this.$refs.infiniteScroll.resume();
        }
      }, 0);
    }
  },
  watch: {
    async ["messages.length"]() {
      if (this.isScrollOnBottom() || this.inScrollAnimation) {
        await this.scrollToBottom();
        this.$store.commit('chat/reset', 'public');
      }
    },
    chatOpened(newValue) {
      if (newValue /*&& !this.onceScrolledBottom*/) {
        this.scrollToBottom(1);
        ym(69160060, "reachGoal", "chat_open");
      }
    }
  },
  computed: {
    ...mapState({
      user: state => state.user,
      chatOpened: state => state.chat.chatOpened,
      chatWidth: state => state.chat.chatWidth,
      messages: state => state.chat.chatMessages.public
    }),
    ...mapGetters({
      isAuthenticated: "user/isAuthenticated"
    })
  }
};
</script>

<style lang="scss">
@import "../styles/quasar.variables.scss";

#chat {
  @media (max-width: $breakpoint-sm-max) {
    & {
      width: 100% !important;
      border-left: none !important;
      border-top-left-radius: 0 !important;
      background: #0f1318 !important;
    }
  }
  position: fixed;
  z-index: 2001;
  top: 0;
  bottom: 0;
  border-left: 0.08em solid #181e27;
  right: 0;
  transition: all 1s;
  will-change: transform;
  width: 20.625em;
  background: url(/img/chat-bg.png) no-repeat left top / 100% 100%, #0f1318;
  border-top-left-radius: 0.8em;
  display: flex;
  align-items: stretch;
  justify-content: stretch;

  .chat__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    .chat__body {
      flex: 1 1 auto;
    }
  }
  .chat__header {
    border-bottom: 0.07em solid #1c2229;
  }
  .chat__header-btn {
    .q-btn__wrapper {
      border-radius: 0.5em;
      padding: 0.75em;
      border: 0.17em;
      border-style: solid;
      border-color: #2a303e;
      background-color: #181b26;
      transition: 0.5s background, 1s border;
      &:hover {
        background-color: #2a303e;
      }
    }
  }
}
</style>
